.search {
  
  &_desktop {
    position: absolute;
    top: -15px;
    right: -3px;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s;
    background: #fff;
    z-index: 10;
    box-shadow: 0 0 12px rgba(255, 102, 0, 0.2);

    @include media-breakpoint-down(lt) {
      top: -13px;
      //left: -152px;
      left: -18px;
      right: auto;
    }

    > form {
      padding: 0 20px 0 18px;

      @include media-breakpoint-down(tbx){
        padding: 0 20px 0 17px;
      }
    }
    
    .search__input {
      width: 317px;

      @include media-breakpoint-down(tbx){
        width: 323px;
      }
    }
    .search__icon {
      padding: 17px 12px 0 0;

      @include media-breakpoint-down(tbx){
        padding: 16px 12px 0 0;
      }
    }
  }
  
  &_mob {
    position: relative;
    border-radius: 3px;
    
    .search__result {
      position: absolute;
      padding: 12px 8px 24px;
      top: 100%;
      margin-top: -1px;
      background: #fff;
      left: -1px;
      right: -1px;
      border: 1px solid #999999;
      border-top: 0;
      border-radius: 0 0 3px 3px;
      
      &-list-scroll {
        height: 214px;

        .mCSB_inside > .mCSB_container {
          margin-right: 12px;
        }
      }
      &-btn {
        .btn {
          font-size: 18px;
          letter-spacing: 0.36px;
          padding: 5px 28px 8px;
        }
      }
      &-count {
        font-size: 16px;
        letter-spacing: 0.4px;
        margin-bottom: 22px;
      }
      
      &-item {
        &-name {
          padding: 24px 0 23px 13px;
        }
      }
    }
    
    .search__close {
      display: none;
    }
    
    .search__input {
      flex-grow: 1;
      input {
        background: transparent;
        height: 33px;
      }
    }

    .search__icon {
      padding: 7px 12px 0 0;
    }
    
    .search__btn {
      display: none;
    }

    > form {
      padding: 0 11px 0 8px;
    }
    
    &.search_fill {
      background: #fff;
      
      .search__btn {
        display: block;
      }
    }
  }
  
  &_open {
    opacity: 1;
    visibility: visible;
  }

  > form {
    display: flex;
    align-items: baseline;
  }
  
  &__icon {
    align-self: flex-start;
    flex-shrink: 0;
    
    
    svg {
      display: block;
      fill: none;
      stroke: $color__additional;
      width: 18px;
      height: 18px;
    }
  }
  &__input {
    margin: 0 15px 0 0;
    
    
    input {
      display: block;
      width: 100%;
      height: 52px;
      border: 0;
      outline: 0;
      font-size: 18px;
      letter-spacing: 0.36px;


      @include media-breakpoint-down(tbx){
        font-size: 16px;
        letter-spacing: 0.4px;
      }
      
      &::placeholder {
        color: $color__additional;
      }
      
    }
  }
  
  &__btn {
    flex-shrink: 0;
    a {
      @include media-breakpoint-down(tbx){
        font-size: 16px;
        letter-spacing: 0.4px;
      }
    }
  }
  
  &__close {
    flex-shrink: 0;
    align-self: flex-start;
    background: none;
    border: 0;
    outline: 0;
    margin: 17px 0 0 17px;
    cursor: pointer;
    padding: 0;
    
    svg {
      width: 14px;
      height: 14px;
      stroke: none;
      fill: $color__additional;
    }
  }
  
  &__no-result {
    //display: none;
    padding: 9px 15px 25px;

    @include media-breakpoint-down(tbx){
      padding: 9px 15px 21px;
    }
    &-email {
      margin-top: 16px;

      @include media-breakpoint-down(tbx){
        margin-top: 5px;
      }
      
      a {
        @include media-breakpoint-down(tbx){
          font-size: 16px;
          letter-spacing: 0.4px;
        }
      }
    }
    &-contacts {
      //padding-left: 9px;
      margin-top: 17px;
      text-align: center;
      .border-dot-list {
        display: inline-flex;
        
        > li {
          font-size: 18px;
          letter-spacing: 0.36px;

          @include media-breakpoint-down(tbx){
            font-size: 16px;
            letter-spacing: 0.4px;
          }
          
          &:first-child {
            .search__no-result-text-small {
              text-align: right;
            }
          }
          
          &:last-child {
            .search__no-result-text-small {
              text-align: left;
            }
          }
        }
      }
    }
    &-phone {
      position: relative;
      
      svg {
        position: absolute;
        top: 3px;
        left: -24px;;
        width: 16px;
        height: 16px;
        fill: none;
        stroke: $color__additional;
      }
    }
    &-text {
      font-size: 18px;
      letter-spacing: 0.36px;
      text-align: center;
      margin-bottom: 13px;

      @include media-breakpoint-down(tbx){
        font-size: 16px;
        letter-spacing: 0.4px;
      }
      
      &_red {
        color: $color1;

        @include media-breakpoint-down(tbx){
          font-size: 18px;
          letter-spacing: 0.36px;
          margin-bottom: 20px;
        }
      }
      
      &-small {
        font-size: 14px;
        letter-spacing: 0.4px;
        color: $color__additional;
        margin-top: 3px;

        @include media-breakpoint-down(tbx){
          font-size: 12px;
          letter-spacing: 0.48px;
        }
      }
    }
  }
  
  &__result {
    //display: none;
    padding: 9px 13px 21px;

    @include media-breakpoint-down(tbx){
      padding: 9px 20px 24px;
    }
    
    &-count {
      text-align: center;
      color: $color__primary;
      letter-spacing: 0.36px;
      margin-bottom: 20px;
    }
    
    &-btn {
      text-align: center;
      margin-top: 22px;
      @include media-breakpoint-down(tbx){
        margin-top: 24px;
      }
      .btn {
        letter-spacing: 0.36px;
        padding: 5px 18px 8px;
      }
    }
    
    &-list-scroll {
      height: 285px;

      @include media-breakpoint-down(tbx){
        height: 263px;
      }

      .mCSB_inside > .mCSB_container {
        margin-right: 22px;
      }
    }
    &-list {
      width: 100%;
      table-layout: fixed;
      border-collapse: collapse;
    }
    &-item {
      border-top: 1px solid #EAEAEA;
      &:last-child {
        border-bottom: 1px solid #EAEAEA;
      }
      &-img {
        padding: 10px 7px;
        width: 70px;
      }
      &-name {
        padding: 24px 0 20px 6px;

        @include media-breakpoint-down(tbx){
          padding: 22px 0 18px 6px;
        }
        
        a {
          letter-spacing: 0.36px;
          width: 100%;
          white-space: nowrap;
          display: inline-block;
          text-overflow: ellipsis;
          overflow: hidden;

          @include media-breakpoint-down(tbx){
            font-size: 16px;
            letter-spacing: 0.4px;
          }
          
          span {
            color: $color__primary;
          }
        }
      }
    }
  }
}