.buy-with-presents {
  position: relative;
  padding: 35px 35px 34px;
  width: 951px;
  margin: 0 auto;
  
  @include media-breakpoint-down(tbx){
    width: 895px;
    padding: 45px 43px 51px;
  }
  
  @include media-breakpoint-down(tb){
    padding: 42px 16px 30px;
    width: auto;
  }

  &__name {
    font-size: 22px;
    margin-bottom: 44px;

    @include media-breakpoint-down(tbx){
      padding: 0 45px;
      margin-bottom: 38px;
    }

    @include media-breakpoint-down(tb){
      margin-bottom: 12px;
      font-size: 18px;
      padding: 0;
    }
  }
  &__presents-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 32px;

    @include media-breakpoint-down(tbx){
      margin-bottom: 40px;
    }
    @include media-breakpoint-down(tb){
      display: block;
      margin-bottom: 25px;
    }
  }
  &__presents-items-container {
    display: flex;
    flex-wrap: wrap;
    max-width: 585px;
    padding-top: 32px;

    @include media-breakpoint-down(tbx){
      width: 294px;
      padding-top: 0;
      margin-right: 45px;
    }
    
    @include media-breakpoint-down(tb){
      display: none;
    }
  }
  
  &__tractor {
    position: relative;
    width: 266px;
    height: 132px;

    @include media-breakpoint-down(tbx){
      width: 398px;
      height: 222px;
    }

    @include media-breakpoint-down(tb){
      width: auto;
      max-width: 142px;
      height: 80px;
      margin: 0 auto;
    }
    
    img {
      margin: 0 auto;
    }
  }
  
  &__plus {
    position: absolute;
    top: 50%;
    right: -27px;
    width: 14px;
    height: 14px;
    margin-top: -11px;
    
    @include media-breakpoint-down(tbx){
      right: -49px;
      width: 26px;
      height: 26px;
      img {
        width: 26px;
        height: 26px;
      }
    }

    @include media-breakpoint-down(tb){
      display: none;
    }
  }
  
  &__present {
    width: 135px;
    margin: 0 12px 12px 0;


    @include media-breakpoint-up(lt){
      &:nth-child(4n){
        margin-right: 0;
      }
    }

    @include media-breakpoint-down(tbx){
      margin: 0 12px 6px 0;
      
      &:nth-child(2n){
        margin-right: 0;
      }
    }
    
    @include media-breakpoint-down(tb){
      position: relative;
      margin: 0 0 20px;
      width: auto;
      padding-left: 34px;
      
      &:last-child {
        margin: 0;
      }
    }
   
    &-img {
      margin-bottom: 4px;

      @include media-breakpoint-down(tbx){
        margin-bottom: 0;
      }
      
      @include media-breakpoint-down(tb){
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
      }
      
      img {
        margin: 0 auto;
      }
    }
    &-name {
      font-size: 17px;
      letter-spacing: 0.4px;
      text-align: center;
      height: ((17px * 1.25) * 2) + 5;
      overflow: hidden;
    }
  }
  &__present-description {
    font-size: 16px;
    letter-spacing: 0.4px;
    
    
  }
  
  &__present-description2 {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-size: 17px;
    text-align: center;
  }
  &__form {
    margin-bottom: 20px;

    @include media-breakpoint-down(tbx){
      margin-bottom: 28px;
    }

    @include media-breakpoint-down(tb){
      margin-bottom: 17px;
    }
  }
  
  &__flex {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    & > *:not(:last-child) {
      flex: 1 0 0;
      margin-right: 43px;
      
      @include media-breakpoint-down(tbx){
        margin-right: 18px;
      }

      @include media-breakpoint-down(tb){
        flex: none;
        width: 100%;
        margin: 0 0 16px 0;
      }
      
    }
    
    .btn {
      padding: 7px 20px;

      @include media-breakpoint-down(tb){
        font-size: 18px;
        letter-spacing: 0.36px;
        padding: 6px 24px 9px;
      }
    }

    @include media-breakpoint-down(tb){
      flex-direction: column;
    }
  }
  
  &__phone {
    min-width: 0 !important;
  }
  
  &__description {
    font-size: 18px;
    text-align: center;
    letter-spacing: 0.36px;
    color: $color__additional;

    @include media-breakpoint-down(tbx){
      color: black;
    }
    
    @include media-breakpoint-down(tb){
      font-size: 14px;
      letter-spacing: 0.4px;
      color: $color__additional;
      margin-bottom: 15px;
    }
  }
  &__in-complect {
    border-top: 1px solid lightgray;
    padding-top: 15px;
  }
  &__hide-small {
    @include media-breakpoint-down(tb){
      display: none;
    }
  }
  &__show-small {
    display: none;
    @include media-breakpoint-down(tb){
      display: block;
    }
  }
  &__center {
    text-align: center;

    @include media-breakpoint-down(tb){
      letter-spacing: 0.36px;
      margin-bottom: 18px;
    }
  }
}