.btn {
  $rootSelector: &;
  
  display: inline-block;
  cursor: pointer;
  background: $color__primary;
  color: #fff;
  border-radius: 3.388px;
  font-size: 18px;
  text-align: center;
  outline: none;
  padding: 10px;
  transition: 0.2s;
  transition-property: background-color, color, border;
  border: 1px solid transparent;

  @include media-breakpoint-down($mobile-switch){
    font-size: 16px;
    padding: 8px 10px;
  }
  
  &:focus,
  &:hover {
    background: #fff;
    color: $color__primary;
    border-color: $color__primary;
  }
  
  &_size21 {
    font-size: 21px;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(dtx){
      font-size: 18px;
    }

    @include media-breakpoint-down(lt){
      font-size: 17px;
    }

    @include media-breakpoint-down($mobile-switch){
      font-size: 14px;
    }
  }
  
  &_green {
    background: $color__text3;
  }

  &_green2 {
    background: $color__text4;
  }
  
  &_white {
    background: #fff;
    color: #000;
    border: 1px solid $color__additional;
    border-radius: 4.13855px;

    &:focus,
    &:hover {
      border-color: $color__primary;
      background: $color__primary;
      color: #fff;
    }
  }
  
  &_transparent {
    background: transparent;
    color: #000;
    border: 1px solid $color__additional;
    border-radius: 4.13855px;

    &:focus,
    &:hover {
      border-color: $color__primary;
      background: $color__primary;
      color: #fff;
    }
  }
  
  &_min {
    padding: 7px 18px;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(dt){
      font-size: 16px;
    }
  }


  &#{$rootSelector}_reverse {
    background: #fff;
    color: $color__primary;
    border-color: $color__primary;

    &:focus,
    &:hover {
      background: $color__primary;
      color: #fff;
      border-color: transparent;
    }

    &#{$rootSelector}_white {
      border-color: $color__primary;
      background: $color__primary;
      color: #fff;
      
      &:focus,
      &:hover {
        background: transparent;
        color: #000;
        border: 1px solid $color__additional;
        border-radius: 4.13855px;
      }
    }
  }
}