.product {
  border: 1px solid $color__text2;
  background: #fff;
  padding: 13px 19px;
  
  &_320 {
    display: none;
    padding: 22px 8px 22px 0;
    
    .product__name {
      letter-spacing: 0.4px;
      margin-bottom: 15px;

      span {
        border-bottom: 1px dotted #000;
      }
      
      a {
        display: block;
        
        &:hover {
          span {
            border-color: $color__primary;
          }
        }
      }
    }
    
    @include media-breakpoint-down(phx) {
      display: block;
    }
  }
  
  &_960 {
    display: none;
    padding: 11px 15px 16px;

    @include media-breakpoint-down(dt) {
      display: block;
    }

    @include media-breakpoint-down(phx) {
      display: none;
    }


    .product__top-top-right {
      align-items: baseline;
      margin: 0 0 35px;
    }
    
    .product__status {
      .status {
        padding-left: 22px;
      }
    }
    
    .product__pagination {
      .pagination-line {
        > a {
          height: 4px;
        }
      }
    }
    
    .product__spec {
      font-size: 12px;
      letter-spacing: 0.48px;
      text-align: left;
    }
    
    .product__info-top {
      margin: 2px 0 0;
    }
    
    .product__action {
      a {
        margin-right: 22px;
      }
      
      .product_link1 {
        font-size: 18px;
        padding: 5px 18px 8px;
      }
      .product_link2 {
        font-size: 16px;
        padding: 7px 18px 9px;
      }
      .product_link3 {
        font-size: 17px;
        padding: 6px 18px 8px;
      }
      .product_link4 {}
    }
    
    .product__bottom {
      margin-top: 28px;
    }
    
  }
  
  &_1920 {
    @include media-breakpoint-down(dt) {
      display: none;
    }
  }
  
  &__link {
    margin-top: 16px;
  }
  
  &__top {
    
    &-left {
      width: 54%;
      flex-grow: 1;

      @include media-breakpoint-down(dtx){
        width: 58%;
      }

      @include media-breakpoint-down(dt) {
        display: flex;
        width: 65%;
      }

      @include media-breakpoint-down(tbx){
        //width: 70%;
      }
    }
    
    &-right {
      width: 315px;
      padding-left: 27px;
      flex-shrink: 0;

      @include media-breakpoint-down(dtx){
        //width: 42%;
      }

      @include media-breakpoint-down(dt) {
        //width: 35%;
      }

      @include media-breakpoint-down(tbx){
        width: 208px;
        padding-left: 18px;
      }
    }
    
    &-col {
      margin-right: 10px;
      
      &:first-child {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex: 0 0 auto;
        width: 150px;
        min-width: 150px;
        
        @include media-breakpoint-down(tbx){
          min-width: 115px;
          width: 115px;
        }
      }
      
      &:last-child {
        flex: 1 1 auto;
        margin-right: 0;
      }
    }
    
  }

  &__top-top-right {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin: 5px 0 56px;
  }
  
  &__bottom {
    margin-top: 13px;
    @include media-breakpoint-down(phx){
      margin-top: 0;
    }
  }
  
  &__center {
    max-width: 205px;
    margin: 0 auto;
    
    .product__info-bottom {
      margin-top: 10px;
      margin-bottom: 0;
      
      & > div:not(:last-child) {
        margin-bottom: 8px;
      }
    }
    
    .product__image-mob {
      margin-bottom: 10px;
    }
    
  }
  
  &__full {
    .product__link {
      text-align: center;
    }
    
    .product__price {
      display: flex;
      justify-content: center;
      align-items: baseline;
      margin-top: 3px;
      
      .price,
      .price-old {
        margin: 0;
      }
      
      .price-old + .price {
        margin-left: 20px;
      }
    }
  }
  
  &__name {
    font-size: 21px;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(dtx){
      font-size: 18px;
    }

    @include media-breakpoint-down(phx){
      font-size: 16px;
    }

    // ((font-size * line-height * line-count) + border-weight * line-count)
    a {
      display: flex;
      flex-direction: column;
      height: ((21px * 1.15 * 2) + 5);
      overflow: hidden;

      @include media-breakpoint-down(dtx){
        height: ((18px * 1.15 * 2) + 5);
      }

      @include media-breakpoint-down(phx){
        height: ((16px * 1.15 * 2) + 5);
      }
    }
  }
  
  &__gift-count {
    position: relative;
    font-size: 12px;
    letter-spacing: 0.48px;
    color: $color__additional;
    padding: 6px 0 0 27px;
    
    img {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  
  &__props {
    margin-top: 12px;

    @include media-breakpoint-down(dt) {
      margin-top: 0;
    }
  }
  
  &__row {
    display: flex;
    
    &-2 {
      align-items: flex-end;
    }
  }
  
  &__review {
    a {
      font-size: 16px;
      letter-spacing: 0.4px;

      @include media-breakpoint-down(dtx){
        font-size: 14px;
      }
    }
  }
  
  &__status-left {
    margin: 8px 0 4px;
  }
  
  &__status-left,
  &__status {
    .status {
      font-size: 16px;
      padding-left: 20px;

      @include media-breakpoint-down(dtx){
        font-size: 14px;
      }
    }
  }
  
  &__status {
    .status {
      margin-left: 3px;
    }
  }
  
  &__info {
    &-top {
      margin-bottom: 15px;
    }
    
    // &-bottom {
    //   font-size: 16px;
    //   letter-spacing: 0.4px;
    //   display: flex;
    //   justify-content: space-between;
    //   margin: 16px 0 10px;
      
    //   @include media-breakpoint-down(phx){
    //     display: block;
    //     color: $color__additional;
        
    //     > div {
    //       margin-bottom: 10px;
    //     }
    //   }
    // }
  }
  
  &__garant,
  &__price-down,
  &__new,
  &__percent {
    position: relative;
    padding-left: 30px;
    letter-spacing: 0.4px;

    @include media-breakpoint-down(dtx){
      font-size: 14px;
    }

    @include media-breakpoint-down(phx){
      color: $color__additional;
    }
    
    img {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  
  &__price-down {
    font-size: 16px;
    img {
      top: -1px;
    }
  }

  &__percent {

  }
  
  &__action {
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    @include media-breakpoint-down(dtx){
      display: block;
    }
    
    a {
      display: inline-block;
      margin-right: 16px;
      
      &.dot-link-one-line {
        @include media-breakpoint-down(dt){
          font-size: 16px;
        }
      }
      
      &:last-child {
        margin-right: 0;
      }
    }
  }
  
  &__price {
    white-space: nowrap;

    > div {
      display: inline;
      padding-right: 12px;
      
      &:last-child {
        padding-right: 0;
      }

      @include media-breakpoint-down(dt) {
        display: block;
      }
    }

    @include media-breakpoint-down(dtx){
      margin-top: 10px;
    }

    
    
    .price {
      font-size: 24px;
      letter-spacing: 0.32px;

      @include media-breakpoint-down(dt) {
        //display: block;
        font-size: 20px;
      }

      @include media-breakpoint-down(phx) {
        font-size: 22px;
      }
      
      + .price-old {
        margin-left: 16px;

        @include media-breakpoint-down(dt) {
          margin-left: 0;
          margin-top: 5px;
        }
      }
    }
    .price-not-through {
      text-decoration: none;
    }
    .price-old {
      font-size: 16px;
      letter-spacing: 0.4px;

      @include media-breakpoint-down(dt) {
        //display: block;
        font-size: 16px;
        padding-left: 2px;
      }
      
      + .price {
        margin-left: 16px;

        @include media-breakpoint-down(dt) {
          margin-left: 0;
          margin-top: 5px;
        }
      }
    }
  }
  
  &__image {
    position: relative;
    margin: 10px 0 15px;
    transition: 0.2s;
    
    &-mob {
      margin: 15px 0 18px;
      
      img {
        @extend .img-responsive;
      }
    }
    
    &-more {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(black, 0.5);
      cursor: pointer;
      transition: 0.2s;
      opacity: 0;
      visibility: hidden;
      color: #fff;
      font-size: 16px;
      letter-spacing: 0.4px;
      display: flex;
      align-items: center;
      justify-content: center;
      
      @include media-breakpoint-down(dtx){
        font-size: 14px;
      }
      

      &:focus,
      &:hover {
        color: #fff;
      }
      
      img {
        display: block;
        margin: 0 auto 15px;
      }
    }
    
    &-preload {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      visibility: hidden;
      background: white;
      
      &.show {
        opacity: 1;
        visibility: visible;
      }
    }
    
    &-target {
      //width: 288px;
      height: 162px;

      @include media-breakpoint-down(dtx){
        height: 130px;
      }

      @include media-breakpoint-down(dt){
        height: 107px;
      }

      img {
        display: block;
        @extend .img-responsive;
        margin: 0 auto;
      }
    }
    
    &_open {
      background: rgba(0, 255, 0, 0.15);
      
      .product__image-more {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  
  &__spec {
    font-size: 16px;
    letter-spacing: 0.4px;
    text-align: right;
    margin-bottom: 6px;

    @include media-breakpoint-down(dtx){
      font-size: 14px;
    }
  }
  
  &__gift-wrap {
    display: none;
  }
  
  &__gift {
    display: flex;
    justify-content: space-between;
    padding-top: 24px;
    
    &-name {
      flex: 0 0 auto;
      color: $color__additional;
      font-size: 16px;
      letter-spacing: 0.36px;
      margin-right: 10px;
      padding-top: 9px;
    }
    
    &-item {
      &:hover {
        .gift__plus {
          background: #999900;
        }
      }
    }
    
    &-list {
      flex: 1 1 auto;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
  
}

.product-next {
  
  &__icon {
    position: relative;
    width: 58px;
    height: 58px;
    border-radius: 50%;
    border: 2px solid $color__additional;
    margin: 0 auto;
    transition: 0.2s;
    
    &:before {
      position: absolute;
      width: 18px;
      height: 18px;
      top: 50%;
      left: 50%;
      transform: translateY(-50%) translateX(-16px) rotateZ(135deg);
      border-left: 4px solid $color__additional;
      border-top: 4px solid $color__additional;
      transition: 0.2s;
      content: "";
    }
  }
  
  &__name {
    font-size: 20px;
    text-align: center;
    letter-spacing: 0.36px;
    margin-top: 30px;

    @include media-breakpoint-down(dt){
      font-size: 18px;
    }

    @include media-breakpoint-down(phx){
      font-size: 16px;
    }
  }
  
  
}