  &__btn {
    display: none;
    margin-top: 20px;
    text-align: center;
    
    .btn {
      padding: 6px 22px 9px;
      letter-spacing: 0.4px;

      &.btn_reverse.btn_white,
      &:focus,
      &:active {
        background: #fff;
        color: #000;
        border-color: #999;
      }
    }

    @include media-breakpoint-down(phx) {
      display: block;
    }

    .accordion-tablet & {
      @include media-breakpoint-down(tb) {
        display: block;
      }
    }
  }
  
