body.open-header-mob-popup {
  @include media-breakpoint-down(tb) {
    overflow: hidden;
  }
}

.menu-mob-popup-bg {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: -200px;
  background: rgba(0,0,0,0.85);
  z-index: $popup-bg-z-index;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;

  body.open-header-mob-popup & {
    opacity: 1;
    visibility: visible;
  }
}

.menu-mob-popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: $popup-z-index;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;

  &.open {
    opacity: 1;
    visibility: visible;
  }

  &__content {
    position: relative;
    margin: 56px 16px 16px;
    background: #fff;
    &:before {
      position: absolute;
      right: 0;
      top: -4px;
      border: 7px solid transparent;
      transform: rotateZ(45deg);
      width: 0;
      height: 0;
      border-left-color: #fff;
      border-top-color: #fff;
      content: "";
    }
  }

  &__scroll {
    position: relative;
    max-height: calc(100vh - (56px + 16px));
    overflow: auto;
  }

  &__close {
    position: absolute;
    top: 0;
    right: 0;
    width: 47px;
    height: 47px;
    transition: transform 500ms;
    cursor: pointer;
    background: none;
    border: 0;
    outline: 0;

    &:hover {
      &:before,
      &:after {
        transform: rotate(405deg);
      }
    }

    &:after,
    &:before {
      position: absolute;
      background: #000;
      top: 50%;
      left: 50%;
      content: "";
      transform: rotateZ(45deg);
      transition: transform 500ms;
    }

    &:after {
      width: 19px;
      height: 1px;
      margin-top: -0.5px;
      margin-left: -9.5px;
    }

    &:before {
      width: 1px;
      height: 19px;
      margin-top: -9.5px;
      margin-left: -0.5px;
    }
  }

  &_shop {
    .menu-mob-popup__content {
      &:before {
        @include media-breakpoint-down(tb){
          right: 54px;
        }
        @include media-breakpoint-down(phx){
          right: 38px;
        }
      }
    }
  }

  &_freecall {
    .menu-mob-popup__content {
      &:before {
        @include media-breakpoint-down(tb){
          right: 117px;
        }
        @include media-breakpoint-down(phx){
          right: 101px;
        }
      }
    }
  }

  &_write-to-us {
    .menu-mob-popup__content {
      &:before {
        @include media-breakpoint-down(tb){
          right: 84px;
        }
        @include media-breakpoint-down(phx){
          right: 68px;
        }
      }
    }
  }
}