.top-content {
  //margin-bottom: 21px;
  
  &__row {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include media-breakpoint-down($mobile-switch){
      display: block;
    }
  }

  &__col {
    
    &:last-child {
      @include media-breakpoint-down($mobile-switch){
        display: none;
      }
    }
    
  }

  //.breadcrumbs__list {
