.filter {
    background: $color__secondary;
    padding: 0 23px;
    margin: 0;
  
    @include media-breakpoint-down(dtx) {
        padding: 0;
    }
  
    @include media-breakpoint-down(tb) {
        display: none;
    }
  
    form {
        display: flex;
        flex-wrap: wrap;
    }
  
    .btn-wave-container {
        height: 22px;
    }
  
    &__item {
      
        &-wrap {
          
        }
      
        position: relative;
        padding: 0 13px 0 16px;
        transition: .2s;
      
        @include media-breakpoint-down(tbx) {
            padding: 0 13px 0 24px;
        }
      
        &:hover,
        &.open {
            background-color: #fff;
            box-shadow: 0 0 12px rgba(255, 102, 0, 0.4);
          
            .filter__drop {
                display: block;
                visibility: visible;
                opacity: 1;
                z-index: 5;
            }
          
            .filter__name {
              &:after {
                transform: rotateZ(45deg);
              }
            }

            &::after {
              content: '';
              position: absolute;
              bottom: 0;
              left: 0;
              display: block;
              z-index: 10;
              width: 100%;
              height: 13px;
              background-color: white;
              
              @include media-breakpoint-down(tbx){
                bottom: -3px;
              }
            }
        }
      
        .link-text,
        .filter__count {
            transition: .2s;
        }
        &.filtered .link-text,
        &.filtered .filter__count {
            color: $color__primary;
        }
      
      &-line {
        position: absolute;
        height: 1px;
        background: #fff;
        left: 0;
        width: 100%;
      }
    }
    &__count {
        display: inline-block;
        position: relative;
        font-size: 14px;
        font-weight: 500;
        padding: 0 6px 0 3px;
        top: -10px;
        z-index: 11;
        min-width: 18px;
        @include media-breakpoint-down(tbx) {
            font-size: 12px;
            top: -8px;
        }
    }
    &__name {
        display: inline-block;
        position: relative;
        font-size: 20px;
        letter-spacing: 0.36px;
        padding: 16px 8px 16px 0;
        cursor: pointer;
        user-select: none;
        @include media-breakpoint-down(dtx) {
            font-size: 18px;
        }
        @include media-breakpoint-down(tbx) {
            font-size: 16px;
            padding: 10px 8px 8px 0;
        }
        &::after {
            position: absolute;
            top: 50%;
            right: 0;
            width: 5px;
            height: 5px;
            border-top: 2px solid $color__primary;
            border-left: 2px solid $color__primary;
            transform: rotateZ(-135deg);
            margin-top: -2px;
            transition: 0.2s;
            content: "";

            @include media-breakpoint-down(dtx) {
                margin-top: -3px;
            }

            @include media-breakpoint-down(tbx) {
                margin-top: -5px;
            }
        }
    }
  
    &__drop {
        display: none;
        opacity: 0;
        position: absolute;
        left: 0;
        top: 100%;
        width: 100%;
        min-width: 205px;
        padding: 0 16px;
        background: #fff;
        box-shadow: 0 0 12px rgba(255, 102, 0, 0.4);
        transition: .2s;
        visibility: hidden;
      
        @include media-breakpoint-down(tbx) {
            padding: 0;
            width: auto;
            right: -24px;
        }
        &-items {
            margin-bottom: 21px;
            @include media-breakpoint-down(tbx) {
                padding: 10px 26px;
                margin-bottom: 8px;
            }
        }
        &-item {
            margin-bottom: 10px;
            @include media-breakpoint-down(tbx) {
                margin-bottom: 12px;
            }
            &:last-child {
                margin-bottom: 0;
            }
        }
        .filter__name {
            color: $color__primary;
            &::after {
                transform: rotateZ(45deg);
                margin-top: -1px;
            }
        }
    }
}