.lang {
  position: relative;
  user-select: none;

  &:hover {
    .lang__list {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
  }
  
  > a {
    position: relative;
    display: block;
    padding-right: 20px;
    cursor: pointer;

    @include media-breakpoint-down(lt){
      padding-right: 16px;
    }
    
    &:after {
      @extend %dropdown-arrow;
      position: absolute;
      top: 50%;
      right: 0;
      margin-top: -4px;
      transition: 0.2s;
      content: "";

      @include media-breakpoint-down(tb){
        right: -3px;
      }
    }
  }
  
  img {
    display: block;

    //@include media-breakpoint-down(lt){
    //  width: 18px;
    //}
    
    @include media-breakpoint-down(tb) {
      width: auto;
    }
  }
  
  &__flag {
    position: relative;

    &:after {
      @extend %dropdown-arrow;
      position: absolute;
      top: 50%;
      right: -19px;
      margin-top: -4px;
      transform: rotateZ(180deg);

      //@include media-breakpoint-down(lt){
      //  right: -16px;
      //}
      
      @include media-breakpoint-down(tb){
        right: -18px;
      }
    }
    
    &_active {
      &:after {
        content: "";
      }
    }
  }
  
  &__list {
    position: absolute;
    right: -11px;
    top: -15px;
    font-size: 18px;
    background: #fff;
    box-shadow: 0 0 12px rgba(255, 102, 0, 0.2);
    z-index: $lang-z-index;

    visibility: hidden;
    opacity: 0;
    transform: translateY(50px);
    transition: 0.2s;

    //@include media-breakpoint-down(lt){
    //  right: -14px;
    //  top: -16px;
    //}
    
    @include media-breakpoint-down(tb){
      right: -14px;
      top: -15px;
      transform: translateY(0);
    }
    
    &-table {
      display: table;
      border-collapse: collapse;
    }
    
    &-row {
      display: table-row;
      text-decoration: none;
      font-size: 18px;
      letter-spacing: 0.36px;
      cursor: pointer;

      @include media-breakpoint-down(tb){
        .lang__list-cell {
          &:last-child {
            padding: 10px 29px 10px 14px;
          }
        }
      }
      
      &:last-child {
        border-bottom: 0;

        @include media-breakpoint-down(tb){
          .lang__list-cell {
            &:last-child {
              padding: 10px 29px 14px 14px;
            }
          }
        }
      }
      
      &:hover {
        background: $color__secondary;
      }
      
      &.active {
        background: #fff;
        
        .lang__flag {
          &:after {
            content: "";
          }
        }

        @include media-breakpoint-down(tb){
          .lang__list-cell {
            &:last-child {
              padding: 16px 29px 10px 14px;
            }
          }
        }
      }
    }
    
    &-cell {
      display: table-cell;
      vertical-align: middle;
      
      &:first-child {
        padding: 12px 14px 12px 20px;

        @include media-breakpoint-down(tb){
          display: none;
        }
      }
      
      &:last-child {
        position: relative;
        padding-right: 30px;
        
        @include media-breakpoint-down(tb){
          // padding: 16px 29px 14px 14px;
        }
      }
    }
  }
  
  &_active {
    .lang__list {
      //display: block;
    }
  }
  
  &.lang_hide {
    .lang__list {
      visibility: hidden;
      opacity: 0;
    }
  }
}

.menu + .header__lang {
  margin-top: 15px;
  margin-left: 12px;

  @include media-breakpoint-down(dt) {
    margin-top: 14px;
  }
}

.header__item-lang {
  @include media-breakpoint-down(lt){
    .lang {
      
      .lang__flag {
        &:after {
          right: -16px;
          margin-top: -3px;
        }
      }
      
      .lang__list {
        top: -17px;
        right: -14px;
      }
      
      > a {
        &:after {
          margin-top: -3px;
        }
      }
      
      img {
        width: 18px;
      }
      
    }
  }
}