.facts {
  &.close {
    .facts__turn {
      display: none;
    }

    .facts-label__wrap {
      cursor: pointer;

      > span {
        @include media-breakpoint-up(tb) {
          border-bottom: 1px dotted;
        }
      }
    }

    .facts-label__text {
      @include media-breakpoint-down(phx) {
        border-bottom: 1px dotted;
      }
    }
  }


  &__header {
    display: flex;
    align-items: center;
    padding: 20px 30px;
    margin: 0 -32px 20px;
    background: $color__secondary;

    @include media-breakpoint-down(phx) {
      margin: 0 -16px 24px;
      padding: 14px 16px 16px;
    }

    .btn {
      margin-right: 20px;
      padding: 7px 20px;

      @include media-breakpoint-up(tbx) {
        min-width: 135px;
        margin-right: 72px;
      }
    }
  }

  &__turn {
    font-size: 20px;
    letter-spacing: 0.36px;
    text-align: center;

    span {
      display: inline-block;
      border-bottom: 1px dotted $color__additional;
      transition: color .25s;
      cursor: pointer;

      &:hover {
        color: $color__primary;
        border-color: $color__primary;
      }

      &::after {
        content: '';
        display: inline-block;
        margin-left: 6px;
        width: 11px;
        height: 8px;
        background: url('../img/dropdown-arrow.svg') no-repeat;
      }
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    counter-reset: section;

    @include media-breakpoint-down(phx) {
      padding: 0;
    }
  }
}

.facts-label {
  position: relative;
  display: inline-block;
  padding: 4px 20px 0;

  @include media-breakpoint-down(phx) {
    padding: 0;
  }

  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 0;
    display: block;
    width: 48px;
    height: 28px;

    @include media-breakpoint-down(phx) {
      display: none;
    }
  }
  &::before {
    left: 0;
    background-image: url("../img/svg/fact-label-left.svg");
  }
  &::after {
    right: 0;
    background-image: url("../img/svg/fact-label-right.svg");
  }

  &__wrap {
    position: relative;
    font-size: 14px;
    padding: 0 0 0 34px;
    z-index: 1;

    @include media-breakpoint-up(tb) {
      padding: 7px 20px;
      width: 395px;
      text-align: center;
      font-size: 16px;
      background: $color__text4;
      color: #fff;
    }
    @include media-breakpoint-down(phx) {
      height: 36px;
      width: 145px;
    }
  }

  &__num {
    @include media-breakpoint-down(phx) {
      position: absolute;
      top: -6px;
      left: 0;
      font-weight: bold;
      font-size: 46px;
      color: $color__text4;
    }
  }
}

.fact {
  position: relative;
  padding: 0 80px 0 0;
  margin: 0 0 40px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(220, 220, 220, 0.5);

  @include media-breakpoint-down(tb) {
    margin-bottom: 20px;
  }
  @include media-breakpoint-down(phx) {
    flex-direction: column;
    align-items: flex-start;
    padding-right: 0;
    padding-bottom: 15px;
  }

  &::before {
    content: counters(section, ".");
    counter-increment: section;
    position: absolute;
    bottom: -28px;
    right: 21px;
    font-size: 144px;
    line-height: 1;
    color: #ddd;
    font-weight: bold;
    z-index: 0;

    @include media-breakpoint-down(phx) {
      bottom: auto;
      top: 19px;
      right: 20px;
    }
  }

  &:nth-child(2n) {
    padding-left: 89px;
    padding-right: 0;
    flex-direction: row-reverse;

    @include media-breakpoint-down(phx) {
      flex-direction: column;
      align-items: flex-end;
      padding-left: 0;
    }

    &::before {
      right: auto;
      left: 28px;

      @include media-breakpoint-down(phx) {
        left: 20px;
      }
    }

    .fact__pic {
      margin-right: 0;
      margin-left: 40px;

      @include media-breakpoint-down(phx) {
        margin-left: 0;
      }
    }
  }

  &__pic {
    position: relative;
    flex-shrink: 0;
    width: 135px;
    height: 135px;
    margin-right: 40px;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 1;

    @include media-breakpoint-down(phx) {
      margin-right: 0;
      margin-bottom: 17px;
    }
  }

  &__descr {
    position: relative;
    padding: 0;
    margin: 0;
    font-size: 16px;
    z-index: 1;
  }
}