.views-products-horizontal {
  &__name {
    &.section-nav {
      margin-bottom: 16px;

      @include media-breakpoint-down(tb){
        display: block;
        text-align: center;
        margin-bottom: 36px;
      }

      .clients-section__title {
        padding: 0 12px;
        
        @include media-breakpoint-down(tb){
          font-size: 20px;
          padding: 0;
        }
      }
    }
  }
  
  &__gallery {
    position: relative;
    border-top: 1px solid $color__text2;
    border-bottom: 1px solid $color__text2;

    @include media-breakpoint-down(phx) {
      border: 0;
    }

    &::before,
    &::after {
      content: '';
      display: block;
      width: 1px;
      position: absolute;
      top: 0;
      bottom: 0;
      background: $color__text2;

      @include media-breakpoint-down(phx) {
        display: none;
      }
    }
    &::before {
      left: 0;
    }
    &::after {
      right: 0;
    }
  }
  
  &__swiper {
    
  }
  
  .swiper-slide {
    border-left: 1px solid $color__text2;

    @include media-breakpoint-down(phx){
      border: 0;
    }
  }
  .swiper-pagination-kronos {
    @include media-breakpoint-up(tb){
      display: none;
    }
  }

  .swiper-button-next-kronos,
  .swiper-button-prev-kronos {
    position: absolute;
    width: 36px;
    height: 40px;
    cursor: pointer;
    background: #fff;
    z-index: 12;
    outline: none;
    user-select: none;
    border: none;
    border-radius: 0;
    transition: 0.2s;

    @include media-breakpoint-down(phx) {
      border-radius: 50%;
    }

    &.swiper-button-disabled {
      visibility: visible;
      @include media-breakpoint-down(phx) {
        visibility: hidden;
      }
    }

    &::after {
      display: none;
      @include media-breakpoint-down(phx) {
        display: block;
      }
    }

    svg {
      display: block;
      @include media-breakpoint-down(phx) {
        display: none;
      }
    }
  }
  .swiper-button-next-kronos {
    top: -1px;
    right: 0;
    border-bottom: 1px solid $color__text2;

    @include media-breakpoint-down(phx) {
      top: auto;
      border: 1px solid $color__additional;
      width: 24px;
      height: 24px;
    }
  }
  .swiper-button-prev-kronos {
    top: -1px;
    right: 36px;
    left: auto;
    border-bottom: 1px solid $color__text2;
    border-left: 1px solid $color__text2;

    @include media-breakpoint-down(phx) {
      top: auto;
      right: auto;
      border: 1px solid $color__additional;
      width: 24px;
      height: 24px;
    }
  }
}

.views-product-horizontal {
  display: block;
  padding: 18px 26px;
  height: 323px;
  
  @include media-breakpoint-down(dtx){
    padding: 16px;
  }
  
  @include media-breakpoint-down(tbx){
    padding: 14px 12px 14px 12px;
    height: 194px;
  }
  
  @include media-breakpoint-down(tb){
    height: 220px;
  }

  @include media-breakpoint-down(phx){
    height: auto;
    padding: 0;
    display: flex;
    flex-direction: column-reverse;
  }

  &__img {
    //background: rgba(0, 255, 0, 0.15);

    @include media-breakpoint-down(tbx) {
      height: 90px;
    }
    @include media-breakpoint-down(tb) {
      height: 100px;
    }

    img {
      max-height: 100%;
      object-fit: contain;
    }
  }
  
  &__name {
    font-size: 24px;
    letter-spacing: 0.32px;
    margin-bottom: 50px;
    line-height: 1.25;
    height: (1.25em * 2);
    padding-right: 50px;
    overflow: hidden;

    @include media-breakpoint-down(dtx){
      font-size: 18px;
    }

    @include media-breakpoint-down(tbx){
      font-size: 16px;
      margin-bottom: 20px;
    }

    @include media-breakpoint-down(tb){
      font-size: 16px;
      letter-spacing: 0.4px;
    }

    &.dot-link-lines::after {
      bottom: 0;
    }
  }
  
  &:hover,
  &:focus {
    .views-product-horizontal__img + .views-product-horizontal__name {
      span {
        border-color: $color__primary;
      }
    }

    .dot-link-lines {
      &::before,
      &::after {
        width: 100%;
      }

      .dot-link-lines__name {
        color: $color__primary;
        border-bottom-color: $color__primary;
      }
    }
  }
}