.docs-gallery {
  position: absolute;
  bottom: 0;
  right: -50px;
  width: 120px;
  overflow: visible;
  
  .swiper-button-next-kronos,
  .swiper-button-prev-kronos {
    @include media-breakpoint-down(tb) {
      display: none;
    }
  }

  //remove
  .swiper-button-next-kronos {
    top: -340px;
    right: 49px;
  }
  .swiper-button-prev-kronos {
    top: -340px;
    right: 85px;
  }

  .swiper-slide {
    width: 25%;
    height: 100px;
  }

  .swiper-slide-active {
    z-index: 4;
    .docs-gallery__el {
      bottom: 0;
      opacity: 1;
    }
    .docs-gallery__more {
      opacity: 1;
    }

    + .swiper-slide {
      z-index: 3;
      .docs-gallery__el {
        bottom: -30px;
        opacity: 1;
      }

      + .swiper-slide {
        z-index: 2;
        .docs-gallery__el {
          bottom: -60px;
          opacity: 1;
        }

        + .swiper-slide {
          z-index: 1;
          .docs-gallery__el {
            opacity: 1;
          }
        }
      }
    }
  }

  &__el {
    position: absolute;
    bottom: -90px;
    right: 0;
    display: block;
    width: 235px;
    height: 330px;
    opacity: 0;
    transition: all .25s;
    cursor: pointer;

    .clients-section_nowrap & {
      width: 200px;
      height: 278px;
    }
  }

  &__pic {
    position: absolute;
    bottom: -10px;
    right: 0;
    transition: bottom .25s ease-out;
    width: 100%;
    height: 100%;

    img {
      min-height: 100%;
      min-width: 100%;
      width: 100%;
      object-fit: cover;

      .clients-section_nowrap & {
        width: 100%;
      }
    }
  }

  &__more {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    height: 46px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    letter-spacing: 0.36px;
    background: $color__primary;
    color: #fff;
    opacity: 0;
    z-index: 5;

    &:hover,
    &:focus {
      color: #fff;
    }

    svg {
      width: 20px;
      height: 20px;
      margin-right: 10px;
      fill: transparent;
      stroke: #fff;
    }
  }
}