.feedback {
  padding-top: 6px;

  .input-phone__input input,
  .input__input > input {
    height: 49px;
  }

  textarea {
    height: 151px;

    @include media-breakpoint-down(tbx) {
      height: 113px;
    }
  }

  .input-phone label,
  .input__label {
    font-size: 18px;
  }

  .checkbox,
  .radio {
    .check-cust_i + span {
      font-size: 18px;
      letter-spacing: 0.36px;
    }
  }

  .input__group_submit {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 0;
  }

  .input__group:not(:last-child) {
    @include media-breakpoint-down(tbx) {
      margin-bottom: 23px;
    }
  }

  .input__group_dense:not(:last-child) {
    @include media-breakpoint-down(tbx) {
      margin-bottom: 16px;
    }
  }

  .btn {
    padding: 4px 21px 11px;
    border-radius: 3px;
    margin-right: 20px;
  }

  [type="file"] {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }
}