.mfp-wrap {
  .mfp-close {
    font-size: 0;
    width: 44px;
    height: 44px;
    transition: color 200ms, transform 500ms;
    
    &:hover {
      color: #000;

      &:before,
      &:after {
        transform: rotate(405deg);
      }
    }

    &:after,
    &:before {
      position: absolute;
      background: $color__additional;
      top: 50%;
      left: 50%;
      content: "";
      transform: rotateZ(45deg);
      transition: transform 500ms;
      
      @include media-breakpoint-down(tb){
        background: #000;
      }
    }

    &:after {
      width: 19px;
      height: 1px;
      margin-top: -0.5px;
      margin-left: -9.5px;
    }

    &:before {
      width: 1px;
      height: 19px;
      margin-top: -9.5px;
      margin-left: -0.5px;
    }
    
  }
  
  &.popup_image,
  &.popup_gallery,
  &.popup_video-gallery {
    .mfp-close {
      width: 70px;
      height: 70px;
      background: #000;
      color: #fff;
      opacity: 1;

      &:hover {
        transform: rotate(0);
        &:before,
        &:after {
          transform: rotate(405deg);
        }
      }

      @include media-breakpoint-down(tbx) {
        width: 40px;
        height: 40px;
      }

      &:after,
      &:before {
        position: absolute;
        background: #fff;
        top: 50%;
        left: 50%;
        content: "";
        transform: rotateZ(45deg);
        transition: transform 500ms;
      }

      &:after {
        width: 31px;
        height: 3px;
        margin-top: -1.5px;
        margin-left: -15.5px;

        @include media-breakpoint-down(tbx) {
          width: 18px;
          height: 2px;
          margin-top: -1px;
          margin-left: -9px
        }
      }

      &:before {
        width: 3px;
        height: 31px;
        margin-top: -15.5px;
        margin-left: -1.5px;

        @include media-breakpoint-down(tbx) {
          width: 2px;
          height: 18px;
          margin-top: -9px;
          margin-left: -1px
        }
      }
    }
  }

  &.popup_gallery {
    .mfp-content {
      position: static;
    }

    
  }

  &.popup_video-gallery {
    .mfp-content {
      max-width: 970px;

      @include media-breakpoint-down(tbx) {
        max-width: 590px;
      }
    }

    .mfp-container {
      right: 0;
      margin: 0 auto;
      padding-left: 60px;
      padding-right: 60px;
      width: auto;
      max-width: calc(970px + 192px);

      @include media-breakpoint-down(tbx) {
        max-width: calc(580px + 192px);
      }

      @include media-breakpoint-down(phx) {
        padding-left: 0;
        padding-right: 0;
      }
    }

    .mfp-close {
      padding: 0;
      right: 0;
      top: 0;
    }
  }

  &.popup_image,
  &.popup_video-gallery {
    .mfp-arrow {
      width: 46px;
      height: 46px;
      margin-top: -23px;
      border-radius: 50%;
      background: #000;
      opacity: 1;
      overflow: hidden;

      @include media-breakpoint-down(tbx) {
        width: 40px;
        height: 40px;
      }

      @include media-breakpoint-down(phx) {
        display: none;
      }

      &::before {
        top: 14px;
        left: 18px;
        width: 14px;
        height: 14px;
        border: none;
        border-left: 3px solid #fff;
        border-top: 3px solid #fff;
        margin: 0;
        opacity: 1;

        @include media-breakpoint-down(tbx) {
          top: 13px;
          left: 16px;
          width: 11px;
          height: 11px;
        }
      }
      &::after {
        display: none;
      }
    }

    .mfp-arrow-left {
      left: 10px;

      &::before {
        transform: rotate(-45deg);
      }
    }

    .mfp-arrow-right {
      right: 10px;

      &::before {
        left: 12px;
        transform: rotate(135deg);
      }
    }
  }

  &.popup_image {
    .mfp-container {
      @include media-breakpoint-down(phx) {
        padding: 0;
      }
    }

    .mfp-img {
      padding-top: 100px;

      @include media-breakpoint-down(tb) {
        padding-top: 110px;
      }
      @include media-breakpoint-down(phx) {
        padding-top: 0;
        padding-bottom: 0;
      }
    }

    .mfp-figure {
      &:after {
        top: 100px;

        @include media-breakpoint-down(tb) {
          top: 110px;
        }
        @include media-breakpoint-down(phx) {
          top: 0;
          bottom: 0;
        }
      }

      > .mfp-close {
        display: none;
      }
    }
  }
  
  &.popup-fixed {
    .popup {
      margin-top: 16px;
      margin-bottom: 16px;
    }
  }
}

