.popup {
  position: relative;
  margin: 16px auto;
  background: #fff;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.2);
  
  &__name {
    font-size: 22px;
    letter-spacing: 0.36px;
    text-align: center;

    @include media-breakpoint-down(lt) {
      font-size: 18px;
    }

    @include media-breakpoint-down($mobile-switch){
      font-size: 20px;
    }
  }
  
  &__row {
    @include clearfix();
    margin: 0 -17px 33px;

    @include media-breakpoint-down($mobile-switch){
      margin: 0;
    }
    
    &_btn {
      text-align: center;

      .popup__col {
        margin-bottom: 0;
      }
    }

    &_line {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: -17px;
      margin-right: -17px;

      @include media-breakpoint-down(phx) {
        flex-direction: column;
      }

      &::after {
        display: none;
      }

      .popup__col {
        float: none;
        width: auto;
        padding: 0 17px;
        margin-bottom: 0;

        + .popup__col {
          @include media-breakpoint-down(phx) {
            margin-top: 23px;
          }
        }
      }
    }

    &_space-between {
      justify-content: space-between;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
  
  &__col {
    float: left;
    min-height: 1px;
    padding: 0 17px;
    width: 100%;

    @include media-breakpoint-down($mobile-switch){
      float: none;
      padding: 0;
      width: auto;
      margin-bottom: 32px;
    }
    
    &_53 {
      width: 53%;

      @include media-breakpoint-down($mobile-switch){
        width: auto;
      }
    }
    
    &_50 {
      width: 50%;

      @include media-breakpoint-down($mobile-switch){
        width: auto;
      }
    }
    
    &_47 {
      width: 47%;

      @include media-breakpoint-down($mobile-switch){
        width: auto;
      }
    }
  }
  
  
  .mfp-close {
    
  }
  
}

@include media-breakpoint-up(tbx) {

  .popup-animate-left {
    position: relative;
    transition: left 0.4s, opacity 0.4s;
    transition-timing-function: linear;
    left: -1000px;
    opacity: 0;

    &.show {
      left: 0;
      opacity: 1;
    }
  }

  .popup-animate-right {
    position: relative;
    transition: right 0.4s, opacity 0.4s;
    transition-timing-function: linear;
    right: -1000px;
    opacity: 0;

    &.show {
      right: 0;
      opacity: 1;
    }
  }

  .popup-animate-top {
    position: relative;
    transition: top 0.4s, opacity 0.4s;
    transition-timing-function: linear;
    top: -1000px;
    opacity: 0;

    &.show {
      top: 0;
      opacity: 1;
    }
  }

  .popup-animate-scale {
    position: relative;
    transition: transform 0.4s;
    transition-timing-function: linear;
    transform: scale(0);

    &.show {
      transform: scale(1);
    }
  }

  @keyframes popup-animate-close-top {
    from {
      top: -1000px;
      opacity: 0;
      color: #fff;
    }
    80% {
      color: #fff;
    }
    to {
      top: 0;
      opacity: 1;
      color: $color__additional;
    }
  }
  .popup-animate-close-top {
    .mfp-close {
      top: -1000px;
      opacity: 0;
      animation: popup-animate-close-top 0.5s linear forwards;
    }

    &.show {
      .mfp-close {

      }
    }
  }
  
}




