.gift {
  position: relative;
  width: 100px;
  
  img {
    @extend .img-responsive;
    margin: 0 auto;
  }
  
  &__plus {
    position: absolute;
    top: -10px;
    left: 0;
    width: 19px;
    height: 19px;
    border-radius: 50%;
    background: $color__additional;
    transition: .2s;
    
    &:before,
    &:after {
      position: absolute;
      top: 50%;
      left: 50%;
      background: #fff;
      content: "";
    }
    
    &:before {
      width: 1px;
      height: 11px;
      margin-top: -5.5px;
      margin-left: -0.5px;
    }
    
    &:after {
      width: 11px;
      height: 1px;
      margin-top: -0.5px;
      margin-left: -5.5px;
    }
  }
  
  &:hover {
    .gift__plus {
      background: $color__text3;
    }
  }
  
  &_112x63 {
    display: flex;
    align-items: center;
    width: 112px;
    height: 63px;
    
    .gift__plus {
      top: 0;
    }
  }
}