.banner-product {
  $rootSelector: &;
  
  position: relative;
  
  &__normal {
    position: relative;
    background: #fff;
    height: 220px;
    //padding: 12px 14px 8px;
    padding: 12px 14px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include media-breakpoint-down(dtx){
      padding: 12px 10px;
    }
  }

  &__banner {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 220px;
  }
  
  &__open {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    top: -1px;
    min-height: calc(100% + 2px);
    width: calc(25% - 24px);
    z-index: 11;
    
    @include media-breakpoint-down(tbx){
      width: calc(50% - 32px);
    }
  }
  
  &__category {
    margin-bottom: 5px;
    
    font-size: 16px;
    letter-spacing: 0.4px;
    color: $color__additional;
    line-height: 1;
    
    + #{$rootSelector}__name {
      &_line2 {
        .banner-products__item_mini & {
          margin-top: 1px;
        }
      }
    }
  }
  
  &__name {
    a {
      display: inline-block;
      font-size: 21px;
      letter-spacing: 0.36px;
      overflow: hidden;
      height: ((21px * 1.15) * 1);
      text-overflow: ellipsis;
      max-width: 100%;

      @include media-breakpoint-down(dtx){
        font-size: 18px;
        height: ((18px * 1.15) * 1);
      }
    }

    &_line2 {

      a {
        height: ((21px * 1.15) * 2);

        @include media-breakpoint-down(dtx){
          font-size: 21px;
          height: ((21px * 1.15) * 2);

          .banner-products__item_mini & {
            font-size: 18px;
            height: ((18px * 1.15) * 2);
          }
        }
      }
    }
  }
  
  &__status {
    margin-top: 4px;
    
    .status {
      font-size: 16px;
      padding-left: 19px;
      
      &:before {
        left: 1px;
        top: 4px;
      }
    }
  }
  
  &__img {
    position: absolute;
    
    img {
      @extend .img-responsive;
      margin: 0 auto;
    }
    
    .banner-products__item_mini & {
      width: 184px;
      height: 126px;
      top: 54px;
      right: -77px;
    }

    .banner-products__item_normal &,
    .banner-products__item_full & {
      width: 300px;
      height: 168px;
      top: 14px;
      right: 9px;

      @include media-breakpoint-down(dt){
        width: 200px;
      }

      @include media-breakpoint-down(lt){
        width: 300px;
      }
      
      @include media-breakpoint-down(tbx){
        width: 284px;
        height: 159px;
      }
    }
  }
  
  &__price {
    
    .price-old {
      font-size: 16px;
      letter-spacing: 0.4px;
      margin-bottom: 6px;

      //@include media-breakpoint-down(lt){
      //  margin-bottom: 2px;
      //}
    }
    
    .price {
      font-size: 22px;
      letter-spacing: 0.36px;
    }
    
  }
  
  &__triggers {
    ul {
      @extend %list;
      
      li {
        position: relative;
        font-size: 16px;
        letter-spacing: 0.4px;
        padding-left: 30px;
        margin-bottom: 14px;

        @include media-breakpoint-down(lt){
          color: $color__additional;
        }
        
        &:last-child {
          margin-bottom: 0;
        }
        
        img {
          position: absolute;
          top: 0;
          left: 0;
        }

        &.item-gift {
          img {
            left: 3px;
            top: -5px;
          }
        }
      }
    }

    + #{$rootSelector}__price {
      margin-top: 14px;

      @include media-breakpoint-down(lt){
        margin-top: 4px;
      }
    }
  }
  
  &__zoom {
    position: absolute;
    bottom: 10px;
    right: 10px;

    @include media-breakpoint-down(tbx){
      bottom: 17px;
      right: 17px;
    }
    
    svg {
      display: block;
      width: 22px;
      height: 22px;
      fill: none;
      stroke: $color__additional;
      
      @include media-breakpoint-down(tbx){
        width: 17px;
        height: 18px;
      }
    }
  }

  &__spec {
    font-size: 14px;
    letter-spacing: 0.4px;
    margin-bottom: 2px;
  }
  
  &_left {
    
  }
  
  &_right {
    
  }

  &:hover,
  &_open {
    //#{$rootSelector}__open {
    //  visibility: visible;
    //  opacity: 1;
    //}
  }

  &_x_auto_y_auto {}
  &_x_auto_y_2 {
    #{$rootSelector}__banner {
      height: ((220px * 2) + 1);
    }
  }

  &_x_auto_y_3 {
    #{$rootSelector}__banner {
      height: ((220px * 3) + 2);
    }
  }

  &_x_auto_y_4 {
    #{$rootSelector}__banner {
      height: ((220px * 4) + 3);
    }
  }
  
}

.banner-product-open {
  background: #fff;
  border: 1px solid $color__text2;
  //padding: 16px 19px;
  padding: 16px 14px;

  @include media-breakpoint-down(dtx){
    padding: 12px 10px;
  }
  
  &__top {
    display: flex;
    justify-content: space-between;
    
    &-left {}
    &-right {}
    
  }
  
  &__action {
    display: flex;
    align-items: center;
    
    a {
      margin-right: 14px;
      
      &.btn {
        padding: 7px 14px;
        white-space: nowrap;
      }
      
      &:last-child {
        margin-right: 0;
      }
    }
  }
  
  &__main {
    display: flex;
    align-items: flex-end;
    
    &-left,
    &-right {
      flex-shrink: 0;
      flex-grow: 0;
    }
    
    &-left {
      width: 35%; 
    }
    &-right {
      width: 65%;
      padding-bottom: 20px;
    }
  }
  
  &__image {
    position: relative;
    background: rgba(41, 167, 16, 0.15);
    width: 272px;
    height: 153px;
    margin-left: auto;

    &-more {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(black, 0.5);
      cursor: pointer;
      transition: 0.2s;
      opacity: 0;
      visibility: hidden;
      color: #fff;
      font-size: 16px;
      letter-spacing: 0.4px;
      display: flex;
      align-items: center;
      justify-content: center;

      @include media-breakpoint-down(dtx){
        font-size: 14px;
      }


      &:focus,
      &:hover {
        color: #fff;
      }

      img {
        display: block;
        margin: 0 auto 15px;
      }
    }

    &-preload {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      visibility: hidden;
      background: white;

      &.show {
        opacity: 1;
        visibility: visible;
      }
    }
    
    img {
      @extend .img-responsive;
      margin-left: auto;
    }

    &_open {
      background: rgba(0, 255, 0, 0.15);

      .banner-product-open__image-more {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  
  &__category {
    font-size: 16px;
    letter-spacing: 0.4px;
    color: $color__additional;
  }
  
  &__name {
    font-size: 0;
    margin-top: 12px;
    
    a {
      font-size: 21px;
      letter-spacing: 0.36px;
    }
  }
  
  &__status {
    margin-top: 12px;
    
    .status {
      font-size: 16px;
      padding-left: 18px;
      
      &:before {
        top: 4px;
      }
    }
  }
  
  &__count {
    a {
      display: inline-block;
      font-size: 14px;
      letter-spacing: 0.4px;
      
      span {
        white-space: normal;
      }
    }
  }
  
  &__price {
    
    .price-old {
      font-size: 18px;
      letter-spacing: 0.36px;
      margin-bottom: 14px;
    }

    .price {
      font-size: 25px;
      letter-spacing: 0.32px;
      margin-bottom: 24px;
    }
    
    &_old {
      .price {
        margin-bottom: 10px;
      }
    }
  }
  
  &__spec {
    font-size: 14px;
    letter-spacing: 0.4px;
    color: $color__additional;
    margin-bottom: 12px;
  }
  
  &__props {
    margin-bottom: 18px;
  }
  
  &__pagination {
    margin: 16px 0 24px;
    
    .pagination-line {
      a {
        height: 4px;
      }
    }
  }
  
  &__gift {
    display: flex;
    justify-content: center;
    margin: 28px 0 15px;
    
    .gift {
      width: 90px;
    }
  }
  
  &__triggers {
    
    ul {
      @extend %list;
      li {
        position: relative;
        font-size: 16px;
        letter-spacing: 0.4px;
        padding-right: 30px;
        color: $color__additional;
        
        img {
          position: absolute;
          top: 0;
          right: 0;
        }
      }
    }
    
    &-left {
      margin-bottom: 26px;
      
      ul {
        @extend %list;
        li {
          position: relative;
          font-size: 16px;
          letter-spacing: 0.4px;
          padding-left: 32px;
          margin-bottom: 20px;
          
          &:last-child {
            margin-bottom: 0;
          }

          img {
            position: absolute;
            top: 0;
            left: 0;
          }
          
          &.item-gift {
            img {
              left: 3px;
              top: -5px;
            }
          }
        }
      }
    }
  }
  
  &_open {
    visibility: visible;
    opacity: 1;
  }
}