.section-title {
  position: relative;
  display: block;
  margin: 0 0 41px;
  color: $color__text;
  font-size: 32px;
  font-weight: normal;
  letter-spacing: 0.32px;
  text-align: center;

  @include media-breakpoint-down(phx) {
    font-size: 20px;
    line-height: 1.25;
  }

  &::before {
    content: '';
    position: absolute;
    top: 46%;
    left: 0;
    right: 0;
    display: block;
    height: 1px;
    background: $color__text2;

    @include media-breakpoint-down(phx) {
      display: none;
    }
  }

  span {
    position: relative;
    display: inline-block;
    padding: 0 40px;
    background: #fff;
    z-index: 1;
  }
}