.why-buy {
  background: $color__secondary;
  margin: 0 (-$container-padding-tbx);
  padding: 22px 0;
  
  @include media-breakpoint-down(phx){
    margin: 0 -($container-padding-phx);
  }
  
  &__name {
    text-align: center;
    font-size: 20px;
    letter-spacing: 0.36px;
    margin-bottom: 15px;
    
    a {
      padding-right: 0;
      text-transform: none;
    }
    
    .dot-link-one-line__arrow {
      top: 9px;
      right: -14px;
    }
  }
  
  &__gallery {
    //padding-bottom: 50px;
  }
  
  &__swiper {
    padding: 0 50px;
  }

  .swiper-button-prev-kronos,
  .swiper-button-next-kronos {
    //bottom: 16px;
    background: transparent;
  }
  
  .swiper-button-prev-kronos {
    left: 31px;
  }
  
  .swiper-button-next-kronos {
    right: 31px;
  }
}