@keyframes logo-animation {
  from {
    stroke-dasharray: 0 420;
  }
  50% {
    stroke-dasharray: 420 420;
    stroke-dashoffset: 0;
  }
  to {
    stroke-dashoffset: -420;
  }
}

@keyframes logo-animation-footer {
  from {
    stroke-dasharray: 0 320;
  }
  50% {
    stroke-dasharray: 320 320;
    stroke-dashoffset: 0;
  }
  to {
    stroke-dashoffset: -320;
  }
}

@keyframes logo-animation-default {
  0% {
    opacity: 0;
    left: 0;
  }
  90% {
    opacity: 0;
    left: 0;
  }
  95% {
    opacity: .5;
  }
  to {
    opacity: 1;
    left: 110%;
  }
}

@keyframes logo-animation-hover {
  from {
    opacity: 0;
    left: 0;
  }
  50% {
    opacity: .5;
  }
  to {
    opacity: 0;
    left: 110%;
  }
}

.logo {
  flex: 0 0 auto;
  
  @include media-breakpoint-down(dt){
    width: 150px;
    margin-top: 6px;
  }

  @include media-breakpoint-down(lt){
    width: 183px;
    margin-top: 5px;
  }

  @include media-breakpoint-down($mobile-switch){
    width: auto;
    margin-top: 0;
    opacity: 0;
    transform: translateX(-10px);
    transition: 1s ease;

    .anim-mobile-header & {
      opacity: 1;
      transform: translateX(0);
    }
  }
  
  a {
    position: relative;
    display: block;
    padding-bottom: 30px;
    overflow: hidden;

    @include media-breakpoint-down(tb) {
      padding-bottom: 0;
    }
    
    &:hover {
      .logo__text {
        svg {
          rect {
            stroke-dasharray: 420;
          }
        }
      }

      &:before {
        animation: logo-animation-hover .7s ease-in-out forwards;
      }
    }

    &:before{
      position: absolute;
      top: 0;
      width: 40px;
      height: 100%;
      content: '';
      background: #eee;
      opacity: 0.5;
      transition: none;
      transform: skewX(-20deg);
      animation: logo-animation-default 9.5s ease-in-out infinite;
      z-index: 3;

      .header_line & {
        background: #fff;
      }
    }
  }
  
  &__text {
    position: absolute;
    text-transform: uppercase;
    color: $color__primary;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: 0.24px;
    line-height: 16px;
    top: 42px;
    left: 55px;

    @include media-breakpoint-down(dt) {
      font-size: 10px;
      top: 32px;
      left: 40px
    }

    @include media-breakpoint-down(lt) {
      top: 37px;
      left: 47px;
    }

    @include media-breakpoint-down(tbx){
      display: none;
    }
    
    svg {
      position: absolute;
      top: -3px;
      left: -3px;
      width: calc(100% + 6px);
      height: calc(100% + 6px);
      fill: none;
      
      rect {
        stroke-dasharray: 0 420;
        stroke-width: 2px;
        stroke: $color__primary;
        transition: stroke-width 1s, stroke-dasharray 1s, stroke-dashoffset 1s;
      }
    }
  }
  
  &_animation {
    svg {
      rect {
        animation-name: logo-animation;
        animation-duration: 2s;
        animation-iteration-count: 1;
      }
    }
  }
}