.header {

  &__mob {
    position: relative;
    width: 100%;
    z-index: $header-z-index;
    background: $color__secondary;
    transition: 0.2s;
    display: none;
    padding-bottom: 20px;
    padding-top: 55px;

    @include media-breakpoint-down($mobile-switch){
      display: block;
    }
  }
  
}

.header-info-mob {
  display: flex;
  justify-content: space-between;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px $container-padding-tbx;
  transition: .3s ease;
  z-index: 1000;
  
  @include media-breakpoint-down(phx){
    padding: 20px $container-padding-phx;
  }
  
  &__left,
  &__right {}

  .header_line & {
    background: #fff;
    box-shadow: 0 0 12px rgba(255, 102, 0, 0.4);
  }
}

