.menu-mob {
  ul {
    @extend %list;
    display: flex;
    
    li {        
      position: relative;
      margin-right: 13px;

      @include media-breakpoint-down($mobile-switch) {
        opacity: 0;
        transform: translateX(10px);

        .anim-mobile-header & {
          opacity: 1;
          transform: translateX(0);
        }

        &:nth-child(1) {
          transition: .6s 1.1s ease;
        }
        &:nth-child(2) {
          transition: .6s .8s ease;
        }
        &:nth-child(3) {
          transition: .6s .7s ease;
        }
        &:nth-child(4) {
          transition: .6s .5s ease;
        }
      }
      
      &:last-child {
        margin-right: 0;
      }
      
      a {
        display: block;
        
        svg {
          display: block;
          stroke: $color__additional;
          fill: none;
          
          &.menu-mob-phone {
            width: 18px;
            height: 18px;
          }
          
          &.menu-mob-mail {
            width: 21px;
            height: 16px;
          }
          
          &.menu-mob-location {
            margin-top: -1px;
            width: 15px;
            height: 19px;
          }
        }
      }
      
      &.open {
        z-index: $popup-z-index + 1;
        
        a {
          position: relative;
          
          &:before {
            position: absolute;
            top: 50%;
            left: 50%;
            margin-top: -17px;
            margin-left: -17px;
            width: 34px;
            height: 34px;
            border-radius: 50%;
            background: $color__secondary;
            content: "";
          }
          
          svg {
            position: relative;

            &.menu-mob-location {
              transform: scale(0.78);
            }

            &.menu-mob-phone {
              transform: scale(0.8);
            }
          }
        }
      }
    }
  }
}

.hamburger {
  display: block;
  cursor: pointer;
  
  &__line {
    width: 24px;
    height: 2px;
    background: $color__additional;
    margin-bottom: 6px;
    
    &:last-child {
      margin-bottom: 0;
    }
  }
  
}