.menu-mob-popup-shop {
  padding: 42px 24px 18px;

  &__name {
    letter-spacing: 0.36px;
    margin-bottom: 8px;
  }

  &__map,
  &__desc,
  &__city {
    letter-spacing: 0.4px;
    font-size: 16px;
  }

  &__city {
    color: $color__primary;
    margin-bottom: 12px;
  }

  &__desc {
    p {
      margin: 10px 0;
      &:last-child {
        margin-bottom: 8px;
      }
    }
  }

  &__gallery {
    margin: 20px -24px 0;
    padding-bottom: 39px;

    .swiper-button-prev-kronos {
      left: 17px;
      //bottom: 7px;
    }

    .swiper-button-next-kronos {
      right: 17px;
      //bottom: 7px;
    }

    .lupa {
      position: absolute;
      bottom: 10px;
      right: 16px;
      width: 24px;
      height: 24px;
    }

    &-item {
      position: relative;
      display: block;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      padding-top: 46%;

      &-desc {
        position: absolute;
        bottom: 0;
        left: 0;
        color: #fff;
        padding: 12px 16px;
      }

      &-name {
        font-size: 14px;
        letter-spacing: 0.4px;
        margin-bottom: 4px;
      }

      &-type {
        font-size: 18px;
        letter-spacing: 0.36px;
      }
    }
  }

}