.input {
  position: relative;
  border: 1px solid $color__text2;
  border-radius: 3px;
  background: #fff;

  &__group {
    margin-bottom: 32px;

    &_dense {
      margin-bottom: 16px
    }
  }
  
  &__label {
    position: absolute;
    top: -13px;
    left: 13px;
    font-size: 18px;
    letter-spacing: 0.36px;
    color: $color__additional;
    background: #fff;
    padding: 0 4px;
    z-index: 1;

    @include media-breakpoint-down(dt){
      font-size: 16px;
      letter-spacing: 0.4px;
    }

    @include media-breakpoint-down($mobile-switch){
      top: -12px;
      left: 7px;
    }
  }
  
  &__input {
    position: relative;

    > textarea,
    > input {
      display: block;
      width: 100%;
      height: 49px;
      border: none;
      outline: none;
      background: none;
      font-size: 20px;
      letter-spacing: 0.36px;
      padding: 17px 17px 9px;

      @include media-breakpoint-down($mobile-switch){
        height: 41px;
        font-size: 18px;
        padding: 10px 12px 8px;
      }
    }

    > textarea {
      height: auto;
      min-height: 98px;
      resize: none;
    }
  }
  
  &_file-textarea {
    padding-bottom: 35px;

    .input__input {
      > textarea {
        min-height: 100px;
        
        @include media-breakpoint-down(tbx){
          min-height: 92px;
        }
        
        @include media-breakpoint-down(tb){
          min-height: 63px;  
        }
      }
    }
  }
}

.input-phone {
  position: relative;
  border: 1px solid $color__text2;
  background: #fff;
  border-radius: 3px;
  min-width: 342px;
  
  &.popup-animate-scale {
    z-index: 1;
  }

  @include media-breakpoint-down(dt){
    min-width: 322px
  }
  
  > label {
    position: absolute;
    top: -13px;
    left: 13px;
    font-size: 18px;
    letter-spacing: 0.36px;
    color: $color__additional;
    background: #fff;
    padding: 0 4px;
    z-index: 1;
    
    @include media-breakpoint-down(dt){
      font-size: 16px;
      letter-spacing: 0.4px;
    }

    @include media-breakpoint-down($mobile-switch){
      top: -12px;
      left: 7px;
    }
  }
  
  &__row {
    display: flex;
    align-items: center;
  }
  
  &__placeholder {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    //height: 49px;
    font-size: 20px;
    letter-spacing: 0.36px;
    padding: 16px 17px 0;
    background: #fff;
    cursor: text;
    outline: none;
    white-space: nowrap;
    overflow: hidden;
    
    &-inner {
      width: 100%;
      text-align: left;
      overflow: hidden;
    }

    @include media-breakpoint-down($mobile-switch){
      //height: 41px;
      font-size: 18px;
      padding: 13px 12px 0;
    }

    .input-phone.hide_placeholder & {
      display: none;
    }
  }
  
  &__code {
    color: black;
  }
  
  &__phone {
    color: $color__additional;
  }
  
  &__input {
    position: relative;
    flex: 1 1 auto;
    border-right: 1px solid $color__text2;
    
    input {
      display: block;
      width: 100%;
      //height: 49px;
      border: none;
      outline: none;
      background: none;
      font-size: 20px;
      letter-spacing: 0.36px;
      padding: 17px 17px 9px;

      @include media-breakpoint-down($mobile-switch){
        //height: 41px;
        font-size: 18px;
        padding: 14px 12px 7px;
      }
    }
  }
  
  &__lang {
    width: 78px;
    flex: 0 0 auto;
    padding: 4px 0 0;

    @include media-breakpoint-down(tb){
      width: 66px;
      padding: 2px 0 0;
    }
    
    .lang {
      width: 43px;
      margin: 0 auto;
      
      @include media-breakpoint-up(tbx){
        &__list {
          top: -20px;
          right: -19px;
          
          &-cell {
            &:last-child {
              padding-right: 38px;  
            }
          }
          
          &-row {
            &.active {
              .lang__list-cell {
                vertical-align: top;
                &:first-child {
                  padding-top: 16px;
                  padding-bottom: 8px;
                }
                &:last-child {
                  padding-top: 20px;
                }
              }
            }
          }
        }
      }

      @include media-breakpoint-down(tb){
        width: 39px;
      }
    }
  }
  
  &_mob {
    border: 0;
    min-width: 0;

    .input-phone__lang {
     // width: 70px;
      .lang {
        //width: 43px;
      }
    }
    .input-phone__input {
      input {
        height: 36px;
      }
    }
  }
  
  &_form1 {

    @include media-breakpoint-between(tbx, lt) {
      width: 312px;
      min-width: 0;
    }
    
    .input-phone {
      
      &__placeholder {
        @include media-breakpoint-between(tbx, lt){
          padding: 10px 17px 0;
        }
      }
      &__input {
        input {
          @include media-breakpoint-between(tbx, lt){
            padding: 11px 17px 6px;
          }
        }
      }
    }
  }
}

.input-file {
  $rootSelector: &;
  
  label {
    display: flex;
    align-items: center;
    cursor: pointer;

    &:hover {
      .input-file__name {
        color: $color__primary;
      }
    }
  }

  &__name {
    font-size: 16px;
    word-break: break-all;
    transition: color .25s;

    > span {
      @include media-breakpoint-down(tb){
        border-bottom: 1px dotted currentColor;
      }
    }
  }

  &__icon {
    stroke: none;
    flex-shrink: 0;
    width: 23px;
    height: 24px;
    margin-right: 15px;
    
    &_desk {
      @include media-breakpoint-down(tb){
        display: none;
      }
    }
    
    &_mob {
      display: none;
      @include media-breakpoint-down(tb){
        display: block;
      }
    }
  }
  
  &__input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }
  
  &_textarea {
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 0 17px 8px 17px;
    width: 100%;
    
    @include media-breakpoint-down(tbx){
      padding: 0 14px 11px 14px;
    }
    
    @include media-breakpoint-down(tb){
      padding: 0 10px 9px 12px;
    }
    
    label {
      align-items: flex-start;
      justify-content: flex-end;
    }
    
    #{$rootSelector}__name {
      font-size: 16px;
      letter-spacing: 0.4px;
      padding-top: 2px;
      max-width: calc(100% - 38px);

      @include media-breakpoint-down(tb){
        font-size: 14px;
        padding-top: 0;
        max-width: calc(100% - 25px);
      }
      
      > span {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap !important;
        width: 100%;
        display: block;
      }
    }
    
    #{$rootSelector}__icon {
      width: 23px;
      height: 24px;
      fill: $color__primary;
      
      &_desc {
        margin: 0 10px 0 0;
      }
      
      &_mob {
        margin: 0 0 0 10px;
      }
      
      @include media-breakpoint-down(tb){
        width: 15px;
        height: 16px;
        fill: $color__additional;
      }
    }
  }
}


.checkbox,
.radio {
  input[type='checkbox'],
  input[type='radio'] {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
  }

  label {
    position: relative;
    display: inline-block;
    padding-left: 32px;
    user-select: none;
    cursor: pointer;

    @include media-breakpoint-down(tbx){
      padding-left: 28px;
    }

    //&:hover {
    //  .check-cust_i {
    //    border-color: #C2C5C9;
    //  }
    //}
  }

  .check-cust_i {
    position: absolute;
    top: 2px;
    left: 0;
    width: 18px;
    height: 18px;
    border: 1px solid #999;
    background: #fff;
    transition: all 0.2s;

    @include media-breakpoint-down(dtx){
      top: 1px;
    }

    @include media-breakpoint-down(tbx){
      top: 3px;
      width: 14px;
      height: 14px;
    }
    

    &:after {
      position: absolute;
      top: 2px;
      left: 2px;
      opacity: 0;
      transition: opacity 0.2s;
      content: "";

      @include media-breakpoint-down(tbx){
        top: 1px;
        left: 1px;
      }
    }

    + span {
      font-size: 20px;
      letter-spacing: 0.36px;
      transition: 0.2s;

      @include media-breakpoint-down(dtx){
        font-size: 18px;
      }

      @include media-breakpoint-down(tbx){
        font-size: 16px;
        letter-spacing: 0.4px;
      }
    }
  }

  .check-cust {
    &:checked + .check-cust_i {
      border-color: $color__primary;

      &:after {
        opacity: 1;
      }

      + span {
        color: $color__primary;
      }
    }

    &:disabled + .check-cust_i {
      //background: #F2F2F2;
      border-color: #dcdcdc;

      + span {
        color: #DCDCDC;

        .check-count {
          color: #DCDCDC;
        }
      }
    }
  }

  .check-count {
    color: #B3B3B3;
  }
}

.checkbox {
  
  .check-cust_i {
    //border-radius: 2px;

    &:after {
      width: 100%;
      height: 100%;
      background: url("../img/check.svg") no-repeat;


      @include media-breakpoint-down(tbx){
        background-image: url("../img/check-mini.svg");
      }
    }
  }

  .check-cust {
    &:disabled + .check-cust_i {
      &:after {
        background: url("../img/check-disabled.svg") no-repeat;

        @include media-breakpoint-down(tbx){
          background-image: url("../img/check-mini-disabled.svg");
        }
      }
    }
  }
  
  &_big {
    min-height: 26px;
    
    label {
      padding-top: 3px;
    }

    .check-cust {
      &:disabled + .check-cust_i {
        &:after {
          background: url("../img/check-big-disabled.svg") no-repeat;

          @include media-breakpoint-down(tb){
            background-image: url("../img/check-mini-disabled.svg");
          }
        }
      }
    }
    
    .check-cust_i {
      width: 26px;
      height: 26px;
      
      @include media-breakpoint-down(tb){
        width: 16px;
        height: 16px;
      }
      
      &:after {
        top: 3px;
        left: 3px;
        background: url("../img/check-big.svg") no-repeat;

        @include media-breakpoint-down(tb){
          top: 2px;
          left: 2px;
          background-image: url("../img/check-mini.svg");
        }
      }
    }
  }

  &_disabled {
    label {
      cursor: default;
    }
  }

  &_inline-no-text {
    label {
      padding: 0;
    }
    .check-cust_i {
      position: relative;
      display: inline-block;
      top: auto;
      left: auto;
    }
  } 
  
  .feedback &,
  &_feedback {
    .check-cust_i {
      width: 18px;
      height: 18px;
      top: 2px;
      
      &:after {
        background: url("../img/check.svg") no-repeat;
        top: 2px;
        left: 2px;
      }
    }
  }
  
}

.radio {
  .check-cust_i {
    border-radius: 50%;

    &:after {
      background: $color__primary;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      top: 50%;
      left: 50%;
      margin-top: -4px;
      margin-left: -4px;
    }
  }

  &_disabled {
    label {
      cursor: default;
    }

    .check-cust {
      &:disabled + .check-cust_i { 
        &:after {
          background: #DCDCDC;
        }
      }
    }
  }
  
  &_big {
    .check-cust_i {
      width: 29px;
      height: 29px;
      border: 2px solid transparent;
      transition: none;
      
      @include media-breakpoint-down(tb){
        width: 23px;
        height: 23px;
      }
      
      &:after {
        width: 21px;
        height: 21px;
        border: 2px solid $color__additional;
        background: #fff;
        margin-top: -10.5px;
        margin-left: -10.5px;
        opacity: 1;
        box-sizing: border-box;
        transition: none;

        @include media-breakpoint-down(tb){
          width: 17px;
          height: 17px;
          margin-top: -8.5px;
          margin-left: -8.5px;
        }
      }
      
      &:before {
        position: absolute;
        width: 13px;
        height: 13px;
        margin-top: -6.5px;
        margin-left: -6.5px;
        background: $color__primary;
        border-radius: 50%;
        top: 50%;
        left: 50%;
        content: "";
        opacity: 0;
        z-index: 1;

        @include media-breakpoint-down(tb){
          width: 11px;
          height: 11px;
          margin-top: -5.5px;
          margin-left: -5.5px;
        }
      }
    }

    .check-cust {
      
      &:checked + .check-cust_i {
        border-color: $color__primary;
        
        &:after {
          opacity: 0;
        }
        &:before {
          opacity: 1;
        }
      }
      
    }
    
  }
  
  &_inline-no-text {
    label {
      padding: 0;
    }
    .check-cust_i {
      position: relative;
      display: inline-block;
      top: auto;
      left: auto;
    }
  }
}