.clients-section {
  margin-bottom: 70px;

  @include media-breakpoint-down(tbx) {
    margin-bottom: 125px;
  }
  
  @include media-breakpoint-down(tb){
    margin-bottom: 47px;
  }
  
  @include media-breakpoint-down(phx) {
    margin-bottom: 22px;
  }

  .section-title {
    margin-bottom: 21px;
  }

  &__title {
    margin: 0;
    padding: 0 18px;
    font-size: 26px;
    letter-spacing: 0.32px;
    font-weight: normal;
    color: $color__text;

    @include media-breakpoint-down(dtx) {
      font-size: 24px;
    }
    @include media-breakpoint-down(tb) {
      font-size: 22px;
    }
    @include media-breakpoint-down(phx) {
      font-size: 20px;
      padding: 0;
      text-align: center;
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -17px;

    @include media-breakpoint-down(tbx) {
      margin: 0;

      .section-nav {
        padding-left: 10px;
      }
    }

    &_revers-mob {
      @include media-breakpoint-down(phx) {
        flex-direction: column-reverse;
        margin-top: -41px;
      }
    }

    .clients-section_nowrap & {
      flex-wrap: nowrap;
      justify-content: flex-start;
    }
  }

  &__video {
    width: 100%;
    margin-bottom: 67px;
    padding: 0 17px;

    @include media-breakpoint-down(tbx) {
      width: 50%;
    }
    @include media-breakpoint-down(tbx) {
      margin-bottom: 66px;
      padding: 0;
    }
    @include media-breakpoint-down(phx) {
      width: 100%;
      margin-bottom: 36px;
      padding-top: 35px;
    }

    .section-nav {
      justify-content: flex-end;

      @include media-breakpoint-down(tbx) {
        justify-content: flex-start;
      }
      @include media-breakpoint-down(phx) {
        justify-content: center;
        margin-bottom: 19px;
      }
    }

    .dot-link-one-line__name {
      @include media-breakpoint-down(phx) {
        font-size: 20px;
        text-transform: none;
      }
    }

    .dot-link-one-line__arrow {
      @include media-breakpoint-down(phx) {
        top: 9px;
      }
    }
  }

  &__reviews {
    width: 36%;
    padding: 0 17px;

    @include media-breakpoint-down(dt) {
      width: 35%;
    }
    @include media-breakpoint-down(tbx) {
      width: 50%;
      margin-bottom: 66px;
      padding: 0;
    }
    @include media-breakpoint-down(phx) {
      display: none;
    }

    &_sm {
      width: 26.6%;
      flex-shrink: 0;
    }

    .clients-section__title {
      @include media-breakpoint-down(tbx) {
        display: none;
      }
    }
  }

  &__item {
    display: flex;
    width: 64%;
    padding: 0 17px;

    @include media-breakpoint-down(dt) {
      width: 65%;
    }
    @include media-breakpoint-down(tbx) {
      width: 100%;
      padding: 0;
    }

    .clients-section_nowrap & {
      width: 73.4%;

      @include media-breakpoint-down(dt) {
        width: 100%;
      }
      @include media-breakpoint-down(phx) {
        display: flex;
        flex-direction: column-reverse;
      }
    }
  }

  &__faq {
    //width: 37.3%;
    width: 58.6%;

    @include media-breakpoint-down(dt) {
      width: 100%;
    }
    @include media-breakpoint-down(phx) {
      width: 100%;
    }

    .faq {
      height: 440px;

      @include media-breakpoint-down(tbx) {
        height: 380px;
      }
      @include media-breakpoint-down(phx) {
        height: auto;
      }
    }

    .section-nav {
      @include media-breakpoint-down(phx) {
        justify-content: center;
      }
    }
  }

  &__form {
    //width: 26.7%;
    width: 41.4%;
    padding-left: 34px;

    @include media-breakpoint-down(dt) {
      flex-shrink: 0;
      width: 370px;
    }
    @include media-breakpoint-down(phx) {
      display: none;
    }
  }

  &__contacts {
    width: 26.2%;
    flex-shrink: 0;
    padding-left: 34px;
    padding-top: 51px;

    @include media-breakpoint-down(tbx) {
      width: 34%;
      padding-left: 48px;
      padding-top: 44px;
    }
    @include media-breakpoint-down(tb) {
      width: 35%;
      padding-top: 46px;
      padding-left: 34px;
    }
    @include media-breakpoint-down(phx) {
      width: 100%;
      padding-left: 0;
      padding-top: 0;
      margin-bottom: 48px;
    }
  }

  &__views-products {
    width: 73.8%;
    padding: 0 5px 0 4px;

    @include media-breakpoint-down(tbx){
      width: 66%;
    }
    
    @include media-breakpoint-down(tb) {
      width: 64%;
      padding: 0;
    }
    @include media-breakpoint-down(phx) {
      width: 100%;
      margin-bottom: 24px;
    }

    .clients-section__title {
      @include media-breakpoint-down(phx) {
        width: 100%;
      }
    }
  }

  &__show-small {
    display: none;
    @include small {
      display: block;
    }
  }

}