.satisfaction-index {
  @include clearfix();
  
  &__item {
    float: left;
    display: flex;
    align-items: center;
    margin: 0 96px 0 0;

    @include media-breakpoint-down(dt){
      margin: 0 25px 0 0;
    }

    @include media-breakpoint-down(lt){
      float: none;      
      margin: 0 0 20px;
    }
    
    &:last-child {
      margin: 0;
    }
    
    &-img {
      float: left;
      width: 98px;
      flex-shrink: 0;
      flex-grow: 0;
      
      @include media-breakpoint-down(lt){
        width: 48px;
      }
      
      img {
        @extend .img-responsive;
      }
    }
    
    &-info {
      padding-left: 32px;

      @include media-breakpoint-down(dt){
        padding-left: 15px;
      }
    }
    
    &-percent {
      line-height: 1;
      font-size: 36px;
      letter-spacing: 0.32px;

      @include media-breakpoint-down(dt){
        font-size: 22px;
      }
    }
    
    &-text {
      font-size: 20px;
      letter-spacing: 0.36px;

      @include media-breakpoint-down(dt){
        font-size: 16px;
      }

      @include media-breakpoint-down(lt){
        white-space: nowrap;
      }
    }
  }
}