body.tabs-container-open {
  @include media-breakpoint-down(tb) {
    overflow: hidden;
  }
}

.tabs {
  display: flex;      
  flex-wrap: wrap;

  @include media-breakpoint-down($mobile-switch){
    display: block;
  }

  a {
    flex-grow: 1;
    display: block;
    background: $color__secondary;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 0.36px;
    transition: 0.2s;
    padding: 16px 20px 12px;
    text-align: center;
    margin: 8px 1px 0 0;
    
    @include media-breakpoint-down(dtx){
      font-size: 14px;
    }
   
    @include media-breakpoint-down($mobile-switch){
      display: block;
      margin: 0 0 1px 0;
      padding: 10px 16px;
      text-transform: none;
      text-align: left;
      font-size: 16px;
      
      &:last-child {
        margin: 0;
      }
    }
    
    > span {
      display: inline-block;
      position: relative;
      top: -10px;
      padding-left: 3px;
      transition: 0.2s;
      font-size: 12px;
      letter-spacing: 0.36px;
      color: $color__additional;
      white-space: nowrap;
      overflow: hidden;

      @include media-breakpoint-down($mobile-switch){
        top: -8px;
      }

      &.anim-destination {

        > span {
          position: relative;
          display: inline-block;
          transition: transform 0.3s;

          &:before {
            position: absolute;
            white-space: nowrap;
            top: 100%;
            content: attr(data-hover);
          }
        }

      }
    }
    
    &.open,
    &.active,
    &:hover {
      > span {
        &.anim-destination {
          span {
            transform: translateY(-100%);
          }
        }
      }
    }
    
    &.open,
    &:hover {
      @include media-breakpoint-down($mobile-switch){
        background: $color__primary;
        color: #fff;

        > span {
          color: #fff;
        }
      }
    }
    
    &.active,
    &:hover {
      @include media-breakpoint-up($mobile-switch){
        background: $color__primary;
        color: #fff;
        
        > span {
          color: #fff;
        }
      }
    }
  }
}

.tabs-container-head {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  left: 24px;
  height: 66px;
  background: #fff;
  z-index: 10;
  align-items: center;
  padding: 0 16px;

  @include media-breakpoint-down($mobile-switch){
    display: flex;
  }
  
  &__text {
    cursor: pointer;
    font-size: 20px;
    letter-spacing: 0.36px;
    color: $color__additional;
    
    &:before {
      content: '';
      display: inline-block;
      margin-right: 10px;
      width: 17px;
      height: 12px;
      transform: rotate(180deg);
      background: url(../img/svg/arrow.svg) no-repeat;
      background-size: contain;
    }
  }
  
  &__close {
    cursor: pointer;
  }
}

.tabs-container {

  @include media-breakpoint-down($mobile-switch){
    visibility: hidden;
    transform: translateX(100%);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: $header-z-index + 1;
    padding-left: 24px;
    transition: 0.5s;
    
    &_open {
      visibility: visible;
      transform: translateX(0);
    }
  }
  
  &__inner {
    @include media-breakpoint-down($mobile-switch) {
      height: 100%;
      overflow: auto;
      padding: 66px 0 40px;
      background: #fff;
      box-shadow: 0 0 12px rgba(255, 102, 0, 0.2);
    }
  }
  
}
.tab-container {
  display: none;

  @include media-breakpoint-down($mobile-switch){
    background: #fff;    
    padding: 0 16px;
  }
  
  &.active {
    display: block;
  }
}