.address {
  display: flex;
  align-items: flex-start;
  font-size: 18px;
  letter-spacing: 0.36px;
  color: $color__additional;

  @include media-breakpoint-down(tbx) { // change
    font-size: 16px;
  }
  @include media-breakpoint-down(phx) {
    font-size: 14px;
    clear: both;

    > svg, > img {
      display: none;
    }
  }

  svg {
    flex-shrink: 0;
    width: 17px;
    height: 21px;
    margin-right: 10px;
    stroke: $color__primary;
  }

  &__point {
    //display: inline-block;
    //margin-right: 16px;

    &::after {
      content: '  ';
    }
  }

  &__country {
    display: inline-block;
    white-space: nowrap;
    margin-left: 10px;

    img {
      width: 22px;
      margin-right: 7px;
      margin-top: -3px;
    }
  }
}