.footer {
  background: $color__secondary;
  padding: 12px 0 57px;

  @include media-breakpoint-down(dtx){
    padding: 0;
  }
  
  &__top {
    padding: 18px 0;
    @include media-breakpoint-down(lt){
      padding: 10px 0 16px;
      
      a {
        font-size: 14px;
        padding: 0 10px;
      }

      .dot-link-one-line__arrow {
        top: 3px;
      }
    }
    
    @include media-breakpoint-down($mobile-switch){
      display: none;
    }
  }
  
  &__bottom {
    
    &-row {
      display: flex;

      @include media-breakpoint-down($mobile-switch){
        display: block;
        margin: 0 (-$container-padding-tbx);
      }
      
      @include media-breakpoint-down(phx){
        margin: 0 (-$container-padding-phx);
      }
      
      &-mobile {
        display: none;
        
        @include media-breakpoint-down(tbx){
          display: flex;
        }
        
        @include media-breakpoint-down($mobile-switch){
          display: none;
        }
        
        .footer__contacts-left,
        .footer__contacts-right {
          width: 50%;
        }
      }
    }
    
    &-left,
    &-right {
      width: 50%;

      @include media-breakpoint-down($mobile-switch) {
        width: auto;
      }
    }
    
    &-left {
      @include media-breakpoint-down($mobile-switch){
        display: none;
      }
    }
    
    &-right {
      display: flex;

      @include media-breakpoint-down($mobile-switch){
        display: block;
        padding-bottom: 40px;
        background: #fff;
      }
    }
    
    &-slider {
      width: 75%;
      background: #fff;

      @include media-breakpoint-down(dtx){
        width: 100%;
      }
      
      @include media-breakpoint-down(tbx){
        top: -100px;
        margin-bottom: -100px; 
      }
      
      @include media-breakpoint-down($mobile-switch){
        top: 0;
        margin-bottom: 0;
        width: auto;
      }
      
      .swiper-button-prev-kronos {
        @include media-breakpoint-down($mobile-switch){
          left: 32px;
        }
      }
      
      .swiper-button-next-kronos {
        @include media-breakpoint-down($mobile-switch){
          right: 32px;
        }
      }
      
      .swiper-button-prev-kronos,
      .swiper-button-next-kronos {

        @include media-breakpoint-down(tbx){
          background: #fff;
        }
        
      }
      .swiper-pagination-kronos {
        display: none;
        margin-top: 24px;
        
        @include media-breakpoint-down($mobile-switch){
          display: block;
        }
      }
      
      .lupa {
        position: absolute;
        bottom: 16px;
        right: 21px;
        width: 24px;
        height: 24px;

        @include media-breakpoint-down($mobile-switch){
          display: none;
        }
      }
      
      &-info {
        position: relative;
      }
      
      &-shop {
        letter-spacing: 0.36px;
        margin-bottom: 5px;

        @include media-breakpoint-down(tbx){
          font-size: 14px;
          letter-spacing: 0.4px;
          margin-bottom: 8px;
        }
        
        @include media-breakpoint-down($mobile-switch){
          margin-bottom: 5px;
        }
      }
      
      &-name {
        font-size: 32px;
        letter-spacing: 0.32px;
        margin-bottom: 5px;

        @include media-breakpoint-down(tbx){
          font-size: 22px;
          letter-spacing: 0.36px;
          margin-bottom: 8px;
        }

        @include media-breakpoint-down($mobile-switch){
          font-size: 18px;
        }
      }
      
      &-pagination {
        font-size: 14px;
        letter-spacing: 0.4px;
        color: $color__primary;

        @include media-breakpoint-down($mobile-switch){
          display: none;
        }
      }

      .swiper-slide a {
        position: relative;
        display: flex;
        align-items: flex-end;
        color: #fff;
        padding: 28px 22px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;

        @include media-breakpoint-down(tbx){
          padding: 28px 25px;
        }
        
        @include media-breakpoint-down($mobile-switch){
          padding: 22px 16px;
        }
        
        &:before {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: rgba(0,0,0,0.2);
          content: "";
        }
      }
      
      .swiper-wrapper,
      .swiper-slide,
      .swiper-slide a {
        height: 100%;

        @include media-breakpoint-down($mobile-switch){
          height: 300px;
        }
      }
    }
    
    &-links {
      display: flex;
      align-items: center;
      width: 25%;
      padding-left: 40px;
      
      @include media-breakpoint-down(dtx){
        display: none;
      }
    }
    
  }
  
  &__triggers {
    background: #fff;
  }
  
  &__contacts {
    display: flex;
    
    &-desktop {
      @include media-breakpoint-down(tbx){
        display: none;
      }
    }
    
    &-left,
    &-right {
      width: 50%;

      @include media-breakpoint-down(tbx){
        width: 100%;
      }
    }
    
    &-left {
      padding: 24px 0;
    }
    &-right {
      background: $color__primary;
      color: #fff;
      letter-spacing: 0.36px;


    }
    
    &-gallery {
      
      .swiper-button-prev-kronos,
      .swiper-button-next-kronos {
        border-color: #fff;
        background: $color__primary;
        bottom: auto;
        top: 28px;


        &:after {
          border-color: #fff;
        }
      }
      
      .swiper-button-prev-kronos {
        left: auto;
        right: 65px;
      }
      
      .swiper-button-next-kronos {
        right: 28px;
      }
      
    }
    &-item {
      padding: 20px 28px;
    }
    
    &-mobile {
      background: $color__primary;
      color: #fff;
      padding: 24px 0 20px;
    }
    
    &-name {
      font-size: 32px;
      letter-spacing: 0.32px;
      margin-bottom: 16px;
      
      @include media-breakpoint-down($mobile-switch){
        font-size: 22px;
        letter-spacing: 0.36px;
      }
    }
    
    &-city {
      margin-bottom: 16px;

      @include media-breakpoint-down(dtx){
        font-size: 16px;
      }

      @include media-breakpoint-down($mobile-switch){
        font-size: 18px;
        letter-spacing: 0.36px;
      }
    }
    
    &-address {
      margin-bottom: 12px;

      @include media-breakpoint-down(dtx){
        font-size: 16px;
      }

      @include media-breakpoint-down($mobile-switch){
        letter-spacing: 0.4px;
        margin-bottom: 16px;
      }
    }
    
    &-map {
      .dot-link-one-line {
        display: inline-block;
        color: #fff;

        @include media-breakpoint-down(dtx){
          font-size: 16px;
        }

        @include media-breakpoint-down($mobile-switch){
          font-size: 18px;
          letter-spacing: 0.36px;
        }
        
        &__name {
          border-color: #fff;

          &:hover {
            border-color: #fff;
          }

          &::after {
            background-image: repeating-linear-gradient(to right, #fff 0, #fff 1px, transparent 1px, transparent 2px);
          }
        }
      }
    }
  }
}

.footer-links {
  ul {
    @extend %list;
    
    li {
      margin-bottom: 12px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.footer-logo {
  a {
    position: relative;
    display: inline-block;
    padding-right: 128px;

    @include media-breakpoint-down(dt){
      padding-right: 0;
    }
    @include media-breakpoint-down(tbx){
      padding-right: 128px;
    }
    
    img {
      @extend .img-responsive;
    }

    &:hover {
      &:before {
        animation: logo-animation-hover 1s ease-in-out forwards;
      }

      .footer-logo__text {
        svg {
          rect {
            stroke-dasharray: 320;
          }
        }
      }
    }

    &:before {
      position: absolute;
      top: 0;
      width: 40px;
      height: 100%;
      content: '';
      background: #eee;
      opacity: 0.5;
      transition: none;
      transform: skewX(-20deg);
      animation: logo-animation-default 9.7s ease-in-out infinite;
      z-index: 3;

      .header_line & {
        background: #fff;
      }
    }
  }
  
  &__text {
    position: absolute;
    top: 11px;
    right: 0;
    white-space: nowrap;
    color: $color__primary;
    text-transform: uppercase;
    font-size: 10px;
    letter-spacing: 0.24px;
    font-weight: 500;
    line-height: 12px;
    overflow: hidden;

    @include media-breakpoint-down(dt){
      display: none;
    }

    @include media-breakpoint-down(tbx){
      display: block;
    }

    svg {
      position: absolute;
      top: -3px;
      left: 9px;
      width: calc(100% - 6px);
      height: calc(100% + 6px);
      fill: none;

      rect {
        stroke-dasharray: 0 320;
        stroke-width: 2px;
        stroke: $color__primary;
        transition: stroke-width 1s, stroke-dasharray 1s, stroke-dashoffset 1s;
      }
    }
  }

  &_animation {
    svg {
      rect {
        animation-name: logo-animation-footer;
        animation-duration: 2s;
        animation-iteration-count: 1;
      }
    }
  }
}

.footer-phone {
  padding: 16px 10px;

  @include media-breakpoint-down(dtx){
    padding: 12px 10px;
  }
  
  &__time {
    letter-spacing: 0.36px;
    padding: 10px 0 0 35px;
    
    @include media-breakpoint-down(dtx){
      font-size: 16px;
    }
  }
  
  a {
    position: relative;
    display: inline-block;
    
    &:hover {
      svg {
        stroke: $color__primary;
      }
    }
  }
  
  .phone-link {
    padding-left: 35px;
  }
  
  .phone-link__text {
    padding-left: 10px;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(dtx){
      font-size: 16px;
    }
  }
  
  svg {
    position: absolute;
    top: 1px;
    left: 0;
    stroke: $color__additional;
    fill: none;
    transition: 0.2s;
  }
}

.footer-contacts-list {
  padding: 4px 0 0 10px;

  @include media-breakpoint-between(lt, dtx){
    padding: 0 10px 0;
  }
  
  ul {
    @include media-breakpoint-between(lt, dtx){
      display: block;
    }
    
    li {
      padding-right: 15px;

      @include media-breakpoint-between(lt, dtx){
        padding-right: 0;
      }
      
      &:before {
        @include media-breakpoint-between(lt, dtx){
          content: none;
        }
      }
      
      &:last-child {
        padding-right: 0;
      }
      
      + li {
        padding-left: 15px;

        @include media-breakpoint-between(lt, dtx){
          margin-top: 12px;
          padding-left: 0;
        }
      }
      a {
        position: relative;
        
        @include media-breakpoint-down(dtx){
          font-size: 16px;
        }
        
        &.footer-free-call {
          padding-left: 35px;
        }
        
        &.footer-mail {
          padding-left: 32px;

          @include media-breakpoint-between(lt, dtx){
            padding-left: 35px;
          }
        }
        
        svg {
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
          stroke: $color__additional;
          fill: none;
        }
        &:hover {
          svg {
            stroke: $color__primary;
          }
        }
      }
    }
  }
}

.footer__tablet {
  display: none;
  background: #fff;
  padding: 24px 0;
  
  @include media-breakpoint-down(dtx){
    display: block;
  }
  
  @include media-breakpoint-down($mobile-switch){
    display: none;
  }
  
  &-row {
    display: flex; 
    justify-content: space-between;
  }
  
  &-col {
    
  }
  
  .border-dot-list {
    a {
      font-size: 16px;
    }
  }
}

.footer__mobile {
  display: none;
  
  @include media-breakpoint-down($mobile-switch){
    display: block;
  }
  
  &-top {
    background: $color__secondary;
    padding: 26px 0;
    text-align: center;
  }
  
  &-bottom {
    background: #fff;
    padding: 20px 0;
  }
  
  &-logo {
    width: 200px;
    margin: 0 auto 15px;

    a {
      padding-right: 0;

      @include media-breakpoint-down(phx){
        overflow: hidden;
      }
    }
  }
  
  &-phone {
    margin-bottom: 16px;
    a {
      font-size: 20px;
      letter-spacing: 0.36px;
    }
  }
  
  &-time {
    font-size: 16px;
    letter-spacing: 0.4px;
    margin-bottom: 25px;
  }
  
  &-links {
    ul {
      @extend %list;
      
      li {
        margin-bottom: 20px;
        
        &:last-child {
          margin-bottom: 0;
        }
        
        a {
          font-size: 18px;
          letter-spacing: 0.36px;
          display: inline-block;
          
          svg {
            margin: 0 auto;
            display: block;
            stroke: $color__additional;
            fill: none;
            margin-bottom: 10px;
            
            &.footer__mobile-link-phone {
              width: 28px;
              height: 28px;
            }
            
            &.footer__mobile-link-mail {
              width: 29px;
              height: 24px;
            }
            
            &.footer__mobile-link-paint {
              @extend .svg-icon-paint-dims;
            }
          }
        }
      }
    }
  }
}