/* попап (количество купивших) */

.popup.popup-number-buyers {
  max-width: 950px;
  margin: 0 auto;
  padding: 36px 160px 24px;

  @include media-breakpoint-down(tbx) {
    max-width: 920px;
  }
  @include media-breakpoint-down(tb) {
    padding: 42px 20% 35px;
  }
  @include media-breakpoint-down(phx) {
    padding: 45px 16px 30px;
  }
  
  .popup__name {
    margin-bottom: 33px;

    @include media-breakpoint-down(tb) {
      font-size: 18px;
    }
    @include media-breakpoint-down(phx) {
      margin-bottom: 13px;
      font-size: 20px;
    }
  }

  .popup__text {
    margin-top: 0;
    margin-bottom: 45px;
    text-align: center;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(tb) {
      font-size: 16px;
      margin-bottom: 33px;
    }
    @include media-breakpoint-down(phx) {
      font-size: 18px;
      margin-bottom: 24px;
    }

    &_bottom {
      font-size: 18px;
      margin-top: 38px;
      margin-bottom: 0;

      @include media-breakpoint-down(tb) {
        margin-top: 28px;
      }
      @include media-breakpoint-down(phx) {
        font-size: 14px;
        margin-top: 18px;
      }
    }
  }
  
  .input-phone_tender {
    min-width: 0;
  }

  .btn {
    padding: 8px 21px 7px;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(phx) {
      font-size: 18px;
      padding: 8px 15px 7px;
    }
  }
}