.contacts {
  background: $color__secondary;
  padding: 20px 25px 32px;
  height: 325px;
  text-align: center;

  display: flex;
  align-items: center;
  justify-content: center;

  @include media-breakpoint-down(tbx){
    height: 194px;
  }
  @include media-breakpoint-down(tb){
    height: 222px;
    padding: 18px 15px 25px;
  }
  @include media-breakpoint-down(phx){
    height: auto;
    margin: 0 -16px;
    padding: 23px 15px 25px;
  }

  &__wrap {

  }
  
  &__text,
  &__title {
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(dtx){
      font-size: 18px;
      font-weight: normal;
    }
    @include media-breakpoint-down(tbx){
      font-size: 16px;
    }
    @include media-breakpoint-down(phx){
      font-size: 20px;
      font-weight: 500;
    }
    
    + .contacts__text {
      margin-top: 16px;

      @include media-breakpoint-down(phx) {
        margin-top: 14px;
      }
    }
  }

  &__text {
    @include media-breakpoint-down(tb){
      font-size: 14px;
    }
    @include media-breakpoint-down(phx){
      font-size: 20px;
    }
  }
  
  &__name {
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 0.36px;
  }
  
  &__links {
    margin-top: 26px;

    @include media-breakpoint-down(tbx) {
      margin-top: 10px;
    }
    @include media-breakpoint-down(phx) {
      margin-top: 25px;
    }

    ul {
      @extend %list;
      li {
        margin-bottom: 14px;

        @include media-breakpoint-down(tbx) {
          margin-bottom: 10px;
        }
        @include media-breakpoint-down(phx) {
          margin-bottom: 14px;
        }
        
        &:last-child {
          margin-bottom: 0;
        }
        
        a {
          position: relative;
          font-size: 20px;
          letter-spacing: 0.36px;
          padding: 0 0 0 32px;

          @include media-breakpoint-down(dtx){
            font-size: 18px;
          }
          @include media-breakpoint-down(tbx){
            font-size: 16px;
          }
          @include media-breakpoint-down(phx){
            font-size: 20px;
          }
          
          &:not(.dot-link-one-line) {
            padding-top: 2px;

            @include media-breakpoint-down(dtx){
              padding-top: 3px;
            }
          }
          
          img, svg {
            position: absolute;
            top: 6px;
            left: 0;
          }
          
          svg {
            stroke: $color__primary;
            fill: none;
          }
        }
      }
    }

    &-subtext {
      margin-top: 12px;
      font-size: 16px;
      letter-spacing: 0.4px;
    }
  }
  
  &__hide-mob {
    @include media-breakpoint-down(tb){
      display: none;
    }
  }
  
  &__show-mob {
    display: none;
    @include media-breakpoint-down(tb){
      display: block;
    }
  }
  
  &_ru {
    padding: 36px 30px 18px;
    
    @include media-breakpoint-down(tb){
      padding: 24px 30px;
      margin-bottom: 35px;
    }
    
    .contacts__text {
      text-align: center;
      
      @include media-breakpoint-down(tb){
        font-size: 20px;
        font-weight: 500;
      }
    }
    .contacts__links {
      margin-top: 27px;
      text-align: center;
      
      ul {
        li {
          a {
            @include media-breakpoint-down(tb){
              font-size: 20px;
            }
          }
        }
      }
    }
    .contacts__item-mail {
      margin-top: 0 !important;
      img {
        top: 7px;
        left: 0;
      }
    }
    .contacts__item-skype {
      margin-bottom: 13px;
      img {
        top: 5px;
        left: 0;
      }
    }
    .contacts__item-phone {
      margin-bottom: 14px;
      img {
        top: 5px;
        left: 3px;
      }
      
      > div {
        font-size: 16px;
        letter-spacing: 0.4px;
        margin-top: 12px;
      }
    }
  }
}