.popup-free-call {
    position: relative;
    padding: 46px 90px 30px;
    //overflow: hidden;
    width: 560px;
    margin: 0 auto;
  
    @include media-breakpoint-down(tbx){
      width: 548px;
      padding: 50px 50px 30px;
    }
    @include media-breakpoint-down(tb){
      width: auto;
      padding: 41px 16px 9px;
    }
  
    &__name {
        font-size: 20px;
        margin-bottom: 35px;
        @include media-breakpoint-down(tbx){
            font-size: 18px;
        }

        @include media-breakpoint-down(tb){
          margin-bottom: 18px;
        }
    }
    &__input-name {
        margin-bottom: 16px;
    }
    &__phone {
        margin: 0 28px 0 12px;
        @include media-breakpoint-down(tbx){
          max-width: 340px;
          margin: 0 auto;
        }

        @include media-breakpoint-down(tb){
          max-width: none;
          min-width: 0;
        }
    }
    &__description {
        font-size: 16px;
        padding-top: 14px;
        margin-bottom: 22px;
        color: $color__additional;
        text-align: center;
        letter-spacing: 0.4px;

        @include media-breakpoint-down(tbx){
            padding-top: 19px;
            margin-bottom: 32px;
        }
        @include media-breakpoint-down(tb){
            color: black;
            padding-top: 27px;
            padding-left: 6px;
            margin-bottom: 15px;
            font-size: 15px;
            & > *:not(:last-child) {
                margin-bottom: 8px;
            }
            & > *:last-child {
              margin-top: 17px;
            }
        }
    }
    &__send {
      @include media-breakpoint-down(tb){
        margin-top: 15px;        
      }
    }
    &__send-button {
        padding: 7px 22px 8px;
        letter-spacing: 0.36px;
        margin: 0 auto;
        @include media-breakpoint-down(tb){
            padding: 7px 16px 8px;
            border: 1px solid #999999;
            background-color: white;
            color: black;
        }
    }
    &__line {
        position: relative;
        display: inline-block;
    }
    &__orange {
        color: $color__primary;
        padding-right: 9px;
    }
    &__phone-img {
      position: absolute;
      top: 2px;
      left: -27px;
      margin-right: 15px;
    }
    &__phone-text {
        font-size: 18px;
        letter-spacing: 0.36px;
    }
    &__show-small {
        display: none;
        @include media-breakpoint-down(tb){
            display: block;
        }
    }
    &__hide-small {
        @include media-breakpoint-down(tb){
            display: none;
        }
    }
  
    .mfp-close {
      width: 54px;
      height: 54px;
    }
}