.buy-without-presents {
    position: relative;
    padding: 35px;
    //overflow: hidden;
    width: 951px;
    margin: 0 auto;
  
    @include media-breakpoint-down(tbx){
      width: 895px;
      padding: 45px 40px;
    }
  
    @include media-breakpoint-down(tb){
      padding: 40px 16px 20px;
      width: auto;
    }
  
    &__name {
        font-size: 22px;
        margin-bottom: 56px;

        @include media-breakpoint-down(tbx){
            margin-bottom: 42px;
        }
        @include media-breakpoint-down(tb){
          
            margin-bottom: 15px;
            font-size: 18px;
            
        }
    }
    &__img {
        width: 206px;
        margin: 0 auto;
        margin-bottom: 50px;

        @include media-breakpoint-down(tbx){
          width: 312px;
          margin-bottom: 62px;
        }
        @include media-breakpoint-down(tb){
          margin-bottom: 24px;
          width: auto;
          max-width: 142px;
          height: 80px;
        }
      
        img {
          @extend .img-responsive;
          margin: 0 auto;
        }
    }
    &__form {
        margin-bottom: 20px;


        @include media-breakpoint-down(tbx){
            margin-bottom: 28px;
        }
      
        @include media-breakpoint-down(tb){
            margin-bottom: 17px;
        }
    }
    &__flex {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        & > *:not(:last-child) {
            flex: 1 0 0;
            margin-right: 43px;
          
            @include media-breakpoint-down(tbx){
              margin-right: 19px;
            }
            @include media-breakpoint-down(tb){
                flex: none;
                width: 100%;
                margin: 0 0 16px 0;
            }
        }
        .btn {
          padding: 7px 20px;
          @include media-breakpoint-down(tb){
            font-size: 18px;
            letter-spacing: 0.36px;
            padding: 6px 24px 9px;
          }
        }
      
        @include media-breakpoint-down(tb){
            flex-direction: column;
        }
    }
    &__phone {
        min-width: 0 !important;
    }
    &__description {
        font-size: 18px;
        text-align: center;
        letter-spacing: 0.36px;
        color: $color__additional;

        @include media-breakpoint-down(tb){
            font-size: 14px;
            letter-spacing: 0.4px;
        }
    }
}