@mixin isTouch(){
  @media (pointer: coarse) {
    @content;
  }
  //html.touchevents & {
  //  @content;
  //}
}

@mixin clearfix {
    &:after {
        content: "";
        display: table;
        clear: both;
    }
}

@mixin medium() {
    @media (max-width: 1919px) {
        @content;
    }
}

@mixin tab() {
    @media (max-width: 1279px) {
        @content;
    }
}

@mixin small() {
    @media (max-width: 959px) {
        @content;
    }
}

@mixin only-medium() {
    @media (min-width: 960px) AND (max-width: 1919px) {
        @content;
    }
}

@mixin only-small() {
    @media (max-width: 959px) {
        @content;
    }
}