.btn-wave-container {
  position: relative;
  overflow: hidden;
}

.btn-wave {
  position: relative;
  display: inline-block;

  .link-text,
  .link-hover {
    overflow: hidden;
    display: inline-block;

    > span {
      position: relative;
      display: inline-block;
      transition: transform 0.3s ease-in-out;

      @for $i from 1 through 40 {
        &:nth-child(#{$i}) {
          transition-delay: 20ms * $i;
        }
      }
    }
  }

  .link-text {
    white-space: nowrap;
    overflow: hidden;
  }

  .link-hover {
    position: absolute;
    top: 0;
    left: 0;

    > span {
      transform: translateY(100%);
    }
  }

  &.btn-wave-active,
  &:hover,
  &:focus {
    outline: none;

    .link-text {
      > span {
        transform: translateY(-100%);
      }
    }

    .link-hover {
      > span {
        transform: translateY(0);
      }
    }
  }
}


.menu {
  margin: 2px 0 0 34px;

  @include media-breakpoint-down(dt) {
    margin: 2px 0 0 20px;
  }

  @include media-breakpoint-down(lt) {
    position: relative;
  }

  .btn-wave {
    display: block;
  }
  
  .link-hover {
    color: $color__primary;
  }
  
  > ul {
    @extend %list;
    display: flex;
    
    > li {
      position: relative;

      > a {
        position: relative;
        font-size: 18px;
        color: #000;
        text-decoration: none;
        text-transform: uppercase;
        line-height: 1;
        letter-spacing: 0.36px;
        padding: 14px 10px 8px;
        display: block;
        overflow: hidden;
        
        @include media-breakpoint-down(dtx){
          font-size: 16px;
          padding: 14px 8px 10px;
        }

        @include media-breakpoint-down(dt) {
          font-size: 13px;
          padding: 14px 10px 8px;
        }

        @include media-breakpoint-down(lt){
          font-size: 16px;
        }

        @include media-breakpoint-down(tbx){
          font-size: 14px;
          letter-spacing: 0.4px;
        }
      }

      &:after {
        position: absolute;
        background: #fff;
        bottom: -8px;
        width: 100%;
        left: 0;
        height: 8px;
        z-index: 6;
        transform: translateY(-8px);
        transition: 0.2s;
        visibility: hidden;
        opacity: 0;
      }
      
      &:last-child {
        @include media-breakpoint-down(lt){
          > a {
            padding-right: 0;
          }
        }
      }
      
      &.menu-drop-item {
        position: relative;
        
        &:after {
          content: "";
        }

        @include media-breakpoint-down(lt) {
          position: static;
        }
        
        > a {
          position: relative;
          padding-right: 24px;
          z-index: 6;
          
          &:after {
            @extend %dropdown-arrow;
            position: absolute;
            right: 6px;
            margin-top: -1px;
            top: 50%;
            transition: 0.2s;
            content: "";
            
            @include media-breakpoint-down(dt){
              margin-top: -2px;
            }
            @include media-breakpoint-down(tbx){
              right: 5px;
              margin-top: -2px;
            }
          }
        }
      }
      
      &.open,
      &:hover {
        .menu__drop {
          opacity: 1;
          visibility: visible;
          transform: translateY(0);
          
          > ul {
            > li {
              opacity: 1;
              transform: translateX(0);
            }
          }
        }

        &.menu-drop-item {
          &:after {
            transform: translateY(0);
            opacity: 1;
            visibility: visible;
          }
        }
      }

      &.active,
      &:hover {
        > a {
          &:after {
            transform: rotateZ(180deg);
          }
        }

        &.menu-drop-item {
          > a {
            background: #fff;
            box-shadow: 0 0 12px rgba(255, 102, 0, 0.2);
          }
        }
      }
    }
  }
  
  &__drop {
    position: absolute;
    top: 100%;
    left: 0;
    width: 867px;
    padding: 20px;
    background: #fff;
    visibility: hidden;
    opacity: 0;
    transform: translateY(50px);
    transition: 0.2s;
    box-shadow: 0 0 12px rgba(255, 102, 0, 0.2);
    z-index: 5;
    overflow: hidden;
    
    .btn-wave {
      display: flex;
    }

    @include media-breakpoint-down(lt){
      right: 0;
      left: auto;
    }
    
    &-about {
      left: auto;
      right: -246px;

      @include media-breakpoint-down(lt){
        right: 0;
      }
    }
    
    &-catalog {}
    
    &-bg {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-position: right bottom;
    }
    
    &-icon {
      width: 40px;
      margin-right: 12px;
      img {
        display: block;
      }
      //position: absolute;
      //left: 0;
      //top: 50%;
      //transform: translateY(-50%);
    }
    
    > ul {
      @extend %list;
      
      > li {
        margin-bottom: 8px;
        opacity: 0;
        transform: translateX(100px);
        transition: 0.5s 0.2s;
        
        @for $i from 1 through 15 {
          &:nth-child(#{$i}) {
            transition-delay: #{$i * 0.05}s;
          }
        }
        
        &:last-child {
          margin-bottom: 0;
        }
        
        > a {
          display: inline-flex;
          align-items: center;
          
          position: relative;
          transition: 0.2s;
          
          &:hover {
            color: $color__primary
          }
        }
      }
    }
  }
}