.pagination-line {
  width: 100%;
  display: flex;
  justify-content: center;
  
  > a {
    display: block;
    height: 6px;
    background: $color__secondary;
    flex: 1 1 auto;
    margin: 0 2px;
    transition: 0.2s;
    
    &:first-child {
      margin-left: 0;
    }
    
    &:last-child {
      margin-right: 0;
    }
    
    &.active,
    &:hover {
      background: $color__primary;
    }
  }
}

.pagination {
  display: flex;
  align-items: baseline;
  overflow: hidden;
  padding: 10px 0;
  
  @include media-breakpoint-down(tb){
    display: block;
    padding: 14px 16px 16px;
  }
  
  &__page {
    flex: 0 0 auto;
    font-size: 20px;
    letter-spacing: 0.36px;
    margin-right: 16px;
    
    @include media-breakpoint-down(dt){
      font-size: 16px;
      margin-right: 8px;
    }

    @include media-breakpoint-down(tb){
      margin: 0 0 18px;
      text-align: center;
    }
  }
  
  &__list {
    flex: 1 1 auto;

    @include media-breakpoint-down(tb){
      text-align: center;
    }
    
    ul {
      @extend %list;
      display: inline-flex;
      align-items: baseline;
      
      li {
        margin: 0 6px;
        
        @include media-breakpoint-down(dt){
          margin: 0 10px;
        }

        @include media-breakpoint-down(phx){
          margin: 0 5px;
        }
        
        &:first-child {
          margin-left: 0;
        }
        
        &:last-child {
          margin-right: 0;
        }
        
        &.pagination__dots {
          margin: 0 2px;
        }
        
        a, span {
          display: block;
          font-size: 20px;
          letter-spacing: 0.36px;
          text-align: center;
          white-space: nowrap;
          height: 39px;
          min-width: 39px;
          line-height: 36px;
          border-radius: 20px;
          border: 1px solid transparent;
          color: $color__additional;
          padding: 0 5px;
          transition: 0.2s;

          @include media-breakpoint-down(dt){
            height: 33px;
            min-width: 33px;
            line-height: 30px;
            font-size: 16px;
          }
        }
        
        a {

          &:hover {
            background-color: $color__primary;
            color: white;
            border-color: transparent;
          }
          
          @include isTouch {
            &:hover {
              background-color: transparent;
              color: $color__additional;
              border-color: transparent;
            }
          }

          &.active {
            background: transparent;
            border-color: $color__primary;
            color: $color__primary;
          }
        }
      }
    }
    
  }
  
}