.banner-products {
  display: flex;
  flex-wrap: wrap;
  border-right: 1px solid $color__text2;
  border-top: 1px solid $color__text2;
  margin-top: -1px;
  
  &__item {
    border-left: 1px solid $color__text2;
    border-bottom: 1px solid $color__text2;
    
    &_mini {
      
    }
    
    &_normal {}
    
    &_full {}
    
    &_empty {
      border: 0;
    }

    &_dt-hide {
      @include media-breakpoint-down(dt){
        display: none;
      }
    }
  }
  
  &_no-border {
    border-right: 0;
  }
  
  &-abs {
    position: absolute;
    bottom: 100%;
    left: 0;
    width: 100%;
    z-index: 2001;
    border: 0;
    
    .banner-products {
      border: 0;
    }


    .banner-products__item:not(.banner-products__item_empty) {
      border: 1px solid $color__text2;

      + .banner-products__item:not(.banner-products__item_empty) {
        border-left: 0;
      }
    }
    
    .banner-products__item_empty {
      + .banner-products__item:not(.banner-products__item_empty) {
        margin-left: -1px;
      }
    }
  }
  
  &_100 {

    .banner-products__item {

      &_mini {
        width: 12.5%;

        /* position start */
        .banner-product_left {
          .banner-product__open {
            left: -1px;
            //right: calc(-100% - 2px);
          }
        }
        .banner-product_right {
          .banner-product__open {
            //left: calc(-100% - 2px);
            right: -1px;
          }
        }
        /* position end */

        @include media-breakpoint-down(lt){
          width: 25%;
        }

      }

      &_normal {
        width: 25%;

        /* position start */
        .banner-product_left {
          .banner-product__open {
            left: -1px;
            //right: -1px;
          }
        }
        .banner-product_right {
          .banner-product__open {
            //left: -1px;
            right: -1px;
          }
        }
        /* position end */

        @include media-breakpoint-down(lt){
          width: 50%;
        }
      }

      &_full {
        width: 100%;

        /* position start */
        .banner-product_left {
          .banner-product__open {
            left: -1px;
            //right: -1px;
          }
        }
        .banner-product_right {
          .banner-product__open {
            //left: -1px;
            right: -1px;
          }
        }
        /* position end */
      }

    }
    
  }

  &_75 {

    .banner-products__item {

      &_mini {
        width: (100% / 3);

        /* position start */
        .banner-product_left {
          .banner-product__open {
            left: -1px;
            //right: calc(-100% - 2px);
          }
        }
        .banner-product_right {
          .banner-product__open {
            //left: calc(-100% - 2px);
            right: -1px;
          }
        }
        /* position end */

      }

      &_normal {
        width: (100 - (100% / 3));

        /* position start */
        .banner-product_left {
          .banner-product__open {
            left: -1px;
            //right: -1px;
          }
        }
        .banner-product_right {
          .banner-product__open {
            //left: -1px;
            right: -1px;
          }
        }
        /* position end */
      }

      &_full {
        width: 100%;

        /* position start */
        .banner-product_left {
          .banner-product__open {
            left: -1px;
            //right: -1px;
          }
        }
        .banner-product_right {
          .banner-product__open {
            //left: -1px;
            right: -1px;
          }
        }
        /* position end */
      }

    }

  }
  
  &_50 {

    .banner-products__item {
      
      &_mini {
