        }

      }
      
      &_normal {
        width: 50%;

        /* position start */
        .banner-product_left {
          .banner-product__open {
            left: -1px;
            //right: -1px;
          }
        }
        .banner-product_right {
          .banner-product__open {
            //left: -1px;
            right: -1px;
          }
        }
        /* position end */

