    .banner-products__item {
      
      &_mini {
        width: 50%;

        /* position start */
        .banner-product_left {
          .banner-product__open {
            left: -1px;
            //right: calc(-100% - 2px);
          }
        }
        .banner-product_right {
          .banner-product__open {
            //left: calc(-100% - 2px);
            right: -1px;
          }
        }
        /* position end */

      }
      
      &_normal {
        width: 100%;

        /* position start */
        .banner-product_left {
          .banner-product__open {
            left: -1px;
            //right: -1px;
          }
        }
        .banner-product_right {
          .banner-product__open {
            //left: -1px;
            right: -1px;
          }
        }
        /* position end */

      }
      
      &_full {
        width: 100%;

        /* position start */
        .banner-product_left {
          .banner-product__open {
            left: -1px;
            //right: -1px;
          }
        }
        .banner-product_right {
          .banner-product__open {
            //left: -1px;
            right: -1px;
          }
        }
        /* position end */

      }
      
    }
    
  }
  
  &_auto {
    border: 0;
    margin-left: 1px;

    .banner-products__item {
      border: 1px solid $color__text2;
      margin: -1px 0 0 -1px;
    }
  }
  
}