body.popup-content-open {
  overflow: hidden;
}

.popup-content {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0);
  z-index: $header-z-index + 1;
  visibility: hidden;
  transition: background-color 0.5s, visibility 0.5s;

  &_orange {
    .popup-content__body {
      color: #fff;
    }
    .popup-content__head {
      &-text {
        color: #fff;
        &:before {
          background-image: url(../img/svg/arrow-white.svg);
        }
      }
    }
    .popup-content__head,
    .popup-content__scroll {
      background: $color__primary;
    }
  }

  &_open {
    background: rgba(0,0,0,0.8);
    visibility: visible;

    .popup-content__scroll {
      transform: translateX(0);
    }
  }
}

.popup-content__scroll {
  position: fixed;
  top: 0;
  left: 24px;
  right: 0;
  bottom: 0;
  overflow: auto;
  background: #fff;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.2);
  transition: transform 0.5s;
  transform: translateX(100%);
  //padding: 66px 0 40px;
}

.popup-content__body {
  font-size: 16px;
  letter-spacing: 0.4px;
  padding: 66px 28px 0;

  @supports (position: sticky) {
    padding: 0 28px;
  }

  p {
    margin: 1.3em 0;
    &:first-child {
      margin-top: 0;
    }
  }
}
.popup-content__head {
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 66px;
  z-index: 2;
  align-items: center;
  padding: 0 16px;
  background: #fff;

  @supports (position: sticky) {
    position: sticky;
  }

  &-text {
    cursor: pointer;
    font-size: 20px;
    letter-spacing: 0.36px;
    color: #999999;

    &:before {
      content: '';
      display: inline-block;
      margin-right: 10px;
      width: 17px;
      height: 12px;
      transform: rotate(180deg);
      background: url(../img/svg/arrow.svg) no-repeat;
      background-size: contain;
    }
  }

  &-close {}
}