.header {
  background: $color__secondary;
  
  @include media-breakpoint-down($mobile-switch){
    //opacity: 0;
    //transition: .3s ease;
    //
    //.page-init & {
    //  opacity: 1;
    //}
  }
  
  &__bottom {
    padding-top: 166px;

    @include media-breakpoint-down($mobile-switch){
      padding-top: 120px;
    }
    
    &_no-padding {
      padding-top: 0;
    }
  }
  
  &__desktop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: $header-z-index;
    background: $color__secondary;
    transition: transform 0.2s;

    @include media-breakpoint-down($mobile-switch){
      display: none;
    }
  }
  
  &__item {
    margin: 14px 28px 0 0;

    @include media-breakpoint-down(dtx){
      margin: 14px 14px 0 0;
    }

    @include media-breakpoint-down(lt){
      margin: 0;
    }
    
    &-line {}
    
    &-only-line {
      display: none;
    }
    
    &-phone {
      margin-top: -4px;

      @include media-breakpoint-down(dtx){
        margin-top: 0;
      }
    }
    
    &-lang {
      display: none;
      margin: 5px 0 0 0 !important;

      @include media-breakpoint-down(lt){
        display: block;
      }
    }
    
    &:last-child {
      margin-right: 0;
    }
    
    &:nth-child(1),
    &:nth-child(3) {
      > ul {
        flex-direction: row-reverse;
      }
    }
    
    > ul {
      @extend %list;

      @include media-breakpoint-down(lt){
        display: flex;
      }
      
      > li {
        position: relative;
        margin-bottom: 10px;

        @include media-breakpoint-down(lt){
          margin-bottom: 0;
        }

        @include media-breakpoint-down(lt){
          margin: 0 18px 0 0;
        }
        
        &:last-child {
          margin-bottom: 0;
        }
        
        > a {
          position: relative;
          padding-left: 32px;

          @include media-breakpoint-down(dtx){
            font-size: 16px;
          }

          @include media-breakpoint-down(dt){
            font-size: 14px;
          }

          @include media-breakpoint-down(lt){
            padding-left: 26px;
          }
          
          svg {
            position: absolute;
            left: 0;
            stroke: $color__additional;
            fill: none;
            transition: 0.2s;

            @include media-breakpoint-down(lt){
              top: 0px;
            }

            &.header-icon-phone,
            &.header-icon-shop,
            &.header-icon-search,
            &.header-icon-review {
              width: 22px;
              height: 22px;

              @include media-breakpoint-down(lt){
                width: 18px;
                height: 18px;
              }
            }
            
            &.header-icon-video {
              width: 23px;
              height: 23px;

              @include media-breakpoint-down(lt){
                width: 16px;
                height: 16px;
              }
            }
          }
          
          &:hover {
            svg {
              stroke: $color__primary;
            }
          }
          
        }
      }
    }
  }
  
  &_hide {
    .header__mob,
    .header__desktop {
      transform: translateY(-100%);
    }
  }
  
  &_line {

    @include media-breakpoint-down(lt){
      .hide-line-lt {
        display: none;
      }
      
      .menu__drop-catalog {
        left: -203px;
        right: auto;
      }
    }
    
    @include media-breakpoint-down(tbx){
      .hide-line-tbx {
        display: none;
      }
      .menu {
        margin-top: 6px;
      }
    }
    
    &.header_hide {
      .header__mob,
      .header__desktop {
        box-shadow: none;
      }
    }

    .header__desktop {
      position: fixed;
      z-index: $header-z-index;
      
      .logo__text {
        display: none;
      }
      
      .header__item {
        > ul {
          > li {
            display: none;
            
            &.header__item-line,
            &.header__item-only-line {
              display: block;
            }
          }
        }
      }
    }

    .header-info {
      padding: 19px 0 20px;
      
      @include media-breakpoint-down(lt){
        display: flex;
        align-items: center;
        
        
        > div {
          &:last-child {
            margin: 0;
          }
        }
      }
    }

    .logo > a {
      padding-bottom: 0;
    }

    .header__desktop {
      background: #fff;
      box-shadow: 0 0 12px rgba(255, 102, 0, 0.4);
    }
  }
}

.header-info {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 32px 0;
  transition: all .25s;
  
  @include media-breakpoint-down(lt){
    display: block;
  }

  @include media-breakpoint-down(tbx){
    padding: 26px 0;
  }
  
  > div {
    display: flex;
    align-items: flex-start;

    @include media-breakpoint-down(lt){
      &:first-child {
        justify-content: space-between;
      }
      
      &:last-child {
        justify-content: flex-end;
        margin-top: -22px;
      }
    }
  }
}