.social {
  position: relative;
  z-index: 10;
  
  &__drop {
    position: absolute;
    top: -16px;
    right: 46px;
    box-shadow: 0 0 12px rgba(255, 102, 0, 0.4);
    opacity: 0;
    visibility: hidden;
    transform: translateX(50px);
    transition: 0.2s;
    
    &-content {
      position: relative;
      background: #fff;
      z-index: $social-z-index;
      padding: 16px;
      
      > div {
        margin-bottom: 8px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &-arrow-white,
    &-arrow {
      position: absolute;
      top: 28px;
      right: 2px;
      width: 22px;
      height: 22px;
      background: #fff;
      transform: translateY(-50%) translateX(50%) rotateZ(45deg);
    }

    &-arrow-white {
      z-index: 2;
    }
    
    &-arrow {
      z-index: -1;
      box-shadow: 0 0 12px rgba(255, 102, 0, 0.4);
    }
  }
  
  &__list {
    @extend %list;
    
    display: flex;
    align-items: center;
    
    li {
      margin-right: 15px;
      &:last-child {
        margin-right: 0;
      }
    }
    a {
      color: $color__text2;
      font-size: 22px;

      @include media-breakpoint-down(tbx){
        font-size: 18px;        
      }
      
      .fab {
        transition: 0.2s;
      }

      .social_open &,
      &:hover {
        .fab {
          
          &.fa-twitter {
            color: #50A4E6;
          }
          
          &.fa-odnoklassniki {
            color: #EC7E33;
          }
          
          &.fa-instagram {
            color: #8a3ab9;
          }
          
          &.fa-youtube {
            color: #FF0000;
          }
          
          &.fa-vk {
            color: #4D7198;
          }
          
          &.fa-facebook-f {
            color: #3B5692;
          }
          
        }
      }

      .social_open & {
        &:hover {
          .fab {
            opacity: .7;
          }
        }
      }
    }
  }
  
  &__add {
    position: relative;
    display: block;
    width: 27px;
    height: 27px;
    border-radius: 50%;
    border: 1px solid $color__additional;
    transition: 0.2s;
    
    @include media-breakpoint-down(tbx){
      width: 19px;
      height: 19px;
    }
    
    &:before,
    &:after {
      position: absolute;
      top: 50%;
      left: 50%;
      background: #000;
      content: "";
    }
    
    &:before {
      width: 13px;
      height: 1px;
      margin-top: -0.5px;
      margin-left: -6.5px;

      @include media-breakpoint-down(tbx){
        width: 9px;
        margin-left: -4.5px;
      }
    }
    
    &:after {
      width: 1px;
      height: 13px;
      margin-top: -6.5px;
      margin-left: -0.5px;

      @include media-breakpoint-down(tbx){
        height: 9px;
        margin-top: -4.5px;
      }
    }
  }
  
  &_open {
    .social__drop {
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
    }
    
    .social__add {
      transform: rotateZ(45deg);
    }
  }
}

.social-list {
  ul {
    @extend %list;
    
    li {
      margin-bottom: 15px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  
  a {
    display: flex;
    align-items: center;
    
    img {
      margin-right: 12px;
    }

    &:hover {
      .social-list__icon {
        background: $color__primary;
      }
    }
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 26px;
    height: 26px;
    font-size: 16px;
    margin-right: 12px;
    border-radius: 50%;
    background: $color__additional;
    color: $color__secondary;
    transition: background .25s;
  }

  + .footer-links {
    margin-top: 53px;
  }
  
  &_row {

    .social-list__icon {
      margin-right: 0;
      color: #fff;
    }

    ul {
      @include clearfix();
      
      li {
        float: left;
        margin: 0 16px 0 0;
      }
    }
    
    a {
      img {
        margin-right: 0;
      }
    }
  }
  
  &_flex {

    .social-list__icon {
      margin-right: 0;
      color: #fff;
    }

    ul {
      display: flex;
      justify-content: space-between;
      li {
        margin: 0;
      }
    }
    
    a {
      img {
        margin-right: 0;
      }
    }
  }
}