.popup-send-to-director {
  position: relative;
  padding: 24px 86px 30px;
  overflow: hidden;
  width: 951px;
  margin: 0 auto;

  @include media-breakpoint-down(tbx) {
    padding: 34px 123px 23px;
    width: 920px;
  }
  
  @include media-breakpoint-down(tb){
    padding: 70px 16px;
    width: auto;
  }

  &__name {
    font-size: 22px;
    margin-bottom: 34px;

    @include media-breakpoint-down(tbx) {
      font-size: 18px;
      letter-spacing: 0.36px;
      margin-bottom: 26px;
    }
    
    @include media-breakpoint-down(tb){
      font-size: 20px;
      margin-bottom: 29px;
    }
  }

  &__form {
  }

  &__description {
    font-size: 20px;
    text-align: center;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(tbx) {
      font-size: 16px;
      letter-spacing: 0.4px;
    }
    
    @include media-breakpoint-down(tb){
      font-size: 20px;
      letter-spacing: 0.36px;
      margin-top: 43px;
      span {
        display: block;
        font-size: 16px;
        letter-spacing: 0.4px;
        margin-top: 13px;
      }
    }
  }

  &__send {
    display: block;
    margin: 0 auto 20px;
    padding: 6px 22px 9px;

    @include media-breakpoint-down(tbx) {
      margin: 21px auto 0;
      padding: 6px 19px 9px;
      letter-spacing: 0.36px;
    }

    @include media-breakpoint-down(tb){
      margin: 22px auto 0;
      padding: 6px 16px 9px;
    }
    
    &_desk {
      @include media-breakpoint-down(tb){
        display: none;
      }
    }
    
    &_mob {
      display: none;
      @include media-breakpoint-down(tb){
        display: block;
      }
    }
  }
  
  &__action {
    margin-top: 18px;

    @include media-breakpoint-down(tbx){
      display: flex;
      flex-direction: column-reverse;
      margin-top: 15px;
    }

    @include media-breakpoint-down(tb){
      display: block;
    }
  }
}