.seo {

  
  @include media-breakpoint-down(tb){
    padding: 40px 0;
  }

  &__name {
    .section-title {
      @include media-breakpoint-down(phx){
        margin-bottom: 18px;
      }
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -25px;

    @include media-breakpoint-down(tb){
      display: block;
      margin: 0;
    }
  }

  &__col {
    width: 25%;
    padding: 0 25px;

    @include media-breakpoint-down(tbx){
      width: 50%;
      margin: 0 0 15px;
    }

    @include media-breakpoint-down(tb){
      width: auto;
      padding: 0;
    }
  }

  &__toggle {
    margin: 25px 0 0;
    text-align: center;

    @include media-breakpoint-down(tb){
      display: none;
    }

    a {
      cursor: pointer
    }

    .dot-link-one-line__name {
      &::before {
        display: block;
        position: absolute;
        right: -17px;
        margin-top: -2px;
        top: 50%;
        -webkit-transition: 0.2s;
        -o-transition: 0.2s;
        transition: 0.2s;
        content: "";
        background: url(../img/dropdown-arrow.svg) no-repeat;
        width: 11px;
        height: 8px;
        transform: rotate(0);
      }
      &-active::before {
        transform: rotate(-180deg);
      }
    }
    
  }

  &__content {
    @include media-breakpoint-down(tbx){
      font-size: 16px;
    }
    //@include media-breakpoint-down(phx){
    //  font-size: 16px;
    //}
  }

  &__slide {
    display: none;
    margin-top: 15px;
    &-wrapper {
      border-top: 1px solid transparent;
    }
  }

  &__button {
    display: none;
    text-align: center;
    margin-top: 20px;
    
    @include media-breakpoint-down(tb){
      display: block;
    }
    
    .btn {
      padding: 6px 22px 9px;
      letter-spacing: 0.4px;
      
      @include isTouch {
        &:hover {
          background: transparent;
          color: #000;
          border-color: #999999;
        }
        
        &_reverse {
          &:hover {
            background: #fff;
            color: #FF6600;
            border-color: #FF6600;
          }
        }
      }
    }
  }


}