.download-catalog {
  position: relative;
  z-index: 1;
  overflow: hidden;
  margin: 0 -16px;
  
  &__content {
    position: relative;
    padding: 0 16px;
  }

  &__play {
    position: absolute;
    top: 200px;
    left: 50%;
    transform: translateX(-50%);
  }

  &__bg {
    position: absolute;
    background-repeat: no-repeat;
    background-position: left bottom;
    background-size: cover;
    top: -32px;
    left: 0;
    right: 0;
    height: 100%;
    //z-index: 5;
    margin: 0 -16px;

    @include media-breakpoint-down(ph){
      top: -120px;
      left: -68px;
    }
  }
  
  &__name {
    position: relative;
    text-align: center;
    font-size: 20px;
    letter-spacing: 0.36px;
    margin-bottom: 15px;
    z-index: 10;
    
    b {
      font-weight: 400;
    }
    span {
      font-weight: 300;
    }
  }
  
  &__info {
    position: relative;
    display: flex;
    justify-content: center;
    z-index: 10;

    font-size: 12px;
    letter-spacing: 0.48px;
    
    span {
      color: $color__primary;
    }
  }
  
  &__type {
    position: relative;
    padding-right: 22px;
    
    &:after {
      position: absolute;
      width: 3px;
      height: 3px;
      top: 5px;
      right: 10px;
      background: $color__additional;
      border-radius: 50%;
      content: "";
    }
  }
  
  &__update {}
  
  &__steps {
    position: relative;
    background: $color__secondary;
    background-image: linear-gradient(to bottom, #fff 0, $color__secondary 80px);
    //margin: 0 -16px 0;
    margin: 320px -16px 0;
    padding: 0 16px 26px;
    //margin-top: 60px;
    
    ul {
      @extend %list;
    }
  }
  
  &__step {
    position: relative;
    padding-left: 27px;
    margin-bottom: 14px;
    z-index: 10;
    
    &-input {
      margin: 10px 0;
      input {
        background: #fff;
        border-radius: 3px;
        border: 0;
        display: block;
        width: 100%;
        padding: 8px 10px;
        font-size: 16px;
        letter-spacing: 0.4px;
        color: $color__additional;
        outline: none;
        &::placeholder {
          color: $color__additional;
        }
      }
    }
    
    &-index {
      position: absolute;
      top: -2px;
      left: 0;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      border: 1px solid black;
      overflow: hidden;
      font-size: 14px;
      font-weight: 700;
      text-align: center;
      line-height: 18px;
    }
    
    &-label {
      font-size: 16px;
      letter-spacing: 0.4px;
    }
    
    &-info {
      position: relative;
      padding-left: 42px;
      margin: 12px 0 0;
    }
    
    &-icon {
      svg {
        position: absolute;
        top: 0;
        left: 0;
        stroke: $color__additional;
        fill: none;
        
        &.download-catalog__step-sms {
          width: 29px;
          height: 27px;
        }
        
        &.download-catalog__step-secure {
          width: 26px;
          height: 29px;
        }
      }
    }
    
    &-text {
      color: $color__additional;
      font-size: 14px;
      letter-spacing: 0.4px;
    }
    
  }
  
}