.review-gallery {
  display: flex;
  flex-wrap: wrap;
  margin: 30px -8px 7px;

  @include media-breakpoint-down(tbx) { // change
    margin: -5px -4px 16px;
    padding-left: 56px;
  }
  @include media-breakpoint-down(phx) {
    padding-left: 0;
    padding-bottom: 12px;
    margin-bottom: 0;
  }

  &__el {
    position: relative;
    width: 192px;
    padding: 0 8px;
    margin-bottom: 14px;

    @include media-breakpoint-down(dt) {
      width: 201px;
    }
    @include media-breakpoint-down(tbx) { // change
      width: 120px;
      padding: 0 4px;
      margin-bottom: 8px;
    }
    @include media-breakpoint-down(phx) {
      width: 33.3333%;
    }
  }

  &__pic {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 56.2%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    
    &:before {
      position: absolute;
      background: rgba(0,0,0,0.5);
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
    
    @include media-breakpoint-down(tbx){ // change
      &:before {
        content: "";
      }
      
    }
  }
  
  &__lupa {
    position: absolute;
    z-index: 2;
    opacity: 1;
    transition: opacity .3s;
    cursor: pointer;
    bottom: 0;
    right: 12px;

    &:hover {
      opacity: .8;
    }
    
    .lupa-m {
      display: none;
      position: absolute;
      top: 50%; 
      left: 50%;
      margin-top: -9px;
      margin-left: -9px;
    }
    
    @include media-breakpoint-down(tbx){ // change
      right: 0;
      bottom: 0;
      top: 0;
      left: 0;
      
      .lupa {
        display: none;
      }
      
      .lupa-m {
        display: block;
      }
    }
  }
}