.category-select-section {
  margin-bottom: 55px;
  overflow: hidden;

  @include media-breakpoint-down(phx) {
    margin-bottom: 30px;
  }

  .section-title {
    @include media-breakpoint-down(tbx) {
      margin-bottom: 33px;
    }
    @include media-breakpoint-down(phx) {
      margin-bottom: 8px;
    }
  }
}

.category-select {
  position: relative;
  display: flex;
  justify-content: center;

  @include media-breakpoint-down(tbx) {
    flex-wrap: wrap;
  }

  .elastic-border {
    position: absolute;
    top: 0;
    height: 100%;
    left: -50px;
    width: 100px;
    z-index: 2;
    transition: all .3s ease-in-out;
    
    &_left {
      //visibility: hidden;
    }
    
    &_right {
      left: auto;
      right: -50px;
    }

    @include media-breakpoint-down(phx) {
      visibility: hidden;
    }
  }

  &::after {
    @include media-breakpoint-down(phx) {
      content: '';
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 2px;
      background: #fff;
      z-index: 2;
    }
  }

  &__el {
    position: relative;
    display: inline-block;
    width: 14%;
    max-width: 252px;
    height: 336px;
    text-align: center;
    margin: 0 0 30px;
    padding: 0;
    box-sizing: content-box;
    transition: padding .3s ease-in-out;
    z-index: 1;

    @include media-breakpoint-down(tbx) {
      width: 25%;
      height: 300px;
      max-width: none;
    }
    @include media-breakpoint-down(phx) {
      width: 50%;
      height: 230px;
      margin-bottom: 0;
    }
    
    

    &:hover {
      @include media-breakpoint-up(phx) {
        //padding: 0 10px;
        z-index: 3;

        //.elastic-border {
        //  left: -60px;
        //  
        //  &_right {
        //    left: auto;
        //    right: -60px;
        //  }
        //}

        + .category-select__el {
          .elastic-border {
            &_left {
              visibility: hidden;
            }
          }
        }

        .category-select__el-wrap {
          &::before {
            //left: -36px;
            //right: -36px;
          }
        }

        .category-select__pic {
          transform: scale(1.1,1.1);
        }
      }
    }

    &:first-child {
      .category-select__el-wrap {
        &::before {
          border-left: none;
        }
      }
    }

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3) {
      @include media-breakpoint-down(tbx) {
        width: 33.3333%;
      }
      @include media-breakpoint-down(phx) {
        width: 50%;
      }
    }

    &:nth-child(4) {
      @include media-breakpoint-down(tbx) {
        .category-select__el-wrap {
          &::before {
            border-left: none;
          }
        }
      }
    }

    &:last-child:nth-child(2n+1) {
      @include media-breakpoint-down(phx) {
        width: 100%;
      }
    }
  }

  &__el-wrap {
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -126px;
    //transform: translateX(-126px);
    width: 252px;
    //min-width: 100%;
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding-top: 12px;
    transition: all .3s ease-in-out;

    @include media-breakpoint-down(tbx) {
      padding-top: 16px;
    }
    @include media-breakpoint-down(phx) {
      padding-top: 27px;
    }

    &::before {
      //content: '';
      display: block;
      position: absolute;
      top: 0;
      left: -5px;
      right: -5px;
      bottom: 0;
      border-left: 1px solid $color__text2;
      border-right: 1px solid $color__text2;
      background: #fff;
      transition: all .3s ease-in-out;


      @include media-breakpoint-down(tbx) {
        content: "";
      }

      @include media-breakpoint-down(tbx) {
        border-left: none;
        border-right: none;
      }
    }

    @include media-breakpoint-down(tbx) {
      position: relative;
      left: auto;
      width: 100%;
      transform: none;
      margin-left: 0;
    }
    @include media-breakpoint-down(phx) {
      border-left: none;
      border-right: none;
      border-bottom: 1px solid $color__text2;
    }
  }

  &__descr {
    position: relative;
    z-index: 1;

    .h3 {
      margin: 0;
      font-size: 22px;
      letter-spacing: 0.36px;
      font-weight: normal;
      color: $color__text;
      text-transform: uppercase;

      @include media-breakpoint-down(lt) {
        font-size: 20px;
      }
      @include media-breakpoint-down(tbx) {
        font-size: 18px;
      }
      @include media-breakpoint-down(phx) {
        font-size: 16px;
        text-transform: none;
      }

      span {
        @include media-breakpoint-down(phx) {
          border-bottom: 1px dotted currentColor;
        }
      }
    }
  }

  &__subttl {
    display: block;
    margin: 8px 0 0;
    font-size: 16px;
    letter-spacing: 0.4px;
    color: $color__primary;

    @include media-breakpoint-down(tbx) {
      margin-top: 2px;
    }
    @include media-breakpoint-down(phx) {
      font-size: 12px;
      letter-spacing: 0.48px;
    }
  }

  &__pic {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    height: 240px;
    padding: 0 20px;
    text-align: right;
    transform: scale(1,1);
    transition: transform .25s ease;
    z-index: 1;

    @include media-breakpoint-down(phx) {
      height: 140px;
      padding: 0 10px 24px;
    }

    img {
      max-height: 100%;
      width: 100%;
      object-fit: contain;
    }
  }

}