/* попап (подарки) */

.popup.popup-gift {
  width: 950px;
  margin: 0 auto;
  padding: 46px 80px 6px;
  
  @include media-breakpoint-down(tb) {
    width: auto;
    padding: 36px 16px 26px;
  }

  @include media-breakpoint-down(phx){
    padding: 61px 8px 51px;
  }
  
  .popup__name {
    margin-bottom: 22px;
    
    @include media-breakpoint-down(tb) {
      margin-bottom: 26px;
    }
    
    @include media-breakpoint-down(phx) {
      font-size: 20px;
      margin-bottom: 22px;
    }
  }
  
  .input-phone_tender {
    min-width: 0;
  }
}

.popup__gift {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  
  &-item {
    width: 216px;
    margin: 18px 20px;

    @include media-breakpoint-down(tb) {
      margin: 10px 20px;      
    }
    
    @include media-breakpoint-down(phx){
      width: 50%;
      margin: 12px 0;
      padding: 0 8px;
    }
  }
  
  &-link {}
  
  &-img {
    position: relative;
    height: 122px;

    @include media-breakpoint-down(phx){
      height: 71px;      
    }
    
    img {
      @extend .img-responsive;
      margin: 0 auto;
    }
  }
  
  &-name {
    font-size: 17px;
    letter-spacing: 0.4px;
    text-align: center;
    margin-top: 18px;
    line-height: 1.3;
    overflow: hidden;
    max-height: ((17px * 1.3) * 3);

    @include media-breakpoint-down(tb) {
      margin-top: 10px;
      max-height: ((17px * 1.3) * 2);
    }

    @include media-breakpoint-down(phx){
      font-size: 16px;
      max-height: ((16px * 1.3) * 2);
      text-align: left;
    }
    
    &_desktop {
      @include media-breakpoint-down(phx){
        display: none;
      }
    }
    &_mobile {
      display: none;
      @include media-breakpoint-down(phx){
        display: block;
      }
    }
  }
  
  &-plus {
    position: absolute;
    top: 0;
    left: 0;
    width: 19px;
    height: 19px;
    border-radius: 50%;
    background: $color__additional;
    transition: .2s;

    @include media-breakpoint-down(phx){
      width: 16px;
      height: 16px;
    }

    &:before,
    &:after {
      position: absolute;
      top: 50%;
      left: 50%;
      background: #fff;
      content: "";
    }

    &:before {
      width: 1px;
      height: 11px;
      margin-top: -5.5px;
      margin-left: -0.5px;

      @include media-breakpoint-down(phx){
        height: 9px;
        margin-top: -4.5px;
      }
    }

    &:after {
      width: 11px;
      height: 1px;
      margin-top: -0.5px;
      margin-left: -5.5px;

      @include media-breakpoint-down(phx){
        width: 9px;
        margin-left: -4.5px;
      }
    }
  }
  
}