.catalog {
  margin: 34px 0;

  @include media-breakpoint-down(tbx){
    margin: 26px 0 100px;
  }

  @include media-breakpoint-down(tb){
    margin: 34px 0 0;
  }
  
  &__why-buy {
    display: none;
    margin: 40px 0 0;
    
    @include media-breakpoint-down(tb) {
      display: block;
    }
  }
  
  &__menu-panel {
    display: none;
    margin-top: 14px;
    
    @include media-breakpoint-down(tb) {
      display: block;
    }
  }
  
  &__row {
    @include clearfix();
    margin-bottom: 80px;
    
    &:last-child {
      margin-bottom: 0;
    }
  } 
  
  &__aside {
    float: left;
    width: 300px;
    padding: 6px 0;

    @include media-breakpoint-down(dt){
      width: 250px;
    }

    @include media-breakpoint-down(tbx){
      width: 196px;
    }

    @include media-breakpoint-down(tb){
      display: none;
    }
    
  }
  
  &__content {
    margin-left: 340px;

    @include media-breakpoint-down(dt){
      margin-left: 290px;
    }

    @include media-breakpoint-down(tbx){
      margin-left: 224px;
    }

    @include media-breakpoint-down(tb){
      margin-left: 0;
    }
    
    &-bottom {
      @include media-breakpoint-down(dt){
        margin-left: 0;
      }
    }
  }
  
  &__products {
    display: flex;
    flex-wrap: wrap;
    margin: 40px 0 30px;
    border: 1px solid $color__text2;
    border-bottom: 0;
    border-right: 0;

    @include media-breakpoint-down(dt){
      border: 0;
      border-top: 1px solid $color__text2;
      margin-bottom: 52px;
    }
    
    @include media-breakpoint-down(phx){
      border-top: 0;
      margin: 22px 0 22px;
    }

    + .catalog__pagination {
      @include media-breakpoint-down(phx) {
        .pagination {
          padding-top: 0;
        }

        margin-top: -9px;
      }
    }
  }
  
  &__next {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
    border-right: 1px solid $color__text2;
    border-bottom: 1px solid $color__text2;


    @include media-breakpoint-down(dt){
      width: 100%;
      border: 0;
      margin-top: 66px;
    }


    @include media-breakpoint-down(phx){
      width: 50%;
      margin: 0;
      padding: 16px;
      border-bottom: 1px solid $color__text2;
    }
    
    &:hover {
      .product-next {
        &__name {
          &:after {
            width: 0;
          }
        }
        &__icon {
          border-color: $color__primary;
          
          &:before {
            border-color: $color__primary;
          }
        }
      }
    }
  }
  
  &__pagination {
    @include media-breakpoint-down(tb){
      margin: 0 -16px;
      border-bottom: 1px solid $color__text2;
      border-top: 1px solid $color__text2;
    } 
    @include media-breakpoint-down(phx){
      border-top: 0;
    } 
  } 
  
  &__views {
    display: none;
    margin-top: 42px;
    
    @include media-breakpoint-down(tb){
      display: block;
    }
  }
  
  &__product {
    width: 50%;
    border-right: 1px solid $color__text2;
    border-bottom: 1px solid $color__text2;

    @include media-breakpoint-down(dt){
      width: 100%;
      border-left: 1px solid $color__text2;
    }

    @include media-breakpoint-down(phx){
      width: 50%;
      border-left: 0;
      border-right: 0;
    }
    
    &-row {
      display: flex;
      width: 100%;

      @include media-breakpoint-down(dt) {
        display: block;
      }

      @include media-breakpoint-down(phx) {
        display: none;
      }
    }
    
    &-col {
      display: flex;
      flex-direction: column;
      width: 50%;

      @include media-breakpoint-down(dt) {
        display: block;
        width: 100%;
      }
      
      .catalog__product {
        flex: 1 1 auto;
        width: auto;
      }
    }
    
    &_320 {
      display: none;
      
      @include media-breakpoint-down(phx){
        display: block;
      }
    }
    
    .product {
      border: 0;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      &_320 {
        display: none;
        
        @include media-breakpoint-down(phx) {
          display: flex;
        }
      }

      &_960 {
        display: none;

        @include media-breakpoint-down(dt) {
          display: block;
        }

        @include media-breakpoint-down(phx) {
          display: none;
        }
      }

      &_1920 {
        @include media-breakpoint-down(dt) {
          display: none;
        }
      }
    }
  }
  
  &__banner {
    position: relative;
    border-right: 1px solid $color__text2;
    border-bottom: 1px solid $color__text2;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-top: 102%;

    @include media-breakpoint-down(dt){
      border: 0;
    }

    @include media-breakpoint-down(phx){
    }
    
    &-top {}
    
    &-name {
      flex: 0 0 auto;
      font-size: 48px;
      text-transform: uppercase;
      white-space: nowrap;
      
      @include media-breakpoint-down(dtx){
        font-size: 40px;
      }

      @include media-breakpoint-down(phx){
        font-size: 24px;
      }
    }
    
    &-text {
      font-size: 20px;
      font-weight: 200;

      @include media-breakpoint-down(dtx){
        font-size: 18px;
      }

      @include media-breakpoint-down(phx){
        font-size: 14px;
        font-weight: 300;
      }
    }
    
    &-btn {
      margin-top: 20px;

      @include media-breakpoint-down(phx){
        margin-top: 10px;
      }
      
      a {
        padding: 10px 40px;

        @include media-breakpoint-down(phx){
          padding: 10px 16px;
        }
      }
    }
    
    &-info {
      position: absolute;
      padding: 30px;

      @include media-breakpoint-down(phx){
        padding: 16px;
      }
      
      &.catalog__banner_top {
        top: 0;
        left: 0;
        width: 100%;

        .catalog__banner-name {
          margin-right: 22px;
        }
        .catalog__banner-top {
          display: flex;
          align-items: center;
        }
      }
      
      &.catalog__banner_left-center {
        top: 50%;
        left: 0;
        width: 100%;
        transform: translateY(-50%);
      }
    }
    
    &_50 {
      padding-top: 50%;
    }

    &_50-mobile {
      padding-top: 0;
      width: 50%;
    }

    &_auto {
      padding-top: 0;
    }
    
    &_full {
      padding-top: 25%;
      width: 100%;

      @include media-breakpoint-down(dt){
        padding-top: 44%;
      }

      @include media-breakpoint-down(phx){
        padding-top: 60%;
        width: calc(100% + 32px);
        margin: -1px -16px 0;
      }
      
    }
    
    &_show-desktop {
      @include media-breakpoint-down(dt){
        display: none;
      }
    }
    
    &_show-mobile {
      display: none;
      
      @include media-breakpoint-down(dt){
        display: block;
      }
    }
  }
  
  &__name {
    h1 {
      font-size: 32px;
      font-weight: normal;
      letter-spacing: 0.32px;


      @include media-breakpoint-down(tbx){
        font-size: 20px;
        letter-spacing: 0.36px;
        text-transform: uppercase;
        padding: 6px 10px;
        margin: 2px 0;
      }

      @include media-breakpoint-down(tb){
        font-size: 22px;
        padding: 0;
      }
    }
  }

  &__video {
    &_white {
      margin-top: 58px;

      .videoobzor {
        background: #fff;
      }
    }
  }

  &_index {
    margin: 0;



    .catalog__products {
      margin: 0 0 50px;

      @include media-breakpoint-down(phx){
        //margin: 0 -16px;
      }
    }

    .catalog__product {
      @include media-breakpoint-down(phx){
        &:nth-last-child(1),
        &:nth-last-child(2) {
          border-bottom: 0;
        }
      }
    }
  }
  
}