/* попап (пригласить в тендер) */

.popup.popup-invite-tender {
  width: 950px;
  margin: 0 auto;
  padding: 36px 138px;
  
  @include media-breakpoint-down(lt) {
    width: 920px;
    padding: 36px 122px;
  }
  
  @include media-breakpoint-down($mobile-switch){
    width: auto;
    padding: 44px 16px;
  }
  
  .popup__name {
    margin-bottom: 46px;
    
    @include media-breakpoint-down(lt) {
      margin-bottom: 36px;
    }

    @include media-breakpoint-down($mobile-switch){
      margin-bottom: 26px;
    }
  }
  
  .input-phone_tender {
    min-width: 0;
  }
  
  .popup__row_btn {
    margin-top: 40px;
    .btn {
      padding: 6px 17px 9px;
      letter-spacing: 0.36px;
    }
  }
}