.litle-product {
    flex: 2 1 20%;
    padding: 22px 18px 18px;
    border: 1px solid $color__text2;
    margin-left: -1px;
    margin-top: -1px;
    @include only-medium {
        flex: 0 0 33.33%;
        &:nth-child(3n + 1) {
            margin-left: 0;
        }
        &:not(:nth-child(3n + 1)) {
            flex-basis: calc(33.33% + 1px);
        }
    }
    @include small {
        flex: none;
        width: calc(50% - 18px);
        margin: 0;
        border: 0;
        border-bottom: 1px solid $color__text2;
        &:nth-of-type(2n + 1) {
            margin-left: 18px;
            padding-left: 0;
        }
        &:nth-of-type(2n + 2) {
            margin-right: 18px;
            padding-right: 0;
        }
    }
    &__header {
        color: black;
        font-size: 18px;
        margin-bottom: 20px;
        @include medium {
            font-size: 16px;
        }
        @include small {
            max-width: 100%;
            display: flex;
            flex-direction: column;
        }
        div {
            @include small {
                flex: none;
                width: 100%;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                span {
                    border-bottom: 1px dotted $color__additional;
                }
            }
        }
    }
    &__img {
        display: flex;
        width: 100%;
        height: 150px;
        justify-content: center;
        margin-bottom: 20px;
        img {
            height: 100%;
        }
    }
    &__points {
        display: flex;
        justify-content: center;
        margin-bottom: 20px;
    }
    &__point {
        position: relative;
        width: 12px;
        height: 12px;
        border: 1px solid $color__additional;
        border-radius: 50%;
        transition: .2s;
        &:after {
            position: absolute;
            top: 50%;
            left: 50%;
            display: block;
            content: '';
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background-color: transparent;
            transition: .2s;
            transform: translate(-50%, -50%);
        }
        @include only-medium {
            width: calc(20% - 3px);
            height: 6px;
            border-radius: 0;
            border: 0;
            background-color: $color__secondary;
            &::after {
                display: none;
            }
            &-active {
                border: none;
                transform: scale(1);
            }
        }
        &-active {
            border-color: $color__primary;
            transform: scale(1.5);
            &:after {
                background-color: $color__primary;
                transform: translate(-50%, -50%);
            }
            @include only-medium {
                transform: scale(1);
                background-color: $color__primary;
            }
        }
    }
    &__point:not(:last-child) {
        margin-right: 18px;
        @include only-medium {
            margin-right: 3px;
        }
    }
    &__costs {
        display: flex;
        justify-content: space-around;
        align-items: flex-end;
        margin-bottom: 20px;
        @include small {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 10px;
        }
    }
    &__old-cost {
        color: $color__additional;
        font-size: 18px;
        letter-spacing: 0.36px;
        text-decoration-line: line-through;
        @include medium {
            font-size: 16px;
        }
    }
    &__new-cost {
        font-size: 26px;
        letter-spacing: 0.32px;
        color: $color__primary;
        @include medium {
            font-size: 20px;
        }
    }
    &__availability-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto 20px auto;
        @include medium {
            width: 100%;
            justify-content: space-between;
            margin-left: 0;
        }
        @include small {
            justify-content: center;
        }
    }
    &__availability {
        position: relative;
        display: inline-block;
        @include medium {
            font-size: 14px;
        }
    }
    &__availability::before {
        position: absolute;
        top: 45%;
        left: -20px;
        transform: translateY(-50%);
        display: block;
        content: '';
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: #339900;
    }
    &__footer {
        display: flex;
        width: 100%;
        @include medium {
            justify-content: center;
        }
    }
    &__left {
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        @include small {
            width: 100%;
            margin-bottom: 10px;
        }
    }
    &__right {
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        @include medium {
            width: 100%;
        }
    }
    &__plus,
    &__minus {
        flex: none;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 21px;
        height: 21px;
        border-radius: 50%;
        border: 1px solid $color__additional;
        font-size: 18px;
        line-height: 18px;
        padding-top: 1px;
        margin-right: 10px;
        @include medium {
            margin-right: 6px;
        }
        @include small {
            font-size: 16px;
            line-height: 16px;

        }
    }
    &__count {
        flex: none;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 57px;
        height: 37px;
        border: 1px solid $color__additional;
        margin-right: 10px;
        @include medium {
            width: 37px;
            height: 23px;
            font-size: 12px;
            margin-right: 6px;
        }
    }
    &__flex-container {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
    }
    &__hide-medium {
        @include medium {
            display: none;
        }
    }
    &__show-f-medium {
        display: none;
        @include medium {
            display: flex;
        }
    }
    &__hide-small {
        @include small {
            display: none;
        }
    }
    &__show-small {
        display: none;
        @include small {
            display: block;
        }
    }
}