.banner-download-wrap {
  background-color: $color__secondary;
}
.banner-download {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  text-align: right;
  font-size: 0;
  position: static;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  
  &__content {
    display: inline-block;
    margin: 21px;
    border-radius: 3px;
    overflow: hidden;
    text-align: center;
  }
  
  &__link {
    position: relative;
    display: block;
    color: #fff;
    background: $color__primary;
    text-transform: uppercase;
    font-size: 16px;
    letter-spacing: 0.4px;
    padding: 12px 17px 9px 39px;
    
    svg {
      position: absolute;
      top: 12px;
      left: 15px;
      stroke: none;
      fill: white;
      width: 14px;
      height: 17px;
    }
    
    &:focus,
    &:hover {
      color: #fff;
    }
  }
  
  &__info {
    font-size: 14px;
    letter-spacing: 0.4px;
    background: #fff;
    color: #000;
    padding: 6px 4px 3px;
    
    span {
      position: relative;
      padding-right: 11px;
      
      &:after {
        position: absolute;
        width: 3px;
        height: 3px;
        top: 5px;
        right: 4px;
        background: #999999;
        border-radius: 50%;
        content: "";
      }
    }
  }
  
  &_index {
    display: block;
    position: relative;
    transform: translateY(0);
    left: 0;
    right: 0;
    transition: all .3s .5s;
  }
  
  &_catalog {
    display: none;
    width: 100%;
    
    @include media-breakpoint-down(dt){
      display: block;
    }
    
    @include media-breakpoint-down(phx){
      display: none;
    }
  }

  &_wide {
    visibility: hidden;
    transform: translateY(100%);
    transition: all .3s;

    .banner-download-active & {
      visibility: visible;
      transform: translateY(0);
    }
    
    @include media-breakpoint-down(ph) {
      .banner-download__info {
        span {
          display: block;

          &::after {
            display: none;
          }
        }
      }
    }
    @include media-breakpoint-up(dt) {
      .banner-download__wrap {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        height: 120px;
      }

      .banner-download__content {
        display: flex;
        flex-direction: row-reverse;
        margin: 0;
      }

      .banner-download__info,
      .banner-download__link {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 49px;
        padding: 0 25px;
        font-size: 20px;
        line-height: 1;
      }

      .banner-download__info {
        span {
          font-weight: bold;

          &::after {
            margin: 0 4px 4px 12px;
            display: inline-block;
            position: relative;
            top: auto;
            right: auto;
            width: 5px;
            height: 5px;
            background-color: $color__text;
          }
        }
      }

      .banner-download__link {
        padding-left: 60px;

        svg {
          top: 15px;
          left: 25px;
        }
      }
    }
  }
}