.video-all {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid $color__text2;
  height: 220px;
  
  &__block {
    svg {
      display: block;
      margin: 0 auto 20px;
      width: 62px;
      height: 62px;
      fill: none;
      stroke: $color__primary;
    }
  }
  
  &__name {
    font-size: 24px;
  }
}

.video-gallery {
  @include media-breakpoint-down(phx) {
    padding-bottom: 50px;
    margin-left: -16px;
    margin-right: -16px;
  }

  .swiper-pagination {
    @include media-breakpoint-up(tb) {
      display: none;
    }
  }
  
  &__item {
    // border-top: 1px solid $color__text2;
  }

  &_obzor {
    padding-bottom: 50px;
  }

  &_mobile {
    
    @include media-breakpoint-up(tb) {
      .swiper-wrapper {
        display: block;
        height: auto;
      }

      .swiper-slide {
        + .swiper-slide {
          padding-top: 9px;
          margin-top: 12px;
          border-top: 1px solid $color__text2;
        }
      }

      .swiper-button-prev-kronos,
      .swiper-button-next-kronos {
        display: none;
      }

      .video {
        padding-top: 61.5%;
        background: transparent;

        &__bg,
        &__play {
          height: 0;
          padding-top: 61.5%;
        }

        &__play::before {
          background-size: 41px;
        }

        &__title {
          margin-bottom: 7px;
          font-size: 16px;
          letter-spacing: 0.4px;
          text-align: center;
          color: $color__text;
        }

        &__name {
          font-size: 16px;
          color: $color__text;
        }

        &__text {
          margin-right: 0;
          font-family: Lato, sans-serif;
          flex: none;
          font-size: 16px;
          letter-spacing: 0.4px;
          white-space: nowrap;
          color: $color__additional;
        }

        &__content-top,
        &__content-bottom {
          position: relative;
        }
      }

      .video_review {
        .video__content {
          height: auto;

          &-top {
            padding: 8px 0 0;
          }

          &-bottom {
            padding: 10px 0 0;
            text-align: center;
          }
        }
      }

      .address {
        font-size: 14px;
        text-align: center;

        svg {
          width: 12px;
          height: 15px;
          margin-right: 7px;
        }

        &__country {
          img {
            width: 15px;
          }
        }
      }
    }

    @include media-breakpoint-up(tbx) {
      .swiper-slide {
        + .swiper-slide {
          padding-top: 32px;
          margin-top: 28px;
        }
      }

      .video {
        padding-top: 56.25%;

        &__bg,
        &__play {
          padding-top: 56.25%;
        }

        &__play::before {
          background-size: auto;
        }

        &__title {
          margin-bottom: 13px;
          padding-right: 19px;
          font-size: 20px;
          letter-spacing: 0.36px;
        }

        &__name {
          font-size: 18px;
        }

        &__text {
          margin-right: 15px;
          font-size: 18px;
        }
      }

      .video_review {
        .video__content {
          height: auto;

          &-top {
            padding: 17px 27px 0;
          }

          &-bottom {
            display: flex;
            padding: 23px 27px 2px 30px;
            text-align: left;
          }
        }
      }

      .address {
        font-size: 18px;

        svg {
          width: 17px;
          height: 21px;
          margin-right: 10px;
        }

        &__country {
          img {
            width: 22px;
          }
        }
      }
    }

    @include media-breakpoint-down(phx) {
      .address {
        font-size: 14px;
        color: rgba(255,255,255,.65);
      }
    }
  }

  &-info {
    display: none;

    .mfp-wrap & {
      display: block;
    }
  }

  &__title {
    position: absolute;
    bottom: calc(100% + 34px);
    left: 0;
    right: 0;
    text-align: center;
    color: #fff;
    font-size: 22px;
    line-height: 1.2;
    

    @include media-breakpoint-down(tbx){
      font-size: 18px;
      letter-spacing: 0.36px;
      bottom: calc(100% + 14px);
    }
    
    @include media-breakpoint-down(tb) {
      bottom: calc(100% + 11px);
    }
    @include media-breakpoint-down(phx) {
      bottom: calc(100% + 21px);
      font-size: 20px;
    }
  }

  &__nav {
    list-style: none;
    position: absolute;
    bottom: -58px;
    left: 0;
    right: 0;
    margin: 0 auto;
    padding: 0;
    text-align: center;
    line-height: 1;

    @include media-breakpoint-down(tbx) {
      bottom: -41px;
    }
    @include media-breakpoint-down(tb) {
      bottom: -45px;
    }
    @include media-breakpoint-down(phx) {
      bottom: -85px;
    }

    li {
      position: relative;
      display: inline-block;

      @include media-breakpoint-down(phx) {
        display: block;
      }

      + li {
        @include media-breakpoint-down(phx) {
          margin-top: 26px;
        }
        @include media-breakpoint-up(tb) {
          position: relative;
          margin-left: 15px;
          padding-left: 20px;

          &::before {
            content: '';
            position: absolute;
            top: 50%;
            margin-top: -10px;
            left: 0;
            width: 1px;
            height: 21px;
            background: #fff;
          }
        }
      }

      > a, span {
        font-size: 18px;
        color: #fff;

        @include media-breakpoint-down(tbx){
          font-size: 16px;
        }
        @include media-breakpoint-down(phx) {
          font-size: 18px;
        }
      }

      > a {
        &:hover {
          color: $color__primary;
        }
      }
    }
  }
}

.video {
  display: block;
  position: relative;

  &:hover {
    .video__bg {
      //opacity: 1;
    }

    .video__play {
      &::after {
        //opacity: 1;
      }
    }
  }

  &_review {
    @include media-breakpoint-down(ph) {
      width: 100%;
      height: 0;
      padding-top: 67.5%;
    }

    .video__content {
      height: 280px;

      @include media-breakpoint-down(tbx) {
        height: 220px;
      }
      @include media-breakpoint-down(phx) {
        height: 280px;
      }
      @include media-breakpoint-down(ph) {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }

      &-top {
        padding: 16px 20px;

        @include media-breakpoint-down(tbx) {
          padding: 13px 16px;
        }

        @include media-breakpoint-down(phx) {
          padding: 16px;
        }
      }

      &-bottom {
        padding: 14px 20px 26px;

        @include media-breakpoint-down(tbx) {
          padding: 14px 15px 16px;
        }

        @include media-breakpoint-down(phx) {
          padding: 0 16px 15px;
        }
      }
    }

    .video__title {
      @include media-breakpoint-down(phx) {
        margin-bottom: 2px;
      }
    }

    .video__text {
      @include media-breakpoint-down(phx) {
        margin-bottom: 5px;
      }
    }
  }

  &_review-sm {
    .video__content {
      height: 207px;

      @include media-breakpoint-down(tbx) {
        height: 220px;
      }
      @include media-breakpoint-down(phx) {
        height: 280px;
      }
      @include media-breakpoint-down(ph) {
        height: 100%;
      }

      &-top {
        padding-top: 11px;
      }

      &-bottom {
        padding-bottom: 20px;
      }
    }

    .video__name {
      &_link {
        font-size: 18px;
      }
    }

    .video__text {
      margin-bottom: 4px;
      font-size: 14px;

      @include media-breakpoint-down(tbx) {
        margin-bottom: 8px;
      }
    }

    .video__title {
      font-size: 18px;

      @include media-breakpoint-down(tbx) {
        font-size: 16px;
      }
    }

    .video__play {
      &::before {
        background-size: 65px;
      }
    }
  }
  
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #000;
      opacity: .65;
    }
  }
  
  &__content {
    position: relative;
    height: 220px;
    
    &-top,
    &-bottom {
      position: absolute;
    }
    
    &-top {
      top: 0;
      padding: 22px 24px;

      @include media-breakpoint-down($mobile-switch){
        padding: 12px;
      }
    }
    
    &-bottom {
      bottom: 0;
      padding: 14px 24px;
      
      @include media-breakpoint-down(tbx){
        padding: 20px 24px;
      }

      @include media-breakpoint-down($mobile-switch){
        padding: 12px;
      }
    }
  }
  
  &__group,
  &__time {
    font-size: 14px;
    letter-spacing: 0.4px;

    @include media-breakpoint-down(tbx) {
      font-size: 12px;
      letter-spacing: 0.48px;
    }
    
    @include media-breakpoint-down(tb){
      font-size: 14px;
      letter-spacing: 0.4px;
    }
  }
  
  &__group {
    color: #fff;
    opacity: 0.5;
    text-transform: uppercase;
    
    + .video__time {
      margin-top: 6px;
    }
  }
  
  &__time {
    color: $color__primary;
    
    &_top {
      display: none;
      @include media-breakpoint-down($mobile-switch){
        display: block;
      }
    }
    
    &_bottom {
      @include media-breakpoint-down($mobile-switch){
        display: none;
      }
    }
  }
  
  &__name {
    color: #fff;
    font-size: 24px;
    letter-spacing: 0.32px;

    @include media-breakpoint-down(tbx) {
      font-size: 22px;
      letter-spacing: 0.36px;
    }

    @include media-breakpoint-down($mobile-switch) {
      font-size: 18px;
      letter-spacing: 0.4px;
    }
    
    + .video__time {
      margin-top: 10px;
    }

    &_link {
      font-size: 22px;
      position: relative;
      z-index: 1;
      display: inline-block;
      line-height: 1.25;
      letter-spacing: 0.36px;
      transition: none;

      @include media-breakpoint-down(tbx) {
        font-size: 18px;
        letter-spacing: 0.36px;
      }

      > span {
        transition: all .25s;
      }

      &:hover {
        > span {
          color: $color__primary;
        }
      }
    }
  }

  &__text {
    margin-bottom: 8px;
    font-size: 16px;
    letter-spacing: 0.4px;
    color: #fff;
    //opacity: .5;

    @include media-breakpoint-down(tbx) {
      font-size: 12px;
      letter-spacing: 0.48px;
    }
  }

  &__title {
    margin-bottom: 5px;
    font-size: 20px;
    letter-spacing: 0.36px;
    font-weight: normal;
    color: #fff;

    @include media-breakpoint-down(tbx) {
      margin-bottom: 3px;
      font-size: 14px;
      letter-spacing: 0.4px;
    }
  }
  
  &__play {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &::before,
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-size: auto;

      @include media-breakpoint-down(phx) {
        background-size: 68px;
      }
    }
    &::before {
      background: url(../img/youtube.svg) no-repeat center;
      opacity: 0.4;
      @include media-breakpoint-down(phx) {
        background-size: 68px;
      }
    }
    &::after {
      background: url(../img/youtube-active.svg) no-repeat center;
      opacity: 0;
      transition: opacity .25s ease-out;
      z-index: 1;
    }

    &-icon {
      font-size: 20px;
    }
  }
  
}

.videoobzor {
  background: $color__secondary;
  margin: 0 (-$container-padding-tbx) 44px;
  padding: 22px 0 0;

  @include media-breakpoint-down(phx){
    margin: 0 (-$container-padding-phx) 44px;
  }

  &__name {
    text-align: center;
    font-size: 20px;
    letter-spacing: 0.36px;
    margin-bottom: 15px;

    a {
      padding-right: 0;
      text-transform: none;
    }

    .dot-link-one-line__arrow {
      top: 9px;
      right: -14px;
    }
  }
  &__gallery {
    background: #fff;

    .video-gallery {
      margin: 0;
    }
  }

  .swiper-button-prev-kronos,
  .swiper-button-next-kronos {
    //bottom: 16px;
    background: transparent;
  }

  .swiper-button-prev-kronos {
    left: 31px;
  }

  .swiper-button-next-kronos {
    right: 31px;
  }
}