.search-page {
    .catalog__name,
    .catalog__filter {
        display: none;
    }
    &__search-block {
        width: 100%;
        padding: 20px 35px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: 1px solid $color__text2;
        margin-bottom: 72px;
        @include medium {
            flex-direction: column;
            margin-bottom: 63px;
        }
    }
    &__first-block,
    &__second-block {
        flex: 1 1 auto;
        display: flex;
        justify-content: space-between;
        @include medium {
            flex: none;
            width: 100%;
            justify-content: stretch;
        }
    }
    &__description {
        @include medium {
            width: 100%;
            display: flex;
        }
    }
    &__dotted {
        border-bottom: 1px dotted $color__additional;
    }
    &__first-block {
        margin-right: 80px;
        flex: 10 1 auto;
        @include medium {
            flex: none;
            margin-right: 0;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 15px;
        }
    }
    &__input {
        flex: 2 1 auto;
        margin-right: 80px;
        @include medium {
            margin-right: 40px;
        }
    }
    &__main-input:focus + img {
        display: none;
    }
    &__search-img {
        top: 50%;
        left: 13px;
        transform: translateY(-50%);
        position: absolute;
        display: block;
    }
    &__margin-bottom-125 {
        margin-bottom: 125px;
    }
    &__line-second {
        display: flex;
        img {
            margin-right: 15px;
        }
    }
    &__flex-line {
        display: flex;
        justify-content: space-between;
    }
    &__work-time {
        font-size: 15px;
    }
    &__points {
        border-left: 2px dotted $color__additional;
        margin-left: 25px;
        margin-right: 25px;
    }
    &__line {
        line-height: 1.5em;
        letter-spacing: .36px;
    }
    &__padding-top_55p {
        padding-bottom: 55px;
        @include medium {
            padding-bottom: 0;
        }
    }
    &__hide-medium {
        @include medium {
            display: none;
        }
    }
    &__show-f-medium {
        display: none;
        @include medium {
            display: flex;
        }
    }
    &__hide-small {
        @include small {
            display: none;
        }
    }
    &__show-small {
        display: none;
        @include small {
            display: block;
        }
    }
}