.swiper-button-next-2,
.swiper-button-prev-2 {
  position: absolute;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background: $color__additional;
  cursor: pointer;
  top: 50%;
  outline: none;
  transition: 0.2s;
  z-index: 1;

  @include media-breakpoint-down(tbx) {
    width: 40px;
    height: 40px;
  }

  @include media-breakpoint-down(tb){
    border-radius: 50%;
    border: 1px solid $color__additional;
    width: 24px;
    height: 24px;
    background: #fff;

    svg {
      display: none;
    }

    &:after {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 9px;
      height: 9px;
      margin-top: -5px;
      margin-left: -5px;
      border-top: 1px solid $color__additional;
      border-right: 1px solid $color__additional;
      content: "";
    }
  }

  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -6.5px;
    margin-left: -4px;
    width: 8px;
    height: 13px;
    fill: #000;
    stroke: none;
    transition: 0.2s;

    .swiper-container-vertical-kronos &,
    .swiper-container-vertical & {
      width: 12px;
      height: 8px;
      margin-top: -4px;
      margin-left: -6px;
    }
  }
  
  svg {
    fill: #fff;
    width: 20px;
    height: 25px;
    margin-top: -11px;

    @include media-breakpoint-down(tbx) {
      width: 18px;
      height: 23px;
    }
  }
  
  &:hover {
    background: $color__primary;
    @include media-breakpoint-down(tb){
      background: #fff;
    }
  }

  &.point-animation {
    svg {
      @extend %point-animation;
    }

    &.swiper-button-prev-2 {
      svg {
        animation-direction: reverse;
      }
    }

    .swiper-container-vertical-kronos &,
    .swiper-container-vertical & {
      svg {
        animation-name: point-keyframes-vertical;
        animation-direction: normal;
      }

      &.swiper-button-next-2 {
        svg {
          animation-direction: reverse;
        }
      }
    }
  }
  
  &.swiper-button-disabled {
    visibility: hidden;
  }
  
}

.swiper-button-next-2 {
  right: 0;
  margin-top: -27.5px;

  @include media-breakpoint-down(tbx) {
    margin-top: -20px;
  }

  @include media-breakpoint-down(tb){
    top: auto;
    right: 16px;
    bottom: -3px;

    &:after {
      margin-left: -7px;
      transform: rotateZ(45deg);
    }
  }

  svg {
    margin-left: -8px;
  }
}

.swiper-button-prev-2 {
  left: 0;
  margin-top: -27.5px;

  @include media-breakpoint-down(tbx) {
    margin-top: -20px;
  }

  @include media-breakpoint-down(tb){
    right: auto;
    top: auto;
    bottom: -3px;
    left: 16px;

    &:after {
      margin-left: -3px;
      transform: rotateZ(-135deg);
    }
  }

  svg {
    margin-left: -11px;
  }
}


.swiper-pagination-kronos {
  text-align: center;
  font-size: 16px;
  letter-spacing: 0.4px;
  
  span {
    color: $color__primary;
  }

  .swiper-pagination-total {
    color: $color__text;
  }
  
  .js-swiper-root & {
    margin-top: 26px;
  }

  .js-swiper-video & {
    bottom: 7px;

    @include media-breakpoint-down(phx) {
      bottom: 9px;
    }
  }

  &.swiper-pagination-custom {
    @include media-breakpoint-down(phx) {
      padding-bottom: 4px;

      .views-products__gallery & {
        margin-bottom: 0;
        position: relative;
        bottom: 0;
      }
    }
  }
}

.swiper-button-prev-kronos,
.swiper-button-next-kronos {
  position: absolute;
  width: 36px;
  height: 40px;
  cursor: pointer;
  background: #fff;
  z-index: 12;
  outline: none;
  user-select: none;
  transition: 0.2s;

  .no-pagination & {
    display: none;
  }

  @include media-breakpoint-down($mobile-switch) {

    &:not(.swiper-button-prev-kronos_popup),
    &:not(.swiper-button-next-kronos_popup) {
      border-radius: 50%;
      border: 1px solid $color__additional;
      width: 24px;
      height: 24px;

      svg {
        display: none;
      }

      &:after {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 9px;
        height: 9px;
        margin-top: -5px;
        margin-left: -5px;
        border-top: 1px solid $color__additional;
        border-right: 1px solid $color__additional;
        content: "";
      }

      &.swiper-button-disabled {
        visibility: hidden;
      }
    }
  }
  
  //&:after {
  //  position: absolute;
  //  top: 50%;
  //  left: 50%;
  //  transform: translateX(-50%) translateY(-50%);
  //  font-family: "FontAwesome";
  //  font-size: 14px;
  //  color: #000;
  //}
  
  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -6.5px;
    margin-left: -4px;
    width: 8px;
    height: 13px;
    fill: #000;
    stroke: none;
    transition: 0.2s;

    .swiper-container-vertical-kronos &,
    .swiper-container-vertical & {
      width: 12px;
      height: 8px;
      margin-top: -4px;
      margin-left: -6px;
    }
  }

  &.point-animation {
    svg {
      @extend %point-animation;
    }
    
    &.swiper-button-prev-kronos {
      svg {
        animation-direction: reverse;
      }
    }

    .swiper-container-vertical-kronos &,
    .swiper-container-vertical & {
      svg {
        animation-name: point-keyframes-vertical;
        animation-direction: normal;
      }

      &.swiper-button-next-kronos {
        svg {
          animation-direction: reverse;
        }
      }
    }
  }
  
  &.swiper-button-disabled {
    cursor: default;
    
    svg {
      fill: $color__additional
    }
    
    //&:after {
    //  color: $color__additional;
    //}
  }
  
  &:not(.swiper-button-disabled):hover {
    //background: $color__primary;
    svg {
      fill: $color__primary;
    }
  }

  &_mob {
    border-radius: 50%;
    border: 1px solid $color__additional;
    width: 24px;
    height: 24px;

    &.swiper-button-disabled {
      display: none;
    }

    svg {
      display: none;
    }

    &:before {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: transparent;
      content: "";
    }
    
    &:after {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 10px;
      height: 10px;
      margin-top: -5px;
      margin-left: -5px;
      border-top: 1px solid $color__additional;
      border-right: 1px solid $color__additional;
      content: "";
    }
  }

  &_gray {
    background: $color__secondary
  }

  &_popup {
    background: #000;
    width: 45px;
    height: 45px;
    border-radius: 50%;

    @include media-breakpoint-down(tbx) {
      width: 40px;
      height: 40px;
    }
    
    svg {
      fill: #fff;
      width: 20px;
      height: 25px;
      margin-top: -11px;

      @include media-breakpoint-down(tbx) {
        width: 18px;
        height: 23px;
      }
    }
  }

  &.swiper-button-prev-kronos_video,
  &.swiper-button-next-kronos_video {
    @include media-breakpoint-down(tbx) {
      width: 36px;
      height: 40px;
      border: none;
      border-radius: 0;

      &::after {
        display: none;
      }

      svg {
        display: block;
      }
    }

    @include media-breakpoint-down(phx) {
      &.swiper-button-disabled {
        opacity: 0;
      }
    }

    @include media-breakpoint-down(phx) {
      width: 24px;
      height: 24px;
      border-radius: 50%;
      border: 1px solid $color__additional;
      z-index: 12;

      svg {
        display: none;
      }

      &::after {
        display: block;
      }
    }
  }
}

