.banner {
  position: relative;
  
  &__slider {
    
    .swiper-slide {
      overflow: hidden;

      &.swiper-slide-active {

        
      }
      
      &.slide-enter-animate {
        
        .banner-slide__name,
        .banner-slide__desc {
          transform: translateY(0);
          
          &-text {
            transform: translateY(0);
          }
        }

        .banner-slide__mask {
          transition: transform 0.7s, opacity 0.35s;
          transform: translate3d(0, 0, 0);
          opacity: 1;

          &-inner {
            transition: transform 0.7s;
            transform: translateX(0);
          }
        }
        
      }
      
      &.slide-leave-animate {

        .banner-slide__name,
        .banner-slide__desc {
          transform: translateY(105%);
          
          &-text {
            transform: translateY(-105%);
          }
        }

        .banner-slide__mask {
          transition: transform 0.7s, opacity 0.35s;
          transform: rotate(10deg) translate3d(-200px, 0, 0);
          opacity: 0;

          &-inner {
            transition: transform 0.7s;
            transform: translateY(0) rotate(-10deg) translateX(200px) translateZ(0);
          }
        }
        
      }
      
    }

    @include media-breakpoint-down(tb) {
      &.js-swiper {

        opacity: 0;
        transition: opacity .2s;

        .banner-pagination {
          &.banner-pagination_init {
            > div {
              opacity: 1;
              transform: translateX(0);
              outline: none;
            }
          }

          > div {
            opacity: 0;
            transform: translateX(-10px);
          }
        }

        &.slider_init {
          opacity: 1;
        }
        
      }
      
    }

  }
}

@for $i from 1 through 8 {
  .banner__slider .banner-pagination > div:nth-child(#{$i}) {
    transition: .4s 0.2s*$i;
  }
}


.banner-slide {
  
  &__mask {
    overflow: hidden;
    z-index: 1;
    position: absolute;

    left: 35%;
    height: 50%;
    top: 15%;
    width: 10%;
    
    clip-path: polygon(
        0% 0%, 
        0 100%, 
        20px 100%, 
        20px 20px, 
        calc(100% - 20px) 20px, 
        calc(100% - 20px) calc(100% - 20px), 
        20px calc(100% - 20px), 
        20px 100%, 
        100% 100%, 
        100% 0
    );
    
    transform-origin: 50% 0;
    transition-timing-function: ease-in-out;
    transform: rotate(-10deg) translate3d(200px, 0, 0);
    opacity: 0;
    
    @include media-breakpoint-down(dt){
      left: 40%;
    }


    &-inner {
      position: absolute;
      background-position: center;
      background-repeat: no-repeat;
      background-blend-mode: luminosity;
      width: 100vw;
      height: 100vh;
      top: 50%;
      left: 50%;
      margin-left: -50vw;
      margin-top: -50vh;
      transition: transform 1s ease-in-out;
      transform: translateY(0) rotate(10deg) translateX(-200px) translateZ(0)
    }
  }
  
  &__desktop,
  &__tablet,
  &__mobile {
    background-position: right top;
    background-repeat: no-repeat;
    background-size: contain;
  }
  
  &__desktop {
    position: relative;

    @include media-breakpoint-down(tbx){
      display: none;
    }
  }

  &__tablet {
    display: none;
    margin-left: auto;

    img {
      @extend .img-responsive;
    }

    @include media-breakpoint-down(tbx){
      display: block;
    }

    @include media-breakpoint-down(tb){
      display: none;
    }
  }

  &__mobile {
    display: none;
    transition: 1.5s ease;

    .swiper-slide-active & {
      opacity: 0.1;
      filter: grayscale(1);
    }

    img {
      @extend .img-responsive;
      margin: 0 auto;
    }

    @include media-breakpoint-down(tb){
      display: block;
      margin-bottom: 100px;
    }

  }
  
  &__inner {
    position: absolute;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    padding: 0 60px;

    @include media-breakpoint-down(tbx){
      padding: 0 48px;
    }
    
    @include media-breakpoint-down($mobile-switch){
      left: 0;
      right: 0;
      padding: 0 16px;
      justify-content: center;
      text-align: center;
    }
  }
  
  &__info {
    padding-bottom: 65px;
    user-select: none;
    @include small {
      padding-bottom: 90px;
    }
  }
  
  &__name,
  &__desc {
    overflow: hidden;
    transition: 1s;
    transform: translateY(-105%);
    
    &-text {
      transition: 1s;
      transform: translateY(105%);
    }
  }
  
  &__name {
    font-size: 64px;
    text-transform: uppercase;

    @include media-breakpoint-down(lt){
      font-size: 32px;
    }
    
    + .banner-slide__desc {
      margin-top: 8px;

      @include media-breakpoint-down(tbx){
        margin-top: 20px;
      }
    }
  }
  
  &__desc {
    font-size: 22px;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(lt){
      font-size: 18px;
    }

    + .banner-slide__button {
      margin-top: 36px;

      
      @include media-breakpoint-down(tb){
        margin-top: 26px;
      }
    }
  }
  
  &__button {
    
    &-text {
      display: inline-block;
    }
    
    a {
      letter-spacing: 0.36px;
      padding: 9px 18px 13px;

      @include media-breakpoint-down(phx){
        padding: 5px 14px 9px;
      }
    }
    
  }
  
}


.banner-pagination-wrapper {
  position: absolute;
  bottom: 50px;
  left: 0;
  right: 0;
  z-index: 1;

  
  @include media-breakpoint-down(tb){
    bottom: 34px;
  }
}

.banner-pagination {
  padding: 0 60px;

  @include media-breakpoint-down(tbx){
    padding: 0 48px;
  }
  
  @include media-breakpoint-down(tb){
    padding: 0;
    text-align: center;
  }

  .banner-pagination-bullet-desktop {
    @include media-breakpoint-down(tb){
      display: none;
    }
  }
  
  .banner-pagination-bullet-mobile {
    display: none;
    @include media-breakpoint-down(tb){
      display: block;
    }
  }
  
  .banner-pagination-bullet {
    display: inline-block;
    
    svg {
      margin-right: 10px;
      cursor: pointer;
      transform: rotateZ(-90deg);

      @include media-breakpoint-down(tb){
        margin: 0 12px;
      }

      circle {
        stroke-width: 2px;
        stroke: $color__additional;
        fill: none;

        @include media-breakpoint-down(tb){
          stroke-width: 3px;

        }
      }
      
    }
    
    &.banner-pagination-bullet-active {
      svg {
        circle {

          &.banner-pagination-bullet-normal {
            stroke: transparent;
          }

          &.banner-pagination-bullet-progress {
            stroke: $color__primary;
          }

          @include small {
            &.banner-pagination-bullet-normal {
              stroke: transparent;
            }
          }

        }
      }
    }
    
  }
  
}