.menu-panel {
  ul {
    @extend %list;
    display: flex;
    
    li {
      flex-shrink: 1;
      flex-grow: 1;
      width: 50%;
      margin-right: 1px;
      a {
        position: relative;
        display: block;
        background: $color__secondary;
        font-size: 16px;
        letter-spacing: 0.4px;
        text-align: center;
        padding: 12px;
        
        svg {
          position: relative;
          display: inline-block;
          margin-right: 10px;
          fill: #000;
          stroke: none;
          transition: 0.2s;
        }
        
        &:hover {
          svg {
            fill: $color__primary;
          }
        }
      }
    }
  }
  
  &__filter {
    svg {
      width: 14px;
      height: 13px;
    }
  }
  &__share {
    svg {
      width: 11px;
      height: 13px;
    }
  }
}