.views-products-vertical {
  &__name {
    font-size: 26px;
    letter-spacing: 0.32px;
    margin-bottom: 16px;
    padding-left: 20px;

    @include media-breakpoint-down(dtx){
      font-size: 24px;
    }

    @include media-breakpoint-down(tbx){
      font-size: 20px;
      padding-left: 22px;
      letter-spacing: 0.36px;
      margin-bottom: 18px;
    }
  }
  
  &__gallery {
    border: 1px solid $color__text2;

    .swiper-button-prev-kronos,
    .swiper-button-next-kronos {
      top: -1px;
    }
    
    .swiper-button-prev-kronos {
      border-bottom: 1px solid $color__text2;
      border-left: 1px solid $color__text2;
      right: 35px;
    }

    .swiper-button-next-kronos {
      border-bottom: 1px solid $color__text2;
      right: -1px;
    }
  }
  
  &__swiper {
    position: relative;
    height: 753px;

    &:after {
      position: absolute;
      top: 0;
      left: 0;
      height: 1px;
      background: #fff;
      width: 100%;
      content: "";
      z-index: 1;
    }

    @include media-breakpoint-down(tbx){
      height: 557px;
    }
    
  }
  
  .swiper-slide {
    position: relative;

    &:after {
      position: absolute;
      bottom: -1px;
      left: 0;
      height: 1px;
      background: $color__text2;
      width: 100%;
      content: "";
    }
    
    //border-bottom: 1px solid $color__text2;
    //@include media-breakpoint-down(tbx) {
    //}
  }
}

.views-product-vertical {
  display: block;
  padding: 9px 18px 9px 20px;

  @include media-breakpoint-down(tbx){
    padding: 14px 12px 14px 12px;
  }
  
  
  

  &__img {
    //background: rgba(0, 255, 0, 0.15);
    img {
    }
  }
  
  &__name {
    font-size: 21px;
    letter-spacing: 0.28px;
    margin-bottom: 10px;
    height: ((21px * 1.3) * 2 + 10);
    overflow: hidden;

    @include media-breakpoint-down(dtx){
      font-size: 18px;
      height: ((18px * 1.3) * 2);
    }

    @include media-breakpoint-down(tbx){
      font-size: 16px;
      height: ((16px * 1.3) * 2);
      
      .swiper-slide-active & {
        width: 100px;
        text-overflow: ellipsis;
        span {
          display: none;
          &:first-child {
            display: inline-block;
          }
        }
      }
    }
  }
  
  &:hover,
  &:focus {
    .views-product__name {
      
    }
  }
}