* {
  box-sizing: border-box;
}

body {
  font-size: 18px;
  font-family: "Lato", Arial, sans-serif;
  line-height: normal;
  background: #fff;
  color: #000;
  min-width: 320px;
}

.page {
  position: relative;
  //z-index: 100;
  //transition: all .3s;
  overflow: hidden;
}

.img-responsive {
  display: block;
  height: auto;
  width: auto;
  max-height: 100%;
  max-width: 100%;
}

a {
  color: #000;
  text-decoration: none;
  transition: color 0.2s;
  
  &:hover {
    color: $color__primary;
  }
}

.dot-link-lines {
  position: relative;
  display: inline-block;
  line-height: 1.25;
  transition: none;
  cursor: pointer;

  &::before,
  &::after {
    content: '';
    display: block;
    position: absolute;
    right: 0;
    width: 0;
    transition: width .3s;
  }
  &::before {
    top: 0;
    bottom: 0;
    background: linear-gradient(transparent 1.15em, #fff .2em);
    background-size: 1em 1.35em;
  }
  &:after {
    bottom: -2px;
    height: 2px;
    background: #fff;
  }

  &:hover {
    &::before,
    &::after {
      width: 100%;
    }

    .dot-link-lines__name {
      color: $color__primary;
      border-bottom-color: $color__primary;
    }
  }

  &__name {
    letter-spacing: 0.36px;
    border-bottom: 1px dotted $color__additional;
    transition: all .25s;
  }

  &__mob {
    display: none;
    color: $color__additional;
    @include media-breakpoint-down($mobile-switch){
      display: inline;
    }
  }
}
  
.dot-link-one-line {
  cursor: pointer;

  &_wrap-mobile {
    .dot-link-one-line__name {
      @include media-breakpoint-down($mobile-switch){
        white-space: normal;
      }
    }
  }
  &__mob {
    display: none;
    color: $color__additional;
    @include media-breakpoint-down($mobile-switch){
      display: inline;
    }
  }
  &__name {
    position: relative;
    letter-spacing: 0.36px;
    white-space: nowrap;
    border-bottom: none;

    @include media-breakpoint-down(tb) {
       border-bottom: 1px dotted $color__additional;

      &::after {
         display: none;
      }
    }
    @include media-breakpoint-down(phx) {
      white-space: normal;
    }

    &:hover {
      &::after {
        width: 0;
      }
    }

    &:after {
      position: absolute;
      bottom: -2px;
      left: 0;
      width: 100%;
      height: 1px;
      transition: 0.25s;
      background-image: repeating-linear-gradient(to right, $color__additional 0, $color__additional 1px, transparent 1px, transparent 2px);
      content: "";
    }
  }
  
  &__count {
    position: relative;
    top: -8px;
    padding-left: 4px;
    font-size: 12px;
    font-weight: 500;
    color: $color__additional;
    letter-spacing: 0.24px;
    white-space: nowrap;
    overflow: hidden;
    display: inline-block;

    &.anim-destination {
      
      span {
        position: relative;
        display: inline-block;
        transition: transform 0.3s;

        &:before {
          position: absolute;
          white-space: nowrap;
          top: 100%;
          color: $color__primary;
          content: attr(data-hover);
        }
      }
      
    }

    @include media-breakpoint-down(lt){
      top: -2px;
      padding-left: 5px;
    }
  }
  
  &:focus,
  &:hover {
    .dot-link-one-line__name {
      border-color: $color__primary;
      //&:after {
      //  opacity: 0;
      //}
      //&:before {
      //  opacity: 1;
      //}
    }

    .dot-link-one-line__arrow {
      fill: $color__primary;
    }

    .dot-link-one-line__count {
      &.anim-destination {
        span {
          transform: translateY(-100%);
        }
      }
    }
  }
  
  &__arrow {
    position: absolute;
    top: 6px;
    right: 0;
    width: 6px;
    height: 11px;
    fill: $color__additional;
    stroke: none;
    transition: 0.2s;
  }
  
  &_arrow {
    position: relative;
    text-transform: uppercase;
    padding-right: 14px;

    &.point-animation {
      .dot-link-one-line__arrow {
        @extend %point-animation;
      }
    }
  }
  
}

.dot-link-multi-line {
  &__name > * {
    position: relative;
    letter-spacing: 0.36px;
    white-space: nowrap;

    @include media-breakpoint-down(tb) {
      border-bottom: 1px dotted $color__additional;

      &::after {
        display: none;
      }
    }
    @include media-breakpoint-down(phx) {
      white-space: normal;
    }
    
    //&:before,
    &:after {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      transition: 0.25s;
      content: "";
    }

    &:after {
      background-image: repeating-linear-gradient(to right, $color__additional 0, $color__additional 1px, transparent 1px, transparent 2px);
    }

    //&:before {
    //  background-image: repeating-linear-gradient(to right, $color__primary 0, $color__primary 1px, transparent 1px, transparent 2px);
    //  opacity: 0;
    //}
  }
  &__name:hover > * {
    &::after {
      width: 0;
    }
  }
}

.phone-link {
  &__name {
    font-size: 22px;
    position: relative;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(lt){
      font-size: 16px;
      letter-spacing: 0.4px;
    }
    
  }
  &__text {
    color: $color__primary;
  }
}

.svg-assets {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.content {
  padding: 26px 0 0;

  @include media-breakpoint-down(lt){
    padding: 34px 0 0;
  }

  @include media-breakpoint-down($mobile-switch){
    padding: 26px 0 0;
  }
}

.content-head {
  background: $color__secondary;
  
  @include media-breakpoint-down(tbx){
    padding-top: 100px;
  }
  
  @include media-breakpoint-down(tb){
    padding-top: 0;
  }
  
  &__row {
    display: flex;
    align-items: flex-end;

    &-video {
      @include media-breakpoint-down(lt){
        .content-head__col {
          width: 100%;
        }
      }
    }
  }
  
  &__col {
    position: relative;
    width: 50%;
    
    &_100 {
      width: 100%;
    }
  }
  
  &__triggers {
    &-name {
      padding: 20px 20px 20px 0;

      @include media-breakpoint-down(lt) {
        padding: 16px 10px;
        font-size: 14px;
        
        .dot-link-one-line__arrow {
          top: 3px;
        }
      }
    }
    &-content {
    }
  }
  
  &__video {
    &-name {
      padding: 20px 20px 20px 0;

      @include media-breakpoint-down(lt) {
        padding: 16px 10px;
        font-size: 14px;

        .dot-link-one-line__arrow {
          top: 3px;
        }
      }
    }
  }
  
  &_banners {
    position: relative;
    margin-bottom: 60px;

    > .container {
      position: relative;
    }

    &:before {
      position: absolute;
      background: #fff;
      left: 0;
      width: 100%;
      top: 280px;
      bottom: 0;
      content: "";
      
      @include media-breakpoint-down(lt) {
        top: 268px;
      }
    }
  }
  .swiper-button-prev-kronos,
  .swiper-button-next-kronos {
    background-color: $color__secondary;
  }
}

.border-dot-list {
  @extend %list;
  display: flex;
  
  li {
    padding-right: 24px;
    line-height: 1.2;

    @include media-breakpoint-down(lt){
      padding-right: 22px;
    }

    @include media-breakpoint-down($mobile-switch){
      padding-right: 14px;
    }
    
    &:last-child {
      padding-right: 0;
    }
    
    + li {
      padding-left: 24px;
      position: relative;

      @include media-breakpoint-down(lt){
        padding-left: 22px;
      }

      @include media-breakpoint-down($mobile-switch){
        padding-left: 14px;
      }
      
      &:before {
        position: absolute;
        background-image: repeating-linear-gradient(to bottom, $color__additional 0px, $color__additional 1px, transparent 1px, transparent 2px);
        width: 1px;
        height: 100%;
        top: 0;
        left: 0;
        content: "";
      }
    }
  }
}

.status {
  position: relative;
  font-size: 17px;
  letter-spacing: 0.4px;
  padding-left: 24px;

  @include media-breakpoint-down(dtx){
    font-size: 15px;
  }

  @include media-breakpoint-down(lt){
    font-size: 13px;
    padding-left: 20px;
  }

  @include media-breakpoint-down($mobile-switch){
    font-size: 14px;
  }
  
  &:before {
    position: absolute;
    top: 3px;
    left: 0;
    width: 12px;
    height: 12px;
    background: #339900;
    //background: $color__text3;
    border-radius: 50%;
    content: "";

    @include media-breakpoint-down(dtx){
      top: 3px;
    }

    @include media-breakpoint-down(lt){
      top: 1px;
    }
  }
  
  &_red {
    &:before {
      background: $color1;
    }
  }
}

.props {
  
  &__table {
    display: table;
    border-collapse: collapse;
    width: 100%;
  }
  
  &__tr {
    display: table-row;
    border-bottom: 1px solid $color__text2;
    &:last-child {
      border-bottom: 0;
    }
  }
  
  &__td {
    display: table-cell;
    vertical-align: middle;
    font-size: 16px;
    letter-spacing: 0.4px;
    padding: 15px 10px;
    
    @include media-breakpoint-down($mobile-switch){
      padding: 10px;
    }
    
    img {
      margin-right: 12px;
      
      @include media-breakpoint-down(tbx){
        margin-right: 9px;
      }
    }
  }
  
  &_product-banner,
  &_product {
    margin-left: -1px;
    margin-right: -1px;
    
    .props {
      
      &__table {
        border-collapse: separate;
        border-spacing: 1px;
        table-layout: fixed;
      }
      
      &__tr {
        border: 0;
      }
      
      &__td {
        font-size: 18px;
        letter-spacing: 0.36px;
        padding: 4px 12px 6px;
        background: $color__secondary;

        @include media-breakpoint-down(dtx){
          font-size: 14px;
          letter-spacing: 0.4px;
        }
        
        @include media-breakpoint-down(tbx){
          padding: 6px 12px 5px;
          
          &:first-child {
            padding-right: 6px;
          }
          
        }
      }
      
    }
    
  }
  
  &_product-banner {
    margin-left: -1px;
    margin-right: -1px;
    
    .props {
      
      &__td {
        font-size: 17px;
        letter-spacing: 0.4px;
        padding: 7px 12px 3px;
        background: $color__secondary;

        @include media-breakpoint-down(dtx){
          font-size: 14px;
          letter-spacing: 0.4px;
        }
      }
      
    }
    
  }
  
}

.lupa-m {
  width: 18px;
  height: 18px;
  stroke: #fff;
  fill: none;
  transition: 0.2s;
}

.lupa {
  width: 27px;
  height: 27px;
  stroke: #fff;
  fill: none;
  transition: 0.2s;
}

.js-swiper-root {
  position: relative;
}

.price {
  font-size: 28px;
  color: $color__primary;
  letter-spacing: 0.32px;

  &_normal {
    color: #000;
  }
}

.price-old {
  font-size: 18px;
  letter-spacing: 0.36px;
  text-decoration: line-through;
  color: $color__additional;
  
  &_v2 {
    text-decoration: none;
    > span {
      text-decoration: line-through;
    }
  }
}

.aside-item {
  margin-bottom: 66px;
}

.bullet {
  display: inline-block;
  width: 7px;
  margin-right: 7px;
  margin-left: 2px;
  padding-bottom: 2px;

  &::before {
    content: '';
    display: block;
    width: 7px;
    height: 7px;
    background: currentColor;
    border-radius: 50%;
  }
}

.hidden-phx {
  @include media-breakpoint-down(phx) {
    display: none!important;
  }
}
.visible-phx {
  @include media-breakpoint-up(tb) {
    display: none!important;
  }
}
.visible-dtx {
  @include media-breakpoint-down(dt) {
    display: none!important;
  }
}

.hidden-tb {
  @include media-breakpoint-only(tb) {
    display: none!important;
  }
}
.hidden-up-tb {
  @include media-breakpoint-up(tb) {
    display: none!important;
  }
}
.hidden-down-tb {
  @include media-breakpoint-down(tb) {
    display: none!important;
  }
}
.visible-up-tb {
  @include media-breakpoint-down(phx) {
    display: none!important;
  }
}
.visible-down-tb {
  @include media-breakpoint-up(tbx) {
    display: none!important;
  }
}
.hidden-tbx {
  @include media-breakpoint-only(tbx) {
    display: none!important;
  }
}
.hidden-up-tbx {
  @include media-breakpoint-up(tbx) {
    display: none!important;
  }
}
.hidden-down-tbx {
  @include media-breakpoint-down(tbx) {
    display: none!important;
  }
}
.visible-up-tbx {
  @include media-breakpoint-down(tb) {
    display: none!important;
  }
}
.visible-down-tbx {
  @include media-breakpoint-up(lt) {
    display: none!important;
  }
}


.text-light {
  color: $color__additional;
}
.text-center {
  text-align: center;
}

.flex-column-reverse_tb {
  @include media-breakpoint-down(tb) {
    flex-direction: column-reverse!important;
  }
}