.review-avatar {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
  //margin-top: 8px;
  background: #fff;

  &_empty {
    .review-avatar__pic {
      img {
        margin-left: 5px;

        @include media-breakpoint-down(tbx) { // change
          width: 60px;
          margin-left: 0;
        }
      }
    }
  }

  &__inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid $color__text2;
  }

  &__pic {
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: center;

    img {
      max-width: 100%;
    }
  }

  &__descr {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 44px;
    flex-shrink: 0;
    font-size: 20px;
    letter-spacing: 0.36px;
    border-top: 1px solid $color__text2;

    @include media-breakpoint-down(tbx) { // change
      height: 22px;
      font-size: 12px;
      letter-spacing: 0.48px;
    }
    @include media-breakpoint-down(phx) {
      height: 18px;
    }

    > span {
      display: inline-block;
      padding-bottom: 4px;
      padding-left: 8px;

      @include media-breakpoint-down(tbx) { // change
        padding-bottom: 0;
        padding-left: 0;
      }
    }
  }
}