.tooltipster-sidetip  {
  
  &.tooltipster-kronos {
    max-width: 383px;
    
    .tooltipster-box {
      background: black;
      border: 0;
      border-radius: 0;
      box-shadow: none;
    }

    .tooltipster-content {
      color: #fff;
      font-size: 16px;
      letter-spacing: 0.4px;
      line-height: 1.20;
      padding: 14px 14px 12px;
    }
    
    .tooltipster-arrow {
      left: 68px !important;
      bottom: -4px;
      height: 14px;
      margin-left: -14px;
      width: 28px;
      
      &-uncropped {}
      
      &-border {
        border-width: 14px;
      }
      
      &-background {
        border-width: 14px;
        border-top-color: black;
      }
      
    }
    
  }
  
}