.steps {
  display: flex;
  align-items: center;
  font-size: 20px;
  letter-spacing: 0.36px;

  &__text {
    margin-right: 10px;
  }

  &__title {
    margin-right: 17px;
    color: $color__additional;
  }
}

.steps-list {
  flex-shrink: 0;
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.step {
  padding: 0;
  margin: 0;
  font-size: 18px;
  line-height: 1;

  &_active {
    .step__num {
      color: #fff;
      border-color: $color__primary;
      background: $color__primary;
    }
  }

  + .step {
    margin-left: 20px;
  }

  &__num {
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border-radius: 50%;
    border: 2px solid $color__additional;
    color: $color__additional;
    font-weight: 800;
  }
}