.pagination-more {
  position: relative;
  margin-top: 66px;
  text-align: center;

  @include media-breakpoint-down(tb) {
    margin-top: 67px;
  }
  @include media-breakpoint-down(phx) {
    margin-top: 43px;
  }

  &::before {
    content: '';
    position: absolute;
    top: 29px;
    left: 0;
    right: 0;
    display: block;
    height: 1px;
    background: $color__text2;

    @include media-breakpoint-down(phx) {
      top: 8px;
    }
  }

  &__btn {
    position: relative;
    display: inline-block;
    font-size: 20px;
    background: #fff;
    padding: 0 55px;
    z-index: 2;
    cursor: pointer;

    @include media-breakpoint-down(phx) {
      padding: 0;
      background: none;

      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        margin: 0 auto;
        width: 70px;
        height: 30px;
        background: #fff;
      }
    }

    &:hover {
      color: $color__primary;

      .pagination-more__icon {
        border-color: $color__primary;
        &:before {
          border-color: $color__primary;
        }
      }
    }
  }

  &__icon {
    position: relative;
    width: 57px;
    height: 57px;
    margin: 0 auto 26px;
    border-radius: 50%;
    border: 2.43478px solid rgba(153,153,153,.5);
    transition: 0.2s;

    @include media-breakpoint-down(phx) {
      width: 30px;
      height: 30px;
      border-width: 2px;
      margin-bottom: 19px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 14px;
      left: 10px;
      display: block;
      width: 19px;
      height: 19px;
      border-left: 5px solid $color__additional;
      border-top: 5px solid $color__additional;
      transform: rotate(135deg);
      transition: 0.2s;

      @include media-breakpoint-down(phx) {
        width: 9px;
        height: 9px;
        top: 7px;
        left: 5px;
        border-top-width: 3px;
        border-left-width: 3px;
      }
    }
  }
}