.review-doc {
  position: relative;
  display: block;
  box-shadow: 0 0 11.5px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  z-index: 2;

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #000;
    opacity: .5;
  }

  img {
    width: 100%;
  }

  .lupa {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 48px;
    width: 48px;
    transform: translate(-50%, -50%);

    @include media-breakpoint-down(tbx) { // change
      width: 26px;
      height: 26px;
    }
  }
}

.popup-doc-bar {
  position: absolute;
  top: 16px;
  left: 50%;
  transform: translateX(-50%);
  min-width: 1020px;
  line-height: normal;
  color: #fff;

  @include media-breakpoint-down(tbx) {
    min-width: 900px;
  }
  @include media-breakpoint-down(tbx) { // change
    min-width: 670px;
    text-align: center;
  }
  @include media-breakpoint-down(tbx) { // change
    display: none;
  }

  &__info {
    display: none;

    .mfp-wrap & {
      display: block;
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin: 0 -20px;

    @include media-breakpoint-down(tbx) { // change
      flex-direction: column-reverse;
      align-items: center;
    }
  }

  &__item {
    max-width: 50%;
    padding: 0 20px;

    @include media-breakpoint-down(tbx) { // change
      max-width: initial;
    }
  }

  &__name {
    margin-bottom: 5px;
    font-size: 22px;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(tbx) { // change
      font-size: 16px;
    }
  }

  .address {
    color: #fff;
    font-size: 16px;
    letter-spacing: 0.4px;

    @include media-breakpoint-down(tbx) { // change
      display: block;
    }
  }

  .address__country {
    img {
      width: 16px;
    }
  }

  &__link {
    font-size: 18px;
    letter-spacing: 0.36px;
    color: #fff;

    @include media-breakpoint-down(tbx) { // change
      display: inline-block;
      margin-bottom: 9px;
      font-size: 20px;
    }

    span {
      //border-bottom: 1px dotted currentColor;
    }
  }
}