.root-catalog {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  
    @include media-breakpoint-down(tb){
      display: block;
    }
  
    &__product-name {
        max-width: 260px;

        @include media-breakpoint-down(tb) {
          max-width: none;
        }
      
        h3 {
            font: normal 20px Lato;
            letter-spacing: .36px;
            min-height: 2.5em;
            @include medium {
                font-size: 18px;
                min-height: 1em;
                margin-bottom: 25px;
            }
            @include media-breakpoint-down(tb) {
                font-size: 24px;
                letter-spacing: 0.32px;
                margin-bottom: 19px;
                text-align: center;
            }
        }
    }
    &__product-img {
        height: 190px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
      
        @include media-breakpoint-down(tbx){
          height: 108px;
          width: 192px;
        }
        @include media-breakpoint-down(tb){
          height: auto;
          width: auto;
        }
      
        img {
          @extend .img-responsive;
            @include media-breakpoint-down(tb) {
              margin: 0 auto;
            }
        }
    }
    &__product {
        display: block;
        width: 18%;
        padding: 100px 1% 0 1%;
      
        @include medium {
            flex: 1 0 0;
            min-width: 23%;
            max-width: 31%;
            padding-top: 50px;
        }
      
        @include media-breakpoint-down(tb) {
            min-width: 0;
            max-width: none;
            width: auto;
            padding: 16px 0 16px;
            &:not(:last-of-type) {
                border-bottom: 1px solid lightgray;
            }
        }
        li {
            font-size: 20px;
            letter-spacing: 0.36px;
            margin-bottom: 8px;
            list-style-type: none;
            position: relative;
            padding-left: 20px;
          
            &:before {
                display: block;
                content: '';
                position: absolute;
                width: 7px;
                height: 7px;
                border-radius: 50%;
                background-color: gray;
                top: 8px;
                left: -3px;

                @include media-breakpoint-down(tb){
                  width: 4px;
                  height: 4px;
                  left: 0;
                  top: 10px;
                }
            }
          
            @include medium {
                font-size: 16px;
            }
          
            @include media-breakpoint-down(tb){
              font-size: 20px;
              padding-left: 16px;
            }
          
            &:hover {
              &:before {
                background: $color__primary;
              }
            }
        }
    }
    &__flex-wrapper {
        width: 100%;
    }
    &__hover-wrapper {
        position: relative;
        display: inline-block;
    }
    &__hover {
        cursor: pointer;
    }
    &__popup {
        opacity: 0;
        position: absolute;
        left: 100%;
        top: 50%;
        z-index: -100;
        transition: .2s;
      
        &_left-tbx {
          @include media-breakpoint-down(tbx){
            right: 100%;
            left: auto;
  
            .root-catalog__popup-img {
              left: auto;
              right: 55%;
              transform: translate(50%, -50%);
            }
  
            .root-catalog__popup-background {
              transform: rotate(180deg);
            }
          }
        }
      
        @include media-breakpoint-down(tb) {
            display: none;
        }
    }
    &__hover:hover + &__popup {
        z-index: 100;
        opacity: 1;
        transform: translateY(-50%);
    }
    &__popup-background {
        height: 100%;
    }
    &__popup-img {
        position: absolute;
        height: 50%;
        top: 50%;
        left: 55%;
        transform: translate(-50%, -50%);
    }
    &__dotted {
        border-bottom: 2px dotted lightgray;
    }
    &__small-header {
        font-weight: 300;
    }
    @include medium() {
        &__hide-medium {
            display: none;
        }
    }
    @include small() {
        &__hide-small {
            display: none;
        }
    }
    &__inline-block {
        display: inline-block;
    }
}

.root-catalog-container,
.root-catalog-container2,
.root-catalog-container-mb0 {
    overflow: hidden;
    width: calc(1920px - 17px - 50px);
    margin: 0 auto 50px auto;
    @include medium {
        width: 100%;
        padding: 0 25px;
    }
    @include small {
        width: 100%;
        padding: 0 16px;
        margin-bottom: 25px;
    }
}

.root-catalog-container2 {
    overflow: visible;
}

.root-catalog-container-mb0 {
    margin: 0 auto;
}

.root-catalog-download {
    position: relative;
    min-height: 717px;
    overflow: visible;

    @include medium {
      .section-title.hidden-phx::before {
        display: none;
      }

      &__hide-medium {
        display: none;
      }
    }
    @include media-breakpoint-down(tb) {
      min-height: 674px;
    }
    @include small() {
      min-height: 0;
      &__hide-small {
        display: none;
      }
      &__header {
        display: block !important;
        font-size: 20px;
      }
      &__pdf-block_left::after {
        display: none;
      }
    }

    &__header {
      @include medium {
        margin-bottom: 52px;
      }
        @include media-breakpoint-down(phx) {
            margin-bottom: 14px;
        }
    }

    &__header-title {
        font-weight: normal;
        font-size: 32px;
        text-align: center;
        letter-spacing: 0.32px;
        margin: 0;

        @include media-breakpoint-down(phx) {
            font-size: 20px;
        }

        &_sm {
            font-weight: 300;
        }
    }

    &__background {
        position: absolute;
        top: -100px;
        bottom: 0;
        left: -42px;
        right: 0;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: auto;
        z-index: -1;
        img {
            width: 100%;
            @include small {
                min-width: 1000px;
            }
        }
        @include medium {
            left: 0;
        }
        @include media-breakpoint-down(tb) {
          top: 78px;
          background-size: 97%;
        }
        @include small {
            top: 50px;
            left: 10%;
            transform: translateX(-10%);
        }
    }
    &__flex {
        display: flex;
        width: 100%;
        @include medium {
            flex-direction: column-reverse;
        }
    }
    &__left {
        padding-top: 9px;
        width: 51.5%;
        @include medium {
            width: 100%;
        }
        @include small {
            display: none;
        }
    }
    &__right {
        padding-top: 76px;
        width: 48.5%;
        @include medium {
            padding-top: 0;
            width: 100%;
        }
    }
    &__watching-movie {
        display: flex;
        align-items: center;
        padding: 0 0 0 100px;
        img {
            margin-right: 20px;
        }
        @include medium {
            position: absolute;
            top: 282px;
            right: 100px;
            flex-direction: column;
            img {
                margin: 0 0 10px 0;
            }
        }
    }
    &__watching-movie-title {
        width: 145px;
        span {
            border-bottom: 2px dotted lightgray;
            font-family: Lato;
            font-size: 18px;
            letter-spacing: 0.36px;
        }
        @include medium {
            text-align: center;
        }
    }
    &__line1 {
        font-family: Lato;
        font-size: 24px;
        letter-spacing: 0.32px;
        margin-bottom: 13px;
    }
    &__line2 {
        display: flex;
        width: 100%;
        justify-content: space-between;
        margin-bottom: 53px;
    }
    &__block {
        flex: none;
        display: flex;
        align-items: center;
    }
    &__block_left {
        flex: none;
        font-family: Lato;
        font-size: 56px;
        letter-spacing: 0.32px;
        color: $color__primary;
        margin-right: 20px;
        line-height: 1em;
        padding-top: 4px;
        @include medium {
            font-size: 40px;
        }
    }
    &__block_right {
        flex: none;
        font-family: Lato;
        font-size: 18px;
        letter-spacing: 0.36px;
        @include medium {
            //font-size: 16px;
        }
    }
    &__line3 {
        display: flex;
        align-items: center;
        @include medium {
            margin-top: 308px;
            flex-direction: column-reverse;
            align-items: flex-end;
        }
        @include small {
            margin-top: 0;
            align-items: center;
        }
    }
    &__button {
        display: flex;
        border-radius: 5px;
        justify-content: center;
        align-items: center;
        background-color: #999900;
        color: white;
        height: 56px;
        padding: 0 20px;
        margin-right: 57px;
        font-size: 22px;
        letter-spacing: 0.36px;
        font-weight: normal;
        img {
            margin-right: 20px;
        }
        @include medium {
          padding: 0 29px;
            margin: 20px 0 50px 0;
        }
    }
    &__pdf-renew-wrapper {
        display: flex;
        @include small {
            margin-bottom: 150px;
        }
    }
    &__pdf-block {
        display: flex;
        margin-right: 23px;
        align-items: center;
        font-size: 24px;
        @include small {
            font-size: 12px;
            margin-right: 10px;
        }
    }
    &__pdf-block_left {
        position: relative;
        padding-right: 10px;
        margin-right: 10px;
        @include small {
            margin-right: 0;
            padding-right: 5px;
        }
    }
    &__pdf-block_left::after {
        position: absolute;
        right: -3px;
        top: 50%;
        transform: translateY(-50%);
        content: '';
        display: block;
        width: 5px;
        height: 5px;
        background-color: lightgray;
        border-radius: 50%;
    }
    &__pdf-block_right {
        position: relative;
        padding-right: 24px;
        border-right: 2px dotted lightgray;
        color: $color__primary;
        @include small {
            border: none;
            padding-right: 15px;
            &::after {
                position: absolute;
                content: '';
                display: block;
                top: 4px;
                right: 0;
                width: 5px;
                height: 5px;
                background-color: lightgray;
                border-radius: 50%;
            }
        }
    }
    &__renew-block {
        display: flex;
        align-items: center;
        @include small {
            font-size: 12px;
        }
    }
    &__orange {
        padding-left: 7px;
        color: $color__primary;
        @include medium {
            margin-right: 25px;
        }
        @include small {
            margin-right: 0;
        }
    }
    &__yutb-btn {
        margin: 0 auto 150px auto;
        @include small {
            margin-bottom: 100px;
        }
    }
    &__show-small {
        display: none;
        span {
            padding: 0;
        }
        @include small {
            display: block;
        }
    }
}

.root-catalog-small {
    display: none;
    padding: 0 16px 25px 43px;
    background: linear-gradient(white, $color__secondary 33%, $color__secondary);
    @include small {
        display: block;
    }
    &__description {
        position: relative;
        margin-bottom: 15px;
    }
    &__index {
        position: absolute;
        padding-top: 1px;
        display: flex;
        align-items: center;
        justify-content: center;
        left: -27px;
        top: 3px;
        width: 17px;
        height: 17px;
        border-radius: 50%;
        border: 1px solid black;
        font-size: 16px;
        font-weight: normal;
        line-height: 16px;
    }
    &__phone {
        min-width: 0;
        margin-bottom: 15px;
        label {
            display: none;
        }
    }
    .btn {
        margin-bottom: 15px;
    }
    &__flex {
        display: flex;
        width: 100%;
        margin-bottom: 15px;
    }
    .input {
        margin-bottom: 15px;
    }
    &__img {
        width: 27px;
        margin-right: 15px;
    }
    &__text {
        color: $color__additional;
        font-size: 14px;
    }
}