.menu-mob-popup-write-to-us {
  padding: 42px 15px 21px;

  &__name {
    letter-spacing: 0.36px;
    margin-bottom: 18px;
    text-align: center;
  }

  &__form {}
  
  &__btn {
    text-align: center;
    margin: 22px 0 17px;
    
    .btn {
      padding: 6px 15px 9px;
      letter-spacing: 0.4px;
    }
  }
  
  &__links {
    &-name {
      text-align: center;
      letter-spacing: 0.36px;
      margin-bottom: 7px;
    }
    
    &-list {
      table {
        width: 100%;
        
        td {
          font-size: 16px;
          letter-spacing: 0.4px;
          padding: 2px 0;
          
          &:first-child {
            min-width: 110px;
            padding-right: 10px;
          }
        }
      }
    }
  }

}