/* попап (количество купивших) */

.popup.popup-download-catalog {
  max-width: 885px;
  margin: 0 auto;

  @include media-breakpoint-down(tb) {
    max-width: 920px;
  }
  @include media-breakpoint-down(phx) {
    width: 288px;
  }
  
  &.popup-download-catalog_step2 {
    @include media-breakpoint-down(phx){
      .download-price-steps {
        display: block;
      }
      
      .download-price-info,
      .download-price__title,
      .download-price-video,
      .download-price__btn,
      .download-price__bg {
        display: none;
      }
    }
  }
}

.download-price {
  position: relative;
  padding: 37px 48px;
  overflow: hidden;

  @include media-breakpoint-down(tb) {
    padding: 37px 60px 15px;
  }
  @include media-breakpoint-down(phx) {
    padding: 46px 17px 35px;
  }

  &__title {
    position: relative;
    margin: 0 0 25px;
    font-size: 24px;
    text-align: center;
    font-weight: 300;
    letter-spacing: 0.32px;
    color: $color__text;

    @include media-breakpoint-down(tb) {
      font-size: 18px;
    }
    @include media-breakpoint-down(phx) {
      margin-bottom: 54px;
      font-size: 18px;
    }

    b {
      font-weight: normal;
    }
  }

  &-info {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 22px;
    margin-bottom: 27px;

    @include media-breakpoint-down(tb) {
      margin-bottom: 25px;
    }

    &__item {
      flex-shrink: 0;
      position: relative;
      font-size: 18px;

      @include media-breakpoint-down(phx) {
        font-size: 12px;
      }

      &_small {
        font-size: 16px;

        @include media-breakpoint-down(phx) {
          font-size: 12px;
        }
      }

      + .download-price-info__item {
        padding-left: 20px;
        margin-left: 20px;

        @include media-breakpoint-down(phx) {
          margin-left: 10px;
          padding-left: 14px;
        }

        &::before {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          margin-top: -11px;
          display: block;
          width: 2px;
          height: 22px;
          border-left: 1px dotted $color__additional;

          @include media-breakpoint-down(phx) {
            margin-top: -2px;
            width: 3px;
            height: 3px;
            border-radius: 50%;
            background: $color__additional;
            border: none;
          }
        }
      }
    }

    &__name {
      position: relative;
      padding-right: 11px;
      margin-right: 7px;

      @include media-breakpoint-down(phx) {
        padding-right: 0;
        margin-right: 4px;
      }

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        margin-top: -2px;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background: $color__additional;

        @include media-breakpoint-down(phx) {
          display: none;
        }
      }

      .download-price-info__item_small & {
        padding-right: 0;

        &::after {
          display: none;
        }
      }
    }

    &__data {
      color: $color__primary;
    }
  }

  &__bg {
    position: absolute;
    top: 10%;
    left: -25%;
    right: 25%;
    bottom: -10%;
    width: 100%;
    height: 100%;

    @include media-breakpoint-down(phx) {
      background-position: top left;
      top: -3%;
      left: -17%;
      width: auto;
      height: 102%;
    }
  }

  &__btn {
    text-align: center;

    .btn {
      width: 188px;
    }
  }

  &__row {
    position: relative;
    display: flex;

    @include media-breakpoint-down(phx) {
      display: block;
    }
  }

  &-video {
    width: 42%;
    padding: 38px 0;

    @include media-breakpoint-down(tb) {
      width: 46%;
      padding: 53px 0;
    }
    @include media-breakpoint-down(phx) {
      width: 100%;
      padding: 43px 0 117px;
    }

    &__btn {
      width: 117px;
      height: 117px;
      margin: 0 auto;
      cursor: pointer;

      @include media-breakpoint-down(phx) {
        width: 68px;
        height: 48px;
      }

      svg {
        display: block;
        width: 100%;

        @include media-breakpoint-down(phx) {
          height: 100%;
          opacity: .4;
        }
      }
    }
  }

  &-steps {
    position: relative;
    width: 58%;

    @include media-breakpoint-down(tb) {
      width: 54%;
    }
    @include media-breakpoint-down(phx) {
      width: 100%;
      display: none;
    }

    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      counter-reset: section;
    }
  }

  &-step {
    position: relative;
    margin: 0 0 31px;
    padding-left: 56px;
    padding-top: 8px;
    font-size: 16px;
    letter-spacing: 0.4px;

    @include media-breakpoint-down(tb) {
      padding-left: 43px;
      padding-top: 4px;
      margin-bottom: 16px;
    }
    @include media-breakpoint-down(phx) {
      padding-left: 0;
      padding-top: 0;
      font-size: 14px;
      margin-bottom: 25px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &::before {
      content: counters(section, ".");
      counter-increment: section;
      position: absolute;
      top: 2px;
      left: 0;
      display: block;
      width: 32px;
      height: 32px;
      line-height: 31px;
      text-align: center;
      border: 1px solid currentColor;
      border-radius: 50%;
      font-size: 18px;

      @include media-breakpoint-down(tb) {
        width: 26px;
        height: 26px;
        font-size: 16px;
        line-height: 26px;
      }
      @include media-breakpoint-down(phx) {
        position: relative;
        width: 16px;
        height: 16px;
        line-height: 16px;
        font-size: 12px;
        float: left;
      }
    }

    &__label {
      display: block;
      margin-bottom: 20px;
      font-size: 18px;

      @include media-breakpoint-down(tb) {
        margin-bottom: 14px;
        font-size: 16px;
      }
      @include media-breakpoint-down(phx) {
        display: table;
        margin-bottom: 13px;
        margin-left: 26px;
      }
    }

    &__field-group {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 17px;

      @include media-breakpoint-down(tb) {
        margin-bottom: 14px;
      }

      &_mob-wide {
        @include media-breakpoint-down(phx) {
          flex-direction: column;
          align-items: center;
        }
      }
    }

    &__input {
      width: 100%;

      input {
        width: 100%;
        height: 37px;
        line-height: 35px;
        padding-left: 12px;
        padding-right: 12px;
        font-size: 20px;
        letter-spacing: 0.36px;
        border-radius: 0;
        border: 1px solid $color__text2;
        outline: none;
        background: #fff;
        transition: border-color 0.2s;
        
        &:focus {
          border-color: $color__primary;
          
          &::placeholder {
            color: transparent;
          }
        }

        @include media-breakpoint-down(phx) {
          font-size: 16px;
        }
      }

      .input-phone__placeholder {
        font-size: 18px;
        padding: 7px 12px 0 12px;
      }
      .input-phone__input {
        input {
          padding-top: 0;
          padding-bottom: 0;
          font-size: 18px;
          border: none;
        }
      }

      .input-phone {
        border: 1px solid $color__text2;
        border-radius: 0;
      }
    }

    .lang > a:after {
      //margin-top: -1px;
    }

    &__btn {
      flex-shrink: 0;
      width: 135px;
      margin-left: 17px;

      @include media-breakpoint-down(phx) {
        width: 116px;
      }

      .download-price-step__field-group_mob-wide & {
        @include media-breakpoint-down(phx) {
          margin-left: 0;
          margin-top: 14px;
        }
      }

      .btn {
        width: 100%;
        padding: 8px;
      }
    }

    p {
      margin-top: 0;
      margin-bottom: 21px;

      @include media-breakpoint-down(tb) {
        margin-bottom: 16px;
      }
      
      @include media-breakpoint-down(phx){
        &:last-child {
          margin-bottom: 0; 
        }
      }
    }

    &__info {
      margin: 0;
      padding: 10px 21px 13px;
      border: 1px solid $color__text3;
      color: $color__text3;

      @include media-breakpoint-down(phx) {
        padding: 0;
        border: none;
        color: $color__additional;
      }
    }
  }
}