.popup-cheaper {
    position: relative;
    padding: 35px 138px 30px;
    //overflow: hidden;
    width: 951px;
    margin: 0 auto;
    //@include medium {
    //    min-width: 895px;
    //}
  
    @include media-breakpoint-down(tbx){
      width: 920px;
      padding: 35px 123px 30px;
    }
  
    @include media-breakpoint-down(tb){
        padding: 43px 16px 30px;
        width: auto;
    }
  
    .popup__row-cheaper {
        margin-bottom: 50px;

        @include media-breakpoint-down(tbx){
            margin-bottom: 34px;
        }
    }
  
    &__hide-small {
      @include media-breakpoint-down(tb){
        display: none;
      }
    }
  
    &__show-small {
      display: none;
      @include media-breakpoint-down(tb){
        display: block;
      }
    }
  
    &__name {
        font-size: 22px;
        margin-bottom: 45px;

        @include media-breakpoint-down(tbx){
          font-size: 18px;
          letter-spacing: 0.36px;
          margin-bottom: 28px;
        }

        @include media-breakpoint-down(tb){
          font-size: 20px;
          letter-spacing: 0.36px;
          margin-bottom: 25px;
        }
    }
    &__items {
        margin-bottom: 48px;
        @include media-breakpoint-down(tbx){
            margin-bottom: 34px;
        }
        @include media-breakpoint-down(tb){
            margin-bottom: 38px;
        }
    }
    &__item {
        display: flex;
        margin-bottom: 12px;

        @include media-breakpoint-down(tbx){
            margin-bottom: 21px;
        }

        @include media-breakpoint-down(tb){
            margin-bottom: 24px;
        }
    }
    &__phone {
        min-width: 0;
    }
    &__item-index {
        flex: none;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 34px;
        height: 34px;
        border: 1px solid black;
        border-radius: 50%;
        font-size: 16px;
        line-height: 16px;
        margin: -4px 16px 0 0;

        @include media-breakpoint-down(tbx){
            width: 30px;
            height: 30px;
            margin: -4px 21px 0 0;
        }

        @include media-breakpoint-down(tb){
            width: 25px;
            height: 25px;
            font-size: 14px;
            margin: -2px 13px 0 0;
        }
    }
    &__item-text {
        font-size: 18px;
        letter-spacing: 0.36px;

        @include media-breakpoint-down(tbx){
          font-size: 16px;
          letter-spacing: 0.4px;
        }
    }
    &__flex {
        width: 100%;
        display: flex;
        margin-bottom: 40px;

        @include media-breakpoint-down(tbx){
            margin-bottom: 30px;
        }
        @include media-breakpoint-down(tb){
            flex-direction: column;
            margin-bottom: 25px;
        }
    }
    &__w70 {
        flex: none;
        width: 70%;
        @include media-breakpoint-down(tb){
            width: 100%;
            margin-bottom: 22px;
        }
    }
    &__w30 {
        flex: none;
        width: 30%;
        display: flex;
        align-items: center;
        padding-left: 45px;
        @include media-breakpoint-down(tb){
            width: 100%;
            padding-left: 0;
            justify-content: center;
        }
    }
    &__clip {
        margin-right: 15px;
    }
    &__file-text {
        font-size: 16px;
        letter-spacing: 0.4px;
        border-bottom: 2px dotted lightgray;
    }
     &__send {
         .btn {
           padding: 7px 22px 8px;
           @include media-breakpoint-down(tb){
             font-size: 18px;
             letter-spacing: 0.36px;
             padding: 7px 15px 8px;
           }
         }
     }
}