.breadcrumbs {
  padding-left: 16px;

  @include media-breakpoint-down(phx){
    padding-left: 0;
  }
  
  &__list {
    @extend %list;
    font-size: 0;
    
    li {
      position: relative;
      font-size: 18px;
      display: inline-block;
      padding-right: 42px;
      transition: opacity 1s, transform 1s;
      opacity: 0;
      transform: scale(0);

      @for $i from 0 through 8 {
        &:nth-child(#{$i + 1}){
          transition-delay: #{0.5 * $i}s;
        }
      }

      @include media-breakpoint-down(dtx){
        font-size: 16px;
      }

      @include media-breakpoint-down(lt){
        font-size: 14px;
        letter-spacing: 0.4px;
        padding-right: 30px;
      }

      @include media-breakpoint-down(tb) {
        padding-right: 29px;
      }
      
      @include media-breakpoint-down(phx){
        padding-right: 25px;
      }
      
      &.breadcrumbs__mobile {
        display: none;
        color: $color__text;
        
        @include media-breakpoint-down($mobile-switch){
          display: inline-block;
        }
      }
      
      &.breadcrumbs__hide-mobile {
        @include media-breakpoint-down($mobile-switch){
          display: none;
        }
      }
      
      &:after {
        position: absolute;
        top: 6px;
        right: 14px;
        background: url(../img/svg/arrow-breadcrumbs.svg) no-repeat;
        width: 14px;
        height: 9px;
        content: "";

        @include media-breakpoint-down(lt) {
          top: 5px;
          right: 8px;
        }

        @include media-breakpoint-down($mobile-switch) {
          right: 7px;
        }
      }
      
      &:last-child {
        padding-right: 0;
        &:after {
          content: none;
        }
      }
      
      a {
        position: relative;
        letter-spacing: 0.36px;
        display: inline-block;
        line-height: 1.25;
        transition: none;

        &::after {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 1px;
          transition: 0.25s;
          background-image: repeating-linear-gradient(to right, $color__additional 0, $color__additional 1px, transparent 1px, transparent 2px);
          content: "";
        }

        > span {
          color: $color__text;
          transition: all .25s;
        }

        &:hover {
          &::after {
            width: 0;
          }

          > span {
            color: $color__primary;
          }
        }
      }
      
      span {
        color: $color__additional;
      }
    }
  }
  
  &_animate {
    .breadcrumbs__list {
      li {
        transform: scale(1);
        opacity: 1;
      }
    }
  }
  
  &_nopadding {
    padding: 0;
  }
}