.section-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 19px;

  .dot-link-one-line__name {
    font-size: 16px;
    white-space: nowrap;

    @include media-breakpoint-down(dt) {
      font-size: 14px;
    }
  }

  .dot-link-one-line__arrow {
    top: 7px;
  }
}