.detail__bottom {
    + .index-download {
        margin-top: 78px;
    }
}

.index-download {
    position: relative;
    min-height: 717px;
    font-family: Lato, Arial, sans-serif;

    @include medium {
        min-height: initial;
        padding-bottom: 80px;
    }
    @include media-breakpoint-down(phx)  {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        padding-bottom: 0;
    }

    &__flex {
        position: relative;
        display: flex;
        width: 100%;
        z-index: 2;
        @include medium {
            flex-direction: column-reverse;
        }
        @include media-breakpoint-down(phx) {
            padding-bottom: 319px;
        }
    }

    &__left {
        padding-top: 9px;
        width: 51.5%;
        @include medium {
            width: 100%;
        }
    }

    &__right {
        display: flex;
        flex-wrap: wrap;
        padding-top: 76px;
        width: 48.5%;
        @include medium {
            justify-content: flex-end;
            padding-top: 0;
            width: 100%;
        }
        @include media-breakpoint-down(phx) {
            justify-content: center;
        }
    }

    .section-title {
        @include media-breakpoint-down(phx) {
            margin-bottom: 0;
        }
        &::before {
            @include media-breakpoint-down(tbx)  {
                display: none;
            }
        }
    }

    &__header {
        position: relative;
        font-weight: normal;
        font-size: 32px;
        text-align: center;
        letter-spacing: 0.32px;
        margin: 0;

        @include media-breakpoint-down(phx) {
            display: flex;
            flex-direction: column;
            align-items: center;
            font-size: 20px;
            letter-spacing: .36px;
            margin-bottom: 10px;
        }

        > span {
            background: #fff;
            box-shadow: 0 0 70px 50px rgba(255,255,255,1);

            @include media-breakpoint-down(phx) {
                padding: 0;
            }
        }

        b {
            font-weight: normal;
        }

        > span {
            span {
                padding: 0;
            }
        }

        &_light {
            font-weight: 300;

            @include media-breakpoint-down(phx) {
                max-width: 250px;
            }
        }
    }

    &__background {
        position: absolute;
        top: -100px;
        bottom: 0;
        left: -42px;
        right: 0;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: auto;
        z-index: -1;

        @include media-breakpoint-down(tbx) {
            top: 80px;
            background-position: 0 50%;
            background-size: 97%;
        }
        @include media-breakpoint-down(phx) {
            top: 0;
            left: 0;
            background-position: -80px -80px;
            background-size: 900px;
        }

        img {
            width: 100%;

            @include small {
                min-width: 1000px;
            }
        }
    }

    &__button {
        display: inline-block;
        margin-right: 57px;

        @include medium {
            margin: 27px 0 0 0;
        }

        img {
            margin-right: 20px;
        }

        &-wrap {
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 5px;
            background-color: #999900;
            color: white;
            height: 56px;
            padding: 0 20px;
            font-size: 22px;
            letter-spacing: 0.36px;
            font-weight: normal;
            cursor: pointer;
            transition: 0.2s;
          
            &:focus,
            &:hover {
              background: $color__primary;
              color: #fff;
            }

            @include medium {
                padding: 0 29px;
            }
        }
    }

    &-list {
        width: 100%;

        @include medium {
            margin-bottom: 266px;
        }

        &__title {
            margin-bottom: 13px;
            font-size: 24px;
            letter-spacing: 0.32px;

            @include medium {
                display: none;
            }
        }

        &__wrap {
            display: flex;
            width: 100%;
            justify-content: space-between;
            margin-bottom: 53px;
        }

        &__el {
            flex: none;
            display: flex;
            align-items: center;
        }

        &__num {
            font-size: 56px;
            letter-spacing: 0.32px;
            color: $color__primary;
            margin-right: 20px;
            line-height: 1em;
            padding-top: 4px;

            @include medium {
                font-size: 40px;
            }
        }

        &__text {
            font-size: 18px;
            letter-spacing: 0.36px;
        }
    }

    &__youtube-btn {
        display: flex;
        align-items: center;
        cursor: pointer;
        width: 240px;
        margin-left: 85px;

        @include medium {
            position: absolute;
            top: 153px;
            right: 68px;
            width: 200px;
            flex-direction: column;
            text-align: center;
            margin-left: 0;
        }
        @include media-breakpoint-down(phx) {
            width: 68px;
            right: 0;
            left: 0;
            top: 127px;
            margin: 0 auto;
        }

        &__text {
            @include media-breakpoint-down(tbx){
              margin-top: 14px;
            }
            @include media-breakpoint-down(phx) {
                display: none;
            }
            > span {
                display: inline;
                border-bottom: 1px dotted lightgray;
                font-family: Lato, sans-serif;
                font-size: 18px;
                letter-spacing: 0.36px;
            }
        }

        img {
            margin-right: 20px;
            @include medium {
                margin-right: 0;
            }
            @include media-breakpoint-down(phx) {
                width: 100%;
            }
        }
    }

    &__info {
        display: flex;

        @include medium {
            flex-direction: column-reverse;
        }
    }

    &__description {
        display: flex;
        align-items: center;
        font-size: 24px;
        letter-spacing: 0.32px;
        margin-top: 4px;

        @include media-breakpoint-down(tbx) {
            width: 100%;
            justify-content: flex-end;
        }
        @include media-breakpoint-down(phx) {
            justify-content: center;
            font-size: 14px;
        }

        &-item {
            min-height: 24px;
            margin-right: 5px;

            @include media-breakpoint-down(phx) {
                min-height: initial;
            }

            &:not(:first-child) {
                position: relative;
                padding-left: 24px;
                border-left: 1px dotted lightgray;

                @include media-breakpoint-down(phx) {
                    border-left: none;
                    padding-left: 13px;

                    &::before {
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-50%);
                        content: '';
                        display: block;
                        width: 3px;
                        height: 3px;
                        background-color: lightgray;
                        border-radius: 50%;
                    }
                }
            }

            &_sm {
                font-size: 18px;

                @include media-breakpoint-down(phx) {
                    font-size: 14px;
                }
            }

            &_orange {
                color: $color__primary;
                &:not(:last-child) {
                    margin-right: 20px;

                    @include media-breakpoint-down(phx) {
                        margin-right: 10px;
                    }
                }
            }

            &_point {
                position: relative;
                padding-right: 10px;
                margin-right: 10px;

                @include media-breakpoint-down(phx) {
                    padding-right: 0;
                    margin-right: 8px;
                }

                &::after {
                    position: absolute;
                    right: -3px;
                    top: 50%;
                    transform: translateY(-50%);
                    content: '';
                    display: block;
                    width: 5px;
                    height: 5px;
                    background-color: lightgray;
                    border-radius: 50%;

                    @include media-breakpoint-down(phx) {
                        display: none;
                    }
                }
            }
        }
    }

    @include media-breakpoint-down(phx) {
        &__block {
            padding: 0 17px 26px 42px;
            width: 100%;
            display: flex;
            flex-direction: column;
            background: linear-gradient(transparent, $color__secondary 33%, $color__secondary);

            &-item {
                &-name {
                    position: relative;
                    font-size: 16px;
                    margin-bottom: 13px;
                }
                &-number {
                    position: absolute;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    left: -27px;
                    top: 1px;
                    border: 1px solid black;
                    border-radius: 50%;
                    width: 18px;
                    height: 18px;
                    padding-top: -1px;
                    padding-bottom: 1px;
                    font-size: 12px;
                }
                &-description {
                    color: $color__additional;
                    display: flex;
                    margin-bottom: 13px;
                    font-size: 14px;
                    &-img {
                        padding-top: 2px;
                        margin-right: 15px;
                    }
                    &-text {
                        line-height: 1.4;
                        letter-spacing: 0.4px;
                    }
                }
                &-text {
                    letter-spacing: 0.4px;
                }
            }
            .input-phone__input {
                input {
                    padding: 9px 12px 6px;
                }
            }
            .input-phone__placeholder {
                padding: 8px 12px 0;
            }
            .input-phone,
            .input-phone__row {
                min-width: 0;
                width: 100%;
            }
            .index-download__phone,
            .input {
                margin-bottom: 12px;
            }
            .input-phone__lang {
                padding-top: 3px;
            }
            .input__input > input {
                height: 35px;
                padding: 8px 12px;
            }
            button {
                margin-right: auto;
                margin-bottom: 10px;
                min-width: 116px;
            }
        }
    }
}