.desc {
  overflow: hidden;
  
  &__inner {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;

    @include media-breakpoint-down(lt){
      margin: 0 -10px;
    }

    @include media-breakpoint-down(tbx){
      margin: 0 -20px;
    }

    @include media-breakpoint-down($mobile-switch){
      margin: 0;
      display: block;
    }
  }
  
  &__item {
    width: 100%;
    padding: 25px 20px;

    @include media-breakpoint-down(lt){
      padding: 25px 10px;
    }

    @include media-breakpoint-down(tbx){
      padding: 18px 20px;
    }

    @include media-breakpoint-down($mobile-switch){
      width: auto;
      padding: 0;
      margin-bottom: 20px;
    }
    
    &_25 {
      width: 25%;

      @include media-breakpoint-down(tbx){
        width: 50%;
      }

      @include media-breakpoint-down($mobile-switch){
        width: auto;
      }
    }
    
    &_50 {
      width: 50%;

      @include media-breakpoint-down(tbx){
        width: 100%;
      }

      @include media-breakpoint-down($mobile-switch){
        width: auto;
      }
    }
    
    &_75 {
      width: 75%;

      @include media-breakpoint-down(tbx){
        width: 100%;
      }

      @include media-breakpoint-down($mobile-switch){
        width: auto;
      }
    }
    
  }
}

.desc-item {
  
  &__name {
    font-size: 26px;
    letter-spacing: 0.32px;

    @include media-breakpoint-down(dt){
      font-size: 24px;      
    }

    @include media-breakpoint-down(lt){
      font-size: 22px;
    }

    @include media-breakpoint-down($mobile-switch){
      font-size: 20px;
      letter-spacing: 0.36px;
      color: $color__primary;
    }
    
    + .desc-item__slider,
    + .desc-item__img {
      margin-top: 14px;
    }
    
    + .desc-item__text {
      margin-top: 14px;

      @include media-breakpoint-down($mobile-switch){
        margin-top: 8px;
      }
    }
    
    &_hidden {
      visibility: hidden;

      @include media-breakpoint-down($mobile-switch){
        display: none;
      }
    }
  }
  
  &__text {
    font-size: 18px;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(dt){
      font-size: 16px;
    }

    @include media-breakpoint-down(lt){
      font-size: 14px;
    }

    @include media-breakpoint-down(tbx){
      font-size: 16px;
    }

    @include media-breakpoint-down($mobile-switch){
      letter-spacing: 0.4px;
    }

    + .desc-item__slider,
    + .desc-item__img {
      margin-top: 18px;

      @include media-breakpoint-down($mobile-switch){
        margin-top: 14px;
      }
    }

    ul {
      @extend %list;
      
      li {
        position: relative;
        padding-left: 16px;
        margin-bottom: 1px;
        
        &:before {
          position: absolute;
          top: 9px;
          left: 0;
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background: $color__additional;
          content: "";
        }
      }
    }
    
    ul,
    p {
      margin: 14px 0;

      @include media-breakpoint-down($mobile-switch){
        margin: 8px 0;
      }
    }
    
    
  }


  &__slider {

    .swiper-pagination {
      display: none;
      @include media-breakpoint-down(tb){
        display: block;
      }
    }
    
    @include media-breakpoint-down(tb){
      padding-bottom: 40px;
    }
  }
  
  &__slider,
  &__img {
    + .desc-item__text {
      margin-top: 18px;

      @include media-breakpoint-down($mobile-switch){
        margin-top: 8px;
      }
    }
  }
  
  &__img {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    
  }
  
}