.review {
  position: relative;

  @include media-breakpoint-down(tbx) { // change
    font-size: 16px;
  }

  &.review_full {
    &::before {
      content: '';
      position: absolute;
      top: 41px;
      left: 57px;
      bottom: -30px;
      display: block;
      width: 40px;
      border-left: 2px dashed $color__primary;

      @include media-breakpoint-down(tbx) { // change
        left: 19px;
        width: 34px;
      }
      @include media-breakpoint-down(phx) {
        left: 0;
        width: 20px;
      }
    }

    + .review {
      > .review__inner {
        margin-top: 40px;

        @include media-breakpoint-down(tbx) { // change
          padding-top: 0;
          border-top: none;
        }
      }
    }
  }

  //level-2
  > .reviews-list {
    position: relative;
    margin-left: 114px;
    padding-top: 20px;

    @include media-breakpoint-down(tbx) { // change
      margin-left: 56px;
      padding-top: 0;
    }
    @include media-breakpoint-down(phx) {
      margin-left: 22px;
    }

    .review-avatar {
      &::after {
        display: none;
      }
    }

    .review__name {
      @include media-breakpoint-down(tbx) { // change
        position: relative;
        top: -3px;
      }
    }

    .review__head {
      @include media-breakpoint-down(tbx) { // change
        margin-bottom: 8px;
      }
    }

    .review-gallery {
      @include media-breakpoint-down(tbx) { // change
        margin-top: 4px;
        padding-left: 53px;
      }
      @include media-breakpoint-down(phx) {
        padding-left: 0;
      }
    }
  }

  .review {
    &::before {
      content: '';
      position: absolute;
      top: 57px;
      left: -57px;
      width: 40px;
      height: 18px;
      background: #fff;
      border-top: 2px dashed $color__primary;

      @include media-breakpoint-down(tbx) { // change
        top: 71px;
        left: -37px;
        width: 19px;
        height: 24px;
      }
      @include media-breakpoint-down(phx) {
        left: -22px;
        width: 13px;
      }
    }

    &:last-child {
      &:before {
        height: auto;
        bottom: -32px;

        @include media-breakpoint-down(tbx) { // change
          bottom: -42px;
        }
      }
    }

    > .review__inner {
      padding: 23px 24px 34px;
      background: $color__secondary;

      @include media-breakpoint-down(tbx) { // change
        padding: 23px 24px 24px;
      }
      @include media-breakpoint-down(phx) {
        padding: 16px;
      }
    }

    + .review {
      > .review__inner {
        border-top: none;
        padding-top: 24px;
        margin-top: 32px;

        @include media-breakpoint-down(tbx) { // change
          margin-top: 24px;
        }
        @include media-breakpoint-down(phx) {
          padding-top: 16px;
        }
      }
    }

    .review__text {
      background: transparent;
    }

    .review__pic {
      &::after {
        background: transparent;
      }
    }

    .review__foot {
      @include media-breakpoint-down(tbx) { // change
        margin-top: 19px;
      }
    }
  }

  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;

    @include media-breakpoint-down(tbx) { // change
      display: block;
    }
    @include media-breakpoint-down(phx) {
      background: #fff;
    }
  }

  + .review {
    > .review__inner {
      padding-top: 40px;
      margin-top: 36px;
      border-top: 1px solid $color__text2;

      @include media-breakpoint-down(tbx) { // change
        padding-top: 32px;
        margin-top: 41px;
      }
      @include media-breakpoint-down(phx) {
        margin-top: 36px;
      }
    }
  }

  &__pic {
    width: 260px;
    flex-shrink: 0;
    padding-right: 40px;

    @include media-breakpoint-down(tbx) {
      float: left;
      width: 132px;
      padding-right: 20px;
      margin-top: 5px;
    }
    @include media-breakpoint-down(phx) {
      width: 118px;
    }

    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 15px;
      background: #fff;
    }

    &_doc {
      + .review__body {
        .review__head-item {
          padding-top: 18px;

          @include media-breakpoint-down(tbx) { // change
            padding-top: 0;
            top: -2px;
            position: relative;
          }
          @include media-breakpoint-down(phx) {
            top: 0;
            padding-top: 3px;
          }
        }
      }
    }
  }

  &__body {
    width: calc(100% - 260px);

    @include media-breakpoint-down(tbx) { // change
      width: 100%;
    }
  }

  &__head {
    display: flex;
    justify-content: space-between;
    margin: 0 0 25px;

    @include media-breakpoint-down(tbx) { // change
      display: block;
      margin-bottom: 10px;
    }
    @include media-breakpoint-down(phx) {
      margin-bottom: 22px;
    }

    &-item {
      width: 54.2%;
      padding-right: 82px;
      padding-top: 11px;

      @include media-breakpoint-down(tbx) { // change
        width: 100%;
        padding: 0;
      }
      @include media-breakpoint-down(phx) {
        padding-top: 3px;
      }
    }
  }

  &__text {
    margin-bottom: 23px;
    background: #fff;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(tbx) { // change
      margin-bottom: 0;
      padding-bottom: 16px;
      letter-spacing: 0.4px;
      line-height: 1.3;
    }
    @include media-breakpoint-down(phx) {
      clear: both;
    }

    > p {
      margin-top: 0;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__foot {
    display: flex;
    justify-content: space-between;
    font-size: 20px;

    @include media-breakpoint-down(tbx) { // change
      margin-top: 17px;
      font-size: 16px;
    }
    @include media-breakpoint-down(phx) {
      display: block;
      margin-top: 10px;
    }

    &-item {
      margin-right: 12px;

      @include media-breakpoint-down(phx) {
        margin-bottom: 22px;
        margin-right: 0;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  &__name {
    margin: 0;
    font-size: 20px;
    letter-spacing: 0.36px;
    line-height: 1.25;
    word-break: break-word;

    @include media-breakpoint-down(tbx) { // change
      padding-right: 14%;
      font-size: 18px;
    }
    @include media-breakpoint-down(phx) {
      font-size: 16px;
      height: 98px;
      display: flex;
      align-items: center;
      padding-right: 20%;
    }
    @include media-breakpoint-down(ph) {
      padding-right: 0;
    }

    + .address {
      max-width: 70%;
      margin-top: 22px;
      margin-bottom: 23px;

      @include media-breakpoint-down(tbx) { // change
        margin-top: 13px;
      }
      
      .address__country img {
        width: 16px;
      }

      .review_column & {
        margin-top: 11px;
        padding-left: 0;

        @include media-breakpoint-down(tbx) { // change
          margin-top: 7px;
        }
      }
    }
  }

  .video {
    height: 280px;
    margin-bottom: 15px;

    @include media-breakpoint-down(tbx) { // change
      height: 0;
      margin-bottom: 9px;
      padding-top: 56.5%;
    }
  }

  .video__play {
    &::before {
      background-size: 42px;
    }
  }
}