        padding-top: 10px;
      }

      @include media-breakpoint-down(lt){
        font-size: 16px;
        letter-spacing: 0.4px;

        + .trigger-item__info-text {
          padding-top: 10px;
        }
      }
    }

    .trigger-item__info-text {
      font-size: 16px;
      letter-spacing: 0.4px;
    }

  }

  &_banners {
    border-bottom: 1px solid $color__text2;

    .trigger-item__normal {
      @include media-breakpoint-down(lt){
        height: 156px;
      }
    }

    .trigger-item__hover {
      @include media-breakpoint-down(lt){
        padding: 10px 15px;
      }
    }

    .trigger-item__desc {
      @include media-breakpoint-down(lt){
        font-size: 14px;
      }
    }

    .trigger-item__link {
      @include media-breakpoint-down(lt) {
        margin-top: 3px;
        a {
          font-size: 14px;
        }
      }
    }

    .trigger-item__text {
      @include media-breakpoint-down(lt){
        font-size: 16px;
        letter-spacing: 0.4px;

        + .trigger-item__info-text {
          padding-top: 10px;
        }
      }
    }

    .trigger-item__info-text {
      @include media-breakpoint-down(lt){
        font-size: 16px;
        letter-spacing: 0.4px;
      }
    }
  }
}

.trigger-item {
  position: relative;
  background: #fff;
  z-index: 1;

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 1px;
    bottom: 0;
    background: $color__text2;
  }

  &__popup-content {
    display: none;
  }

  &__blobs {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: visibility .4s;
    visibility: hidden;

    div {
      background-color: $color__primary;
      width: 20px;
      height: 20px;
      border-radius: 0 50% 50% 50%;
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(-50%, -50%);
      transition: height .5s ease, width .5s ease;
    }
  }

  &__normal {
    position: relative;
    overflow: hidden;
    height: 220px;
    display: flex;
    align-items: flex-start;
    padding-top: 90px;

    .trigger-item__icon {
      img {

      }
    }
  }

  &__hover {
    position: absolute;
    height: 100%;
    left: 0;
    width: 100%;
    top: 0;
    transition: 0.2s 0s;
    z-index: 4;
    padding: 15px;
    display: flex;
    align-items: flex-end;
    opacity: 0;
    visibility: hidden;
    
    @include media-breakpoint-down(lt){
      display: block;
      padding: 10px 10px 18px 68px;
    }

    .dot-link-one-line__name {
      &::after {
        background-image: repeating-linear-gradient(to right, #fff 0, #fff 1px, transparent 1px, transparent 2px);
      }
    }
  }

  &__desc {
    font-size: 16px;
    letter-spacing: 0.4px;
    color: #fff;
  }
  
  &__full {
    @include media-breakpoint-down(lt){
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
  }

  &__link {
    margin-top: 16px;

    a {
      font-size: 16px;
      color: #fff;
    }

    .dot-link-one-line {
      &:hover {
        .dot-link-one-line__name {
          border-color: #fff;
        }
      }
    }

    .dot-link-one-line__name {
      border-color: #fff;
    }

    .content-head__triggers-content_normal & {
      margin-top: 8px;
    }
  }

  &__open {
    position: absolute;
    bottom: 12px;
    right: 12px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    border: 1px solid $color__additional;
    cursor: pointer;

    &:before,
    &:after {
      position: absolute;
      top: 50%;
      left: 50%;
      background: #000;
      content: "";
    }

    &:after {
      width: 11px;
      height: 1px;
      margin-top: -0.5px;
      margin-left: -5.5px;
    }

    &:before {
      width: 1px;
      height: 11px;
      margin-top: -5.5px;
      margin-left: -0.5px;
    }
  }

  &__icon {
    position: absolute;
    top: -104px;
    left: -88px;
    width: 190px;
    height: 190px;
    border-radius: 50%;
    background: $color__primary;

    @include media-breakpoint-down(lt){
      top: -42px;
      left: -33px;
      width: 107px;
      height: 107px;
    }

    &-container {
      position: absolute;
      left: 88px;
      top: 104px;
      right: 0;
      bottom: 0;
      padding: 18px;
      z-index: 5;

      @include media-breakpoint-down(lt){
        left: 27px;
        top: 35px;
      }
    }

    img {
      transform: scale(1,1);
      transition: transform .35s ease;
    }
  }

  &__text,
  &__info-text {
    letter-spacing: 0.36px;

    @include media-breakpoint-down(dtx){
      font-size: 18px;
    }
  }

  &__text {
    font-size: 20px;

    + .trigger-item__info-text {
      padding-top: 8px;
    }
  }

  &__info-text {
    font-size: 18px;
    color: $color__additional;

    @include media-breakpoint-down(tb){
      font-size: 14px;
    }
  }

  &__content {
    padding: 10px 16px 18px;
  }
  
  &__js {
    overflow: hidden;
    visibility: hidden;
    transition: visibility 0.4s;
    //pointer-events: none;


    .trigger-item__icon {
      z-index: 4;
    }
    
    &.animated-blobs {
      visibility: visible;

      .trigger-item__blobs {
        visibility: visible;

        div {
          width: 1500px;
          height: 1500px;
          transition: height .8s ease-out, width .8s ease-out;

          @include media-breakpoint-down(lt) {
            transition: height .7s ease, width .7s ease;
          }
        }
      }

      .trigger-item__hover {
        transition: .25s .2s;
        opacity: 1;
        visibility: visible;
      }

      .trigger-item__icon {
        img {
          transform: scale(1.4, 1.4);

          @include media-breakpoint-down(dt) {
            transform: scale(1.24, 1.24);
          }

          @include media-breakpoint-down(lt){
            transform: scale(1, 1);
          }
        }
      }
      
    }
    
  }

  &.animated-blobs {

    

    .trigger-item__icon {
      img {
        transform: scale(1.4, 1.4);

        @include media-breakpoint-down(dt) {
          transform: scale(1.24, 1.24);
        }
        @include media-breakpoint-down(lt) {
          transform: scale(1, 1);
        }
      }
    }

    .trigger-item__normal {
      .trigger-item__icon {
        img {
          transform: scale(1.15, 1.15);
        }
      }
    }
  }

  &.animated-block {
    //.trigger-item__hover {
    //  opacity: 1;
    //  visibility: visible;
    //}
  }

