.nav {
  
  &__name {
    font-size: 26px;
    letter-spacing: 0.32px;
    margin-bottom: 20px;
    padding-left: 16px;

    @include media-breakpoint-down(dtx){
      font-size: 24px;
    }

    @include media-breakpoint-down(tbx){
      font-size: 20px;
      padding-left: 10px;
      letter-spacing: 0.36px;
      margin-bottom: 10px;
    }
  }
  
  &__ul {
    display: none;
    border-top: 1px solid $color__text2;

    > ul {
    }
  }
  
  &__img {
    position: relative;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s;
    z-index: 1;
    
    &-content {
      position: absolute;
      top: 50%;
      right: -250px;
      height: 134px;
      width: 240px;
      background: #fff;
      margin-top: -67px;
      box-shadow: 0 0 12px rgba(255, 102, 0, 0.4);
      z-index: 1;
      
      &-inner {
        position: absolute;
        top: 16px;
        bottom: 16px;
        left: 16px;
        right: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgba(0, 255, 0, 0.15);
      }
    }
    
    &-arrow-white,
    &-arrow {
      
      position: absolute;
      top: 50%;
      right: -10px;
      width: 22px;
      height: 22px;
      background: #fff;
      transform: translateY(-50%) translateX(50%) rotateZ(45deg);
    }

    &-arrow-white {
      z-index: 2;
    }
    &-arrow {
      box-shadow: 0 0 12px rgba(255, 102, 0, 0.4);
    }
  }
  
  ul {
    @extend %list;
    
    ul {
      margin-left: 32px;

      @include media-breakpoint-down(tbx){
        margin-left: 12px;
      }
    }
    
    li {
      border-bottom: 1px solid $color__text2;
      
      &.nav-dot {
        > a {
          &:before {
            position: absolute;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background: $color__additional;
            content: "";
            left: 13px;
            top: 18px;
            transition: 0.2s;
            
            @include media-breakpoint-down(tbx){
              top: 16px;
            }
          }
        }

        &.nav-active {
          > a {
            &:before {
              background: $color__primary;
            }
          }
        }
      }
      
      &.nav-drop {  
        
        > a {
          &:before,
          &:after {
            position: absolute;
            top: 24px;
            left: 0;
            background: #000;
            transition: 0.2s;
            content: "";

            @include media-breakpoint-down(tbx){
              top: 19px;
            }
          }
          
          &:before {
            width: 11px;
            height: 1px;
            margin-top: -0.5px;
            left: 8px;
          }
          
          &:after {
            width: 1px;
            height: 11px;
            margin-top: -5.5px;
            left: 13px;
          }
        }
        
        &.nav-open {
          > a {
            &:after {
              opacity: 0;
              transform: rotateZ(90deg);
            }
          }

          > .nav__ul {
            display: block;
          }
        }
        
        &.nav-active {
          > a {
            &:before,
            &:after {
              background: $color__primary;
            }
          }
        }
      }
      
      &:last-child {
        border-bottom: 0;
      }
      
      > a {
        position: relative;
        display: block;
        font-size: 20px;
        letter-spacing: 0.36px;
        padding: 11px 10px 11px 36px;
        word-break: break-word;

        @include media-breakpoint-down(dtx){
          font-size: 18px;
        }

        @include media-breakpoint-down(tbx){
          font-size: 16px;
          letter-spacing: 0.4px;
          padding: 10px 10px 10px 28px;
        }
        
        &:focus,
        &:hover {
          &:before,
          &:after {
            background: $color__primary;
          }
        }
        
        &:hover {
          > .nav__img {
            opacity: 1;
            visibility: visible;
          }
        }
      }

      &.nav-active {
        > a {
          color: $color__primary;
        }
      }
    }
  }
}