/* попап (наши магазины) */

.popup.popup-shop {
  position: static;
  width: 960px;
  background: none;
  box-shadow: none;
  
  @include media-breakpoint-down(tbx) {
    width: 590px;
  }
}

.popup-shop {
  color: #fff;
  
  &__head {
    display: flex;
    justify-content: space-between;
    padding: 16px;

    @include media-breakpoint-down(tbx) {
      padding: 0;
      margin-bottom: 20px;
    }
  }
  
  &__foot {
    display: flex;
    justify-content: space-between;
    padding: 20px 16px;

    @include media-breakpoint-down(tbx) {
      padding: 0;
      margin-top: 22px;
    }
  }
  
  &__map {
    a {
      color: #fff;
      font-size: 16px;
      //font-weight: 600;
      letter-spacing: 0.24px;
      transition: color 0.2s;
      
      span {
        //border-bottom: 1px dotted #fff;
        //transition: border-color 0.2s;
      }

      &:focus,
      &:hover {
        color: $color__primary;

        span {
          //border-color: $color__primary;
        }
      }
    }
  }
  
  &__address,
  &__nav {
    ul {
      @extend %list;
    }
  }
  
  &__address {
    font-size: 16px;
    //font-weight: 600;
    letter-spacing: 0.24px;
    
    @include media-breakpoint-down(tbx) {
      font-weight: normal;
      letter-spacing: 0.4px;
    }
    
    ul {
      display: flex;

      @include media-breakpoint-down(tbx) {
        display: block;
        
        br {
          display: none;
        }
      }
      
      li {
        margin-right: 46px;

        @include media-breakpoint-down(tbx) {
          margin-bottom: 5px;
        }
        
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
  
  &__nav {
    ul {
      display: flex;
      align-items: baseline;
      
      li {
        margin-right: 40px;
        
        &:last-child {
          margin-right: 0;
        }
        
        a {
          position: relative;
          color: #fff;
          //font-weight: 600;
          letter-spacing: 0.18px;
          display: block;

          @include media-breakpoint-down(tbx) {
            font-weight: normal;
            letter-spacing: 0.36px;
          }

          &.popup-shop__icon {
            padding-left: 25px;
          }

          svg {
            position: absolute;
            top: 0;
            left: 0;
            width: 15px;
            height: 19px;
            stroke: #fff;
            fill: none;
            transition: 0.2s;
          }

          span {
            border-bottom: 1px dotted #fff;
            transition: border-color 0.2s;
          }

          &.active,
          &:focus,
          &:hover {
            color: $color__primary;

            svg {
              stroke: $color__primary;
            }

            span {
              border-color: $color__primary;
            }
          }
          
          &.active {
            span {
              border-color: transparent;
            }
          }
        }
      }
    }
  }
  
  &__name {
    //font-weight: 600;
    letter-spacing: 0.18px;

    @include media-breakpoint-down(tbx) {
      font-weight: normal;
      letter-spacing: 0.36px;
      display: none;
    }
  }
  
  &__pagination {

    @include media-breakpoint-down(tbx) {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 1;
      background: rgba(0,0,0,0.5);
      padding: 14px 16px;
    }
    
    .popup-shop__name {
      display: none;

      @include media-breakpoint-down(tbx) {
        display: block;
      }
    }
  }
  
  &__gallery {
    
    &-slides {
      position: relative;
    }
    
    .swiper-container {
      //background: #fff;
    }
    
    .swiper-pagination-kronos {
      margin-top: 28px;

      @include media-breakpoint-down(tbx) {
        margin-top: 0;
        width: auto;
      }
    }
  }
  
  &__item {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding-top: 56%;

    @include media-breakpoint-down(tbx) {
      padding-top: 61%;
    }
  }
}