.reviews {
  margin-top: 39px;
  margin-bottom: 118px;

  @include media-breakpoint-down(tbx) { // change
    margin-top: 34px;
    margin-bottom: 76px;
  }
  @include media-breakpoint-down(phx) {
    margin-bottom: 99px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;

    @include media-breakpoint-down(phx) {
      display: block;
    }
  }

  &_product {
    margin: 60px 0;
    
    @include media-breakpoint-up(tbx){
      + .pagination-more {
        margin-bottom: 100px;
      }
    }
    
    @include media-breakpoint-down(tb){
      margin: 0;

      .reviews-nav {
        display: none;
      }
      
      .review {
        .address__country {
          img {
            position: relative;
            top: 2px;
          }
        }
      }
      .video {
        .address__country {
          display: none;
        }
      }
    }
  }
}

.reviews-nav {
  margin-bottom: 37px;

  @include media-breakpoint-down(tbx) { // change
    margin-bottom: 70px;
  }
  @include media-breakpoint-down(phx) {
    margin-bottom: 48px;
    padding: 0 7%;
  }

  .btn {
    margin-right: 28px;
    padding: 6px 20px 7px;
    border-radius: 3px;
    font-size: 18px;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(phx) {
      width: 100%;
      font-size: 16px;
      margin-right: 0;
      padding: 7px 0;
      letter-spacing: 0.4px;
    }

    + .btn {
      @include media-breakpoint-down(phx) {
        margin-top: 16px;
      }
    }
  }
}

.reviews-aside {
  width: 31.25%;
  padding-left: 106px;

  @include media-breakpoint-down(tbx) { // change
    width: 27.2%;
    padding: 12px 0 0 40px;
  }
  @include media-breakpoint-down(phx) {
    width: 100%;
    padding: 0;
    margin-bottom: 41px;
  }
}

.reviews-body {
  width: 68.75%;

  @include media-breakpoint-down(tbx) { // change
    width: 72.8%;
  }
  @include media-breakpoint-down(phx) {
    width: 100%;
  }
}

