.detail {
  margin-top: 40px;

  @include media-breakpoint-down(lt) {
    margin-top: 18px;
  }

  @include media-breakpoint-down($mobile-switch){
    margin-top: 30px;
  }
  
  &__price {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin: 26px 0 15px;
    
    @include media-breakpoint-down(tbx){
      margin: 8px 0 16px;
    }
    
    @include media-breakpoint-down(tb){
      margin: 16px 0 12px;
    }
    
    .price {
      margin-right: 30px;
      
      @include media-breakpoint-down(tb){
        font-size: 26px;
        letter-spacing: 0.32px;
        margin-right: 20px;
      }

        &-old {
          text-decoration: none;
          @include media-breakpoint-down(tb) {
            font-size: 18px;
            letter-spacing: 0.36px;
            min-width: 122px;
          }

          > span {
            text-decoration: line-through;
          }
        }
    }

    > div {
      display: flex;
      flex: none;
      align-items: baseline;
    }
  }
  
  &__gallery {
    position: relative;
    @include media-breakpoint-down($mobile-switch){
      overflow: hidden;
      padding-bottom: 10px;
    }
    @include media-breakpoint-down($mobile-switch){
      margin: 0 -16px;
    }
  }
    
  &__title {
    
    h1 {
      margin: 0;
      font-size: 32px;
      font-weight: normal;
      letter-spacing: 0.32px;
      line-height: 1.15;
      max-height: (1.15 * 2em);
      overflow: hidden;

      @include media-breakpoint-down(dtx){
        font-size: 30px;
      }

      @include media-breakpoint-down(lt) {
        font-size: 26px;
      }

      @include media-breakpoint-down($mobile-switch){
        font-size: 20px;
        letter-spacing: 0.36px;
      }
    }
  }
  
  &__art {
    font-size: 17px;
    
    @include media-breakpoint-down(dtx){
      font-size: 15px;
    }
    
    @include media-breakpoint-down(lt){
      font-size: 14px;
    }
  }
  
  &__info4 {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    
    @include media-breakpoint-down($mobile-switch){
      align-items: flex-start;
    }
  }
  
  &__info1 {
    padding-top: 10px;
    align-items: center;

    @include media-breakpoint-down(lt){
      padding-top: 0;
    }

    @include media-breakpoint-down($mobile-switch){
      margin-bottom: 14px;
    }
    
    a {
      font-size: 17px;

      @include media-breakpoint-down(dtx){
        font-size: 15px;
      }

      @include media-breakpoint-down(lt){
        font-size: 13px;
      }

      @include media-breakpoint-down($mobile-switch){
        font-size: 14px;
      }
    }

    .status {
      @include media-breakpoint-down(lt) {
        top: 1px;
      }
      @include media-breakpoint-down(tb) {
        //top: 0;
      }
    }

    li + li::before {
      top: 2px;
    }
  }
  
  &__info2 {
    @extend %list;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    top: 2px;

    @include media-breakpoint-down(lt) {
      top: 0;
    }
    
    li {
      display: flex;
      align-items: flex-end;
      font-size: 16px;
      letter-spacing: 0.4px;
      margin-right: 42px;
      min-height: 28px;

      @include media-breakpoint-down(dtx){
        font-size: 15px;
      }

      @include media-breakpoint-down(lt){
        font-size: 14px;
        margin-right: 22px;
        min-height: 18px;
        
        &:last-child {
          margin-right: 0;
        }
      }

      @include media-breakpoint-down($mobile-switch){
        margin-right: 30px;
        margin-bottom: 10px;
        color: $color__additional;
        line-height: 1;
      }
      
      &.price-down {
        img {
          margin-right: 7px;

          @include media-breakpoint-down(lt){
            margin-right: 5px;
            width: 22px;
          }
        }
      }
      
      &.percent {
        margin-right: 46px;

        @include media-breakpoint-down(lt){
          margin-right: 22px;
        }

        @include media-breakpoint-down($mobile-switch){
          margin-right: 30px;
        }
        
        img {
          margin-right: 14px;
          @include media-breakpoint-down(lt){
            margin-right: 8px;
            width: 16px;
          }
        }
      }
      
      &.garant {
        img {
          margin: -6px 8px 0 0;

          @include media-breakpoint-down(lt){
            width: 22px;
            margin: -2px 5px 0 0;
          }
        }
      }
      
      
    }
  }
  
  &__info3 {
    position: relative;
    top: 3px;

    a {
      font-size: 17px;
      letter-spacing: 0.4px;
      white-space: nowrap;

      @include media-breakpoint-down(dtx){
        font-size: 15px;
      }
    }
  }
  
  &__info-row {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    @include media-breakpoint-down(lt){
      justify-content: flex-end;      
    }

    @include media-breakpoint-down($mobile-switch){
      display: block;
    }
  }
  
  
  
  &__col {
    &:nth-child(1),
    &_first {
      width: 720px;

      @include media-breakpoint-down(dtx){
        width: 35%;
      }

      @include media-breakpoint-down(lt){
        width: 50%;
      }

      @include media-breakpoint-down(tbx){
        width: 52%;
      }

      @include media-breakpoint-down($mobile-switch){
        width: auto;
      }
    }
    
    &:nth-child(2) {
      padding-left: 62px;
      width: calc(100% - 720px);

      @include media-breakpoint-down(dtx){
        width: 65%;
        padding-left: 30px;
      }

      @include media-breakpoint-down(lt){
        padding-left: 32px;
        width: 50%;
      }

      @include media-breakpoint-down($mobile-switch){
        padding-left: 0;
        width: auto;
      }
    }
    
    &_gallery {
      @include media-breakpoint-down(tbx){
        min-width: 470px;
      }
      @include media-breakpoint-down(tb){
        min-width: 0;
      }
    }
  }

  &__row {
    margin-bottom: 20px;
    display: flex;

    @include media-breakpoint-down(lt) {
      margin-bottom: 24px;
    }
    
    @include media-breakpoint-down($mobile-switch){
      display: block;
    }

    &-title,
    &-auto {
      @include media-breakpoint-down(lt) {
        justify-content: space-between;
        
        .detail__col {
          width: auto;
        }
      }
    }
    
    &-auto {
      @include media-breakpoint-down(lt) {
        align-items: center;
      }
    }
    
    &-title {
      margin-bottom: 15px;

      @include media-breakpoint-down(lt){
        align-items: baseline;
        margin-bottom: 22px;
      }

      @include media-breakpoint-down($mobile-switch){
        margin-bottom: 12px;
      }
    }

    &-opt {
      align-items: center;
      margin: 0;

      @include media-breakpoint-down($mobile-switch){
        display: flex;
        flex-direction: column-reverse;
        align-items: stretch;
      }
    }
    
    &-mob {
      display: none;
      
      @include media-breakpoint-down($mobile-switch) {
        display: block;
        margin-bottom: 48px;
        
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  
  &__main {
    display: flex;

    @include media-breakpoint-down($mobile-switch){
      display: block;
    }
  }
  
  &__block {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1 1 auto;
    //align-self: flex-end;
    margin-right: 25px;

    @include media-breakpoint-down(dtx){
      margin-right: 0;
    }
    
    &-bottom {
      
      @include media-breakpoint-down(tbx){
        margin-top: 10px;
      }
      @include media-breakpoint-down(tb){
        margin-top: 0;
      }
      
       > div:first-child {
         margin-top: 0;
       }
      
    }
    
  }
  
  &__sert {
    width: 120px;
    margin-left: 24px;

    @include media-breakpoint-down(tbx){
      display: none;
    }
  }
  
  &__banner {
    width: 40%;
    margin-left: 24px;

    @include media-breakpoint-down(dt){
      //width: 350px;
      //flex: 0 0 350px;
      margin-left: 12px;
    }

    @include media-breakpoint-down(lt){
      display: none;
    }
  }
  
  &__action {
    margin: 26px 0 26px;

    @include media-breakpoint-down(tbx){
      margin: 8px 0;
    }

    @include media-breakpoint-down($mobile-switch){
      margin: 22px 0 16px;
    }
  }
  
  &__triggers {
    margin: 10px 0 0;

    @include media-breakpoint-down(tbx){
      margin: 0;
    }
    
    &-desktop {
      @include media-breakpoint-down($mobile-switch){
        //display: none;
      }
    }
    
    &-mob {
      display: none;
      @include media-breakpoint-down($mobile-switch){
        display: block;
        margin-top: 22px;
      }
    }
  }
  
  &__views {
    margin-top: 10px;
    font-size: 17px;
    letter-spacing: 0.4px;
    color: rgba(0,0,0,0.5);

    @include media-breakpoint-down(dtx){
      font-size: 15px;
    }
    @include media-breakpoint-down(lt){
      font-size: 13px;
    }
    @include media-breakpoint-down($mobile-switch){
      display: none;
    }
  }
  
  &__links {
    margin: 26px 0 0;

    @include media-breakpoint-down(lt){
      margin: 16px 0 0;
    }
    
    a {
      font-size: 17px;

      @include media-breakpoint-down(dtx){
        font-size: 15px;
      }

      @include media-breakpoint-down(lt){
        font-size: 14px;
      }
    }

    .border-dot-list li  {
      padding-right: 26px;
      &:last-child {
        padding-right: 0;
      }
      + li {
        padding-left: 32px;
      }
      
      a {
        white-space: normal;
      }
    }
  }
  
  &__buttons {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;

    @include media-breakpoint-down(tbx){
      margin-top: 4px;
    }
    @include media-breakpoint-down(tb){
      margin-top: 12px;
      //justify-content: space-between;
    }
    
    a {
      //width: 50%;
      //padding-left: 0;
      //padding-right: 0;
      
      flex: 1 1 auto;
      
      @include media-breakpoint-down(lt){
        width: 50%;
      }
      
      @include media-breakpoint-down($mobile-switch){
        width: auto;
      }

      @include media-breakpoint-down(phx){
        flex: 1 1 auto;
      }

      &:not(:last-of-type) {
        margin-right: 18px;
      }
      &:not(.btn) {
        font-size: 17px;
      }
    }
    
    &_zap {
      align-items: center;
    }
  }
  
  &__bottom {
    position: relative;
    margin-top: 66px;
    overflow: hidden;
    
    @include media-breakpoint-down(lt){
      margin-top: 25px;
    }
  }
  
  &__tabs {
    flex: 1 1 auto;
    
    &-container {
      //@include media-breakpoint-down($mobile-switch){
      //  display: none;
      //}
    }
  }
  
  &__opt-mob {
    display: none;
    text-align: center;
    padding: 10px 0;
    margin: 20px 0;
    
    @include media-breakpoint-down($mobile-switch) {
      display: block;
    }
    
    a {
      position: relative;
      font-size: 16px;
      padding-left: 34px;

      img {
        top: 50%;
        left: 0;
        position: absolute;
        width: 21px;
        margin-top: -10px;
      }
    }
  }
  
  &__opt {
    padding-left: 108px;

    @include media-breakpoint-down(lt){
      display: none;
    }

    @include media-breakpoint-down($mobile-switch){
      display: block;
      text-align: center;
      padding: 10px 0;
      margin-bottom: 12px;
    }
    
    a {
      position: relative;
      padding-left: 45px;
      font-size: 20px;

      @include media-breakpoint-down(dtx){
        font-size: 15px;
      }

      @include media-breakpoint-down($mobile-switch){
        font-size: 16px;
        padding-left: 34px;
      }
      
      img {
        top: 50%;
        margin-top: -14px;
        left: 0;
        position: absolute;

        @include media-breakpoint-down($mobile-switch){
          width: 21px;
          margin-top: -10px;
        }
      }
    }
  }
  
  &__form {
    width: 62.5%;
    margin: 14px 0 0 auto;

    @include media-breakpoint-down(dtx){
      width: 65%;
    }
    @include media-breakpoint-down(lt){
      width: auto;
    }
    @include media-breakpoint-down($mobile-switch){
      margin: 40px 0 0;
    }
    
    &_1 {
      margin: 114px 0 0 auto;
      @include media-breakpoint-down(dtx){
        margin-top: 14px;
      }
    }
  }
  
  &__bottom-zap {
    display: flex;
    align-items: flex-end;

    @include media-breakpoint-down(tb){
      flex-direction: column-reverse;
      align-items: stretch;
    }
  }
  
  &__views-zap {
    padding-left: 30px;
    padding-right: 30px;
    width: 57%;

    @include media-breakpoint-down(dtx){
      width: 54%;
    }

    @include media-breakpoint-down(dt){
      width: 45%;
    }

    @include media-breakpoint-down(lt){
      width: 70%;
      padding-left: 0;
    }

    @include media-breakpoint-down(tbx){
      padding-right: 48px;
    }

    @include media-breakpoint-down(tb){
      padding: 35px 0;
      width: auto;
    }
  }
  
  &__client-reviews {
    flex-shrink: 0;
    flex-grow: 0;
    width: 25%;
    
    @include media-breakpoint-down(lt){
      display: none;
    }
    
    
    .clients-section__reviews {
      width: auto;
      padding: 0;
    }

    .text-reviews {
      height: 325px;
    }

    .section-nav {
      margin-bottom: 16px;
      //padding-right: 10px;
    }

    .clients-section__title {
      padding: 0 12px;
    }
  }
  
  &__contacts {
    width: 18%;

    @include media-breakpoint-down(dtx){
      width: 21%;
    }

    @include media-breakpoint-down(dt){
      width: 30%;
    }

    @include media-breakpoint-down(tb){
      width: auto;
    }
  }

  // Карточка товара (RU) запчасти

  &_zap {

    .detail__info-row {
      @include media-breakpoint-down(tbx){
        justify-content: flex-start;
      }
    }
    
    .detail__row-info {
      margin-bottom: 24px;
    }

    .detail__banner {
      width: 48.4%;
      margin-left: 24px;
    }
    
    .detail-banner {
      height: 506px;
    }
    
    .detail__top {
      margin-bottom: 73px;

      @include media-breakpoint-down(phx) {
        margin-bottom: 54px;
      }
    }

    .detail__row-info {
      margin-bottom: 24px;
    }

    .detail__buttons {
      align-items: center;
      margin-top: 25px;
      
      @include media-breakpoint-down(tbx){
        margin-top: 15px;
      }
      @include media-breakpoint-down(tb){
        margin-top: 25px;
      }
      
      a {
        @include media-breakpoint-down(lt){
          width: auto;
        }
      }

      @include media-breakpoint-down(phx) {
        display: block;
        text-align: center;
      }

      .btn {
        height: 46px;
        line-height: 42px;
        letter-spacing: 0.36px;
        padding: 0 17px;
        vertical-align: middle;

        @include media-breakpoint-down(tbx){
          padding: 5px 18px 8px;
          height: auto;
          line-height: inherit;
          margin-right: 30px;
        }
        @include media-breakpoint-down(phx) {
          height: 35px;
          line-height: 31px;
          font-size: 14px;
          letter-spacing: 0.4px;
          padding: 0 17px;
          margin-right: 18px;
        }
      }

      &-wrap {
        @include media-breakpoint-down(phx) {
          margin-bottom: 15px;
        }
      }

      > .dot-link-one-line {
        @include media-breakpoint-down(phx) {
          position: relative;
          padding-left: 34px;

          img {
            position: absolute;
            width: 21px;
            margin-top: -9px;
            top: 50%;
            left: 0;
          }
        }
      }
    }

    .dot-link-one-line {
      flex: none;
    }
    
    .detail-gallery {
      background: #fff;
    }

    .detail-gallery-thumbs {
      margin-top: 19px;
    }

    .detail-gallery-pagination {
      display: none;

      @include media-breakpoint-down(tbx) {
        display: block;
      }
    }

    .detail__triggers {
      margin-top: 2px;

      @include media-breakpoint-down(phx) {
        margin-top: 13px;
      }
    }

    .detail__opt-mob {
      @include media-breakpoint-down(tb){
        margin-bottom: 0;
        margin-top: 8px;
      }
    }

    .detail__buttons-mob {
      display: none;
      margin-top: 13px;
      
      a {
        font-size: 15px;
        letter-spacing: 0.4px;
        margin-right: 42px;
        
        img[src$="opt.svg"] {
          width: 14px;
          margin-right: 9px;
        }
      }
      
      @include media-breakpoint-down(tbx){
        display: block;
      }
      @include media-breakpoint-down(tb){
        display: none;
      }
    }
    
    .detail__price {
      
      .price-old {
        @include media-breakpoint-down(tbx){
          font-size: 16px;
        }
      }
      
      @include media-breakpoint-down(tb){
        margin: 16px 0 13px;
        
        + .detail__buttons {
          margin-top: 13px;
        }
      }
    }

    .detail-triggers_scroll {
      @include media-breakpoint-down(tb){
        margin-top: 13px;
      }
    }
    
    .detail-gallery {
      
      .swiper-button-prev-kronos,
      .swiper-button-next-kronos {
        border: 1px solid $color__text2;
      }
      
      .swiper-button-prev-kronos {
        border-right: 0;

        @include media-breakpoint-down($mobile-switch){
          border-right: 1px solid $color__text2;
        }
      }
      
      .swiper-button-next-kronos {
        border-left: 0;
        
        @include media-breakpoint-down($mobile-switch){
          border-left: 1px  solid $color__text2;
        }
      }
      
    }

    .detail__gallery.video-gallery {
      .swiper-button-prev-kronos,
      .swiper-button-next-kronos {
        border: none;

        @include media-breakpoint-down($mobile-switch) {
          border: 1px solid #DCDCDC;
        }
      }

      .swiper-button-next-kronos {
        @include media-breakpoint-down($mobile-switch) {
          right: 29px;
        }
      }

      .swiper-button-prev-kronos {
        @include media-breakpoint-down($mobile-switch) {
          left: 36px;
        }
      }

      .lupa {
        @include media-breakpoint-down($mobile-switch) {
          display: none;
        }
      }
    }

    .detail__info2 {
      top: 4px;
      @include media-breakpoint-down(tbx){
        top: 0;
      }
      @include media-breakpoint-down(tb){
        top: 4px;
      }
    }

    .clients-section__reviews {
      .clients-section__title {
        padding: 0 10px;

        + .dot-link-one-line_arrow {
          position: relative;
          top: 3px;
        }
      }
    }

    .clients-section__item {
      .clients-section__title {
        padding: 0;

        @include media-breakpoint-down(tbx){
          font-size: 18px;
        }
      }
    }
  }
  
  // Карточка товара (RU) с ценой с акцией
  &_1 {}
  
  // Карточка товара (RU) без цены с акцией
  &_2 {
    
    .detail__buttons {
      justify-content: space-between;
      a {
        padding: 9px 15px 11px;
        
        @include media-breakpoint-down(tb){
          padding: 10px;
        }
      }
    }
    
  }
  
  // Карточка товара (RU) с ценой без акции
  &_3 {}
  
  // Карточка товара (RU) без цены и без акции
  &_4 {}

  &__flex-none {
    flex: none !important;
  }

}

.detail-gallery {
  //background: #D9FFD9;
  
  @include media-breakpoint-down($mobile-switch){
    overflow: visible;
    background: none;
  }
  
  &__item {
    position: relative;
    height: 405px;
    display: flex;
    align-items: center;
    justify-content: center;
    
    &-lupa {
      position: absolute;
      display: inline-block;
      bottom: 18px;
      right: 18px;
    }
    
    .lupa {
      width: 20px;
      height: 20px;
      stroke: $color__additional;
      fill: none;
    }

    &-link {
      background: #efefef;

      img {
        @extend .img-responsive;
        margin: 0 auto;

        @include media-breakpoint-down(tbx){
          height: 130px;
        }

        @include media-breakpoint-down($mobile-switch){
          height: 100px;
        }
      }

      &-text {
        position: relative;
        letter-spacing: 0.36px;
        white-space: nowrap;
        text-align: center;

        @include media-breakpoint-down(tbx){
          font-size: 16px;
        }
        @include media-breakpoint-down(phx) {
          white-space: normal;
        }


        span {
          display: inline-block;
          position: relative;

          @include media-breakpoint-down(phx) {
            border-bottom: 1px dotted $color__additional;
          }

          &::after {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 1px;
            transition: 0.25s;
            background-image: repeating-linear-gradient(to right, $color__additional 0, $color__additional 1px, transparent 1px, transparent 2px);
            content: "";

            @include media-breakpoint-down(phx) {
              display: none;
            }
          }
        }
      }

      &:hover {
        .detail-gallery__item-link-text {
          span {
            &::after {
              width: 0;
            }
          }
        }
      }
    }
    
    @include media-breakpoint-down(tbx){
      height: 265px;
