          left: 4px;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background: $color__additional;
          content: "";

          @include media-breakpoint-down($mobile-switch){
            width: 4px;
            height: 4px;
            top: 7px;
            left: 0;
          }
        }
        
        &:last-child {
          border-bottom: 0;
        }
      }
    }
  }
}

.detail-video {
  padding: 0 0 60px;
  
  @include media-breakpoint-down(tbx){
    padding: 25px 0 60px;
  }
  
  @include media-breakpoint-down($mobile-switch){
    padding: 0;
  }
  
  &__inner {
    display: flex;
    flex-wrap: wrap;

    @include media-breakpoint-down($mobile-switch){
      display: block;
    }
  }
  
  &__item {
    border-top: 1px solid $color__text2;
    width: 25%;
    padding: 48px 0;
    
    @include media-breakpoint-down(lt){
      width: (100% / 3);
    }
    
    @include media-breakpoint-down(tbx){
      width: 50%;
      border-top: 0;
      padding: 15px 0;
    }
    
    @include media-breakpoint-down($mobile-switch){
      padding: 0;
      width: auto;
      margin: 0 0 16px;
    }

