.popup.popup-calc-price {
  width: 951px;
  padding: 36px 40px 30px;
  
  @include media-breakpoint-down(tbx){
    width: 920px;
    padding: 30px;
  }
  
  @include media-breakpoint-down(tb){
    width: auto;
    padding: 30px 0 0;
  }
}

.popup-calc-price {
  
  &__top {
    display: flex;
    justify-content: space-between;

    @include media-breakpoint-down(tb){
      justify-content: center;
      padding: 0 16px;
    }
    
    &-item {
      
    }
    
    &-left,
    &-right {
      width: 50%;

      @include media-breakpoint-down(tb){
        width: auto;
      }
    }
    &-left {
      padding-top: 3px;
    }
    &-right {
      text-align: right;

      @include media-breakpoint-down(tb){
        display: none;
      }
    }
  }
  
  &__middle {}
  
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: $color__secondary;
    padding: 20px 30px;

    @include media-breakpoint-down(tbx){
      padding: 12px 30px;
    }

    @include media-breakpoint-down(tb){
      padding: 16px;
    }
    
    &-left,
    &-right {}
    
    &-left {}
    &-right {
      .btn {
        font-size: 18px;
        letter-spacing: 0.36px;
        padding: 5px 15px 8px;
      }
    }
  }
  
  &__garant {
    position: relative;
    letter-spacing: 0.36px;
    padding: 0 0 0 48px;

    @include media-breakpoint-down(tbx){
      font-size: 16px;
    }

    @include media-breakpoint-down(tb){
      font-size: 14px;
      padding: 0 0 0 28px;
    }
    
    img {
      position: absolute;
      top: -6px;
      left: 0;

      @include media-breakpoint-down(tb){
        top: -2px;
        width: 21px;
      }
    }
  }
  
  &__product {
    display: inline-flex;
    
    &-info {
      text-align: right;
    }
    
    &-image {
      margin-left: 20px;
      
      img {
        @extend .img-responsive;
      }
      
      &-1920 {
        padding: 3px 0;
        
        @include media-breakpoint-down(tbx){
          display: none !important;
        }
      }
      
      &-960 {
        display: none !important;
        padding: 3px 10px 3px 0;
        
        @include media-breakpoint-down(tbx){
          display: block !important;
        }
      }
    }
    
    &-name {
      font-size: 20px;
      letter-spacing: 0.36px;
      margin-bottom: 9px;

      @include media-breakpoint-down(tbx){
        font-size: 17px;
        letter-spacing: 0.4px;
        margin-bottom: 5px;
      }
    }
    
    &-count {
      font-size: 14px;
      font-weight: 300;
      letter-spacing: 0.4px;
    }
    
  }
  
  &__steps {
    display: flex;
    align-items: baseline;
    font-size: 20px;
    letter-spacing: 0.36px;

    @include media-breakpoint-down(tb){
      display: block;
      text-align: center;
    }
    
    &-item {
      display: flex;
    }
    
    &-text {
      color: #000;
      margin-right: 27px;

      @include media-breakpoint-down(tb){
        margin-right: 0;   
        margin-bottom: 12px;
      }
    }
    
    &-text-step {
      color: $color__additional;
      margin-right: 16px;

      @include media-breakpoint-down(tb){
        font-size: 18px;
        margin-right: 22px;
      }
    }
    
    &-list {
      display: flex;
      align-items: center;
      
      a {
        display: block;
        width: 30px;
        height: 30px;
        text-align: center;
        border: 2px solid $color__additional;
        background: #fff;
        border-radius: 50%;
        font-size: 18px;
        font-weight: 800;
        line-height: 26px;
        color: $color__additional;
        margin-right: 20px;

        @include media-breakpoint-down(tb){
          border-width: 1px;
          width: 28px;
          height: 28px;
          font-size: 16px;
          font-weight: 900;
          line-height: 25px;
          margin-right: 22px;
        }
        
        &:last-child {
          margin-right: 0;
        }
        
        &.active {
          color: #fff;
          background: $color__primary;
          border-color: $color__primary;
        }
      }
    }
    
  }
  
  &__engine {
    margin: 42px 0 30px;

    @include media-breakpoint-down(tbx){
      margin: 24px 0 20px;
    }

    @include media-breakpoint-down(tb){
      margin: 20px 0 20px;
    }
    
    &-name {
      font-size: 22px;
      letter-spacing: 0.36px;
      text-align: center;
      margin-bottom: 48px;

      @include media-breakpoint-down(tbx){
        font-size: 20px;
        margin-bottom: 24px;
      }
      
      @include media-breakpoint-down(tb){
        margin-bottom: 16px;
      }
    }
    &-gallery {
      position: relative;
      padding: 0 75px;

      .swiper-pagination-kronos {
        display: none;
        @include media-breakpoint-down(tb){
          display: block;
        }
      }

      @include media-breakpoint-down(tbx){
        padding: 0 100px;
      }

      @include media-breakpoint-down(tb){
        padding: 0 16px;
        
        .swiper-button-prev-2,
        .swiper-button-next-2 {
          //display: none;
        }
      }
    }
  }
  
  &__naves {
    margin: 42px 0 30px;

    @include media-breakpoint-down(tbx){
      margin: 24px 0 20px;
    }

    @include media-breakpoint-down(tb){
      margin: 20px 0 20px;
    }
    
    &-name {
      font-size: 22px;
      letter-spacing: 0.36px;
      text-align: center;
      margin-bottom: 38px;

      @include media-breakpoint-down(tbx){
        font-size: 20px;
        margin-bottom: 24px;
      }
      
      @include media-breakpoint-down(tb){
        margin-bottom: 16px;
      }
    }
    &-gallery {
      position: relative;
      padding: 0 75px;

      .swiper-pagination-kronos {
        display: none;
        @include media-breakpoint-down(tb){
          display: block;
        }
      }

      @include media-breakpoint-down(tbx){
        padding: 0 100px;
      }

      @include media-breakpoint-down(tb){
        padding: 0 16px;
        
        .swiper-button-prev-2,
        .swiper-button-next-2 {
          //display: none;
        }
      }
    }
  }
  
}

.popup-calc-price-engine-swiper {
  @include media-breakpoint-down(tb){
    margin-bottom: 14px;
  }
}
.popup-calc-price-naves-swiper {
  @include media-breakpoint-down(tb){
    margin-bottom: 14px;
  }
}

.popup-calc-price-naves {

  &__gift {
    font-size: 16px;
    letter-spacing: 0.4px;
    color: $color__text4;
    text-align: center;
    margin-top: 14px;

    @include media-breakpoint-down(tbx){
      font-size: 14px;
      margin: 0 0 0 16px;
    }

    @include media-breakpoint-down(tb){
      margin: 0 0 0 9px;
    }
  }
  
  &__img {
    margin-bottom: 20px;

    img {
      @extend .img-responsive;
      margin: 0 auto;
    }

    @include media-breakpoint-down(tbx){
      height: 102px;
      margin-bottom: 6px;
    }

    @include media-breakpoint-down(tb){
      height: 74px;
    }
  }

  &__name {
    font-size: 17px;
    letter-spacing: 0.4px;
    text-align: center;
    margin-bottom: 22px;
    height: ((17px * 1.15 * 3) + 5);

    @include media-breakpoint-down(tbx){
      font-size: 16px;
      height: ((16px * 1.15 * 3) + 5);
    }

    @include media-breakpoint-down(tb){
      text-align: left;
      height: ((16px * 1.15 * 2) + 5);
      overflow: hidden;
      margin-bottom: 18px;

      span {
        border-bottom: 1px dotted black;
      }
    }
  }

  &__checkbox {
    text-align: center;

    @include media-breakpoint-down(tb){
      margin: 2px 0 0;
    }
  }
  
  &__checkbox-and-gift {
    @include media-breakpoint-down(tbx){
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  
}

.popup-calc-price-engine {
  
  &__img {
    margin-bottom: 12px;

    img {
      @extend .img-responsive;
      margin: 0 auto;
    }

    @include media-breakpoint-down(tbx){
      height: 102px;
      margin-bottom: 6px;
    }

    @include media-breakpoint-down(tb){
      height: 74px;
    }
  }
  
  &__name {
    font-size: 17px;
    letter-spacing: 0.4px;
    text-align: center;
    margin-bottom: 22px;

    @include media-breakpoint-down(tbx){
      font-size: 16px;
    }

    @include media-breakpoint-down(tb){
      text-align: left;
      height: ((16px * 1.15 * 2) + 2);
      overflow: hidden;
      margin-bottom: 18px;
      
      span {
        border-bottom: 1px dotted black;
      }
    }
  }
  
  &__radio {
    text-align: center;
  }
  
}