.popup.popup-credit {
  max-width: 950px;
  padding: 37px 72px 52px;

  @include media-breakpoint-down(tb) {
    padding: 37px 59px 52px;
  }
  @include media-breakpoint-down(phx) {
    padding: 37px 16px 52px;
  }

  .popup__name {
    margin-bottom: 33px;

    @include media-breakpoint-down(phx) {
      margin-bottom: 18px;
    }
  }

  p {
    margin-top: 0;
    margin-bottom: 25px;

    @include media-breakpoint-down(phx) {
      margin-bottom: 19px;
      font-size: 15px;
    }
  }

  .popup__text_bottom {
    padding: 0 15%;
    margin-bottom: 20px;

    @include media-breakpoint-down(phx) {
      font-size: 14px;
    }
  }

  .popup__form {
    margin: 51px 0 20px;

    @include media-breakpoint-down(phx) {
      margin-top: 33px;
    }

    .btn {
      padding: 8px 20px;

      @include media-breakpoint-down(phx) {
        font-size: 18px;
      }
    }
  }

  .input-phone {
    @include media-breakpoint-down(phx) {
      min-width: initial;
    }
  }
}