.review-product {
  width: 45.8%;

  @include media-breakpoint-down(tbx) { // change
    width: 100%;
    margin-top: 19px;
    margin-bottom: 21px;
  }
  @include media-breakpoint-down(phx) {
    margin-top: 20px;
  }

  .review_column & {
    width: 100%;
  }

  .review__head & {
    margin-top: -3px;

    @include media-breakpoint-down(phx) {
      margin-top: 21px;
    }
  }

  &__row {
    display: flex;
  }

  &__name {
    position: relative;
    display: inline-block;
    line-height: 1.25;
    letter-spacing: 0.36px;
    transition: none;

    &::before {
      //content: '';
      //display: block;
      //position: absolute;
      //top: 0;
      //bottom: 0;
      //right: 0;
      //width: 0;
      //background: linear-gradient(transparent 1em, #fff .25em);
      //background-size: 1em 1.25em;
      //transition: width .3s;
    }

    > span {
      //border-bottom: 1px dotted $color__additional;
      transition: all .25s;
    }

    &:hover {
      &::before {
        //width: 100%;
      }
      > span {
        color: $color__primary;
        border-bottom-color: $color__primary;
      }
    }
  }

  &__subname {
    margin: 0 0 5px;
    line-height: 1;
    letter-spacing: 0.36px;
    color: $color__additional;
    white-space: nowrap;

    .review_column & {
      margin-right: 16px;
      margin-bottom: 3px;
    }
  }

  &__pic {
    flex-shrink: 0;
    width: 186px;
    padding-top: 5px;
    text-align: center;

    @include media-breakpoint-down(tbx) { // change
      display: flex;
      align-items: center;
      justify-content: center;
      width: 120px;
      height: 68px;
      margin-top: -28px;
      padding-top: 0;

      img {
        max-height: 100%;
      }
    }
    @include media-breakpoint-down(phx) {
      width: 98px;
      margin-left: 20px;
    }
  }
}