.views-products {
  &__name {
    font-size: 20px;
    letter-spacing: 0.36px;
    text-align: center;
    margin-bottom: 36px;
  }

  .swiper-slide {
    padding-bottom: 2px;
  }
}

.views-product {
  display: block;
  
  &__img {
    img {
    }
  }
  
  &__name {
    font-size: 16px;
    letter-spacing: 0.4px;
    margin-top: 14px;
    
    span {
      display: inline;
      border-bottom: 1px dotted $color__text;
      transition: 0.2s;
    }
  }
  
  &:hover,
  &:focus {
    .views-product__name {
      span {
        border-color: $color__primary;
      }
    }
  }
}