.banner-block {
  display: flex;

  &__col {
    flex-shrink: 0;
    flex-grow: 0;
    width: 100%;

    &_25 {
      width: 25%;
    }

    &_33 {
      width: (100% / 3);
    }

    &_50 {
      width: 50%;
    }

    &_66 {
      width: (100 - (100% / 3));
    }

    &_75 {
      width: 75%;
    }
  }
}